import React from "react";
import { Card } from 'antd';
import { postAction } from "@/axios";
import NoData from '@/common/components/NoData/NoDataComponent';
import Barchart from '../../../../../../common/components/BizCharts/BarChart'
import ModalTable from './modal'


class MingZu extends React.Component {
    state = {
        nationsData: [],
        visible: false,

    }
    // 打开弹框
    handleClick = () => {
        this.setState({
            visible: true
        })
    }   
    // 关闭弹窗 
    handleClose = () => {
        this.setState({
            visible: false
        })
    }
    //民族情况TOP10 1130
    getNationLabel() {
        const { curYear, curGrade, curCollege, curMajor, curClass } = this.props.filterValue
        postAction(
            "/bigdata/portrait/getNationLabel",
            {
                year: curYear,
                grade: curGrade,
                collegeCode: curCollege,
                majorCode: curMajor,
                classCode: curClass
            }
        ).then(
            (res) => {
                if (res.success && res.obj.length > 0) {
                    let newData = [];
                    res.obj.forEach(({ count, nation }) => {
                        if(nation !== '汉族'){
                            newData.push({
                                "label": nation,
                                "人数": count
                            })
                        }
                        
                    })
                    this.setState({
                        nationsData: newData
                    })
                }
            }
        )
    }

    componentWillReceiveProps(nextProps, nextState) {
        this.setState({
            filterValue: nextProps.filterValue
        }, () => {
            this.getNationLabel()
        })
    }
    componentDidMount = () => {
        this.setState({
            filterValue: this.props.filterValue
        }, () => {
            this.getNationLabel()
        })
    }

    render() {
        if (this.state.nationsData && this.state.nationsData[0]) {
            return (
                <React.Fragment>
                    {
                        this.state.visible &&<ModalTable handleClose={this.handleClose} filterValue={this.props.filterValue}  visible={this.state.visible} filterName={this.props.filterName} />
                    }
                    
                    <Card
                        style={{cursor:'pointer'}}
                        title={<div className="common-title">
                            <i className="iconfont icon-minzu_xiankuang" style={{ marginRight: '5px' }}/>少数民族情况TOP10
                            <span>单位：人</span>
                            </div>}
                        headStyle={{ border: "none", maxHeight: "40px", minHeight: "0" }}
                        bodyStyle={{ padding: 0 }}
                        onClick={
                            this.handleClick
                        }
                    >
                        <Barchart data={this.state.nationsData} height={260} padding={[40, 30, 60, 60]}>

                        </Barchart>                       
                    </Card>
                </React.Fragment>

            );
        } else {
            return (
                <Card
                    title={<div className="common-title">
                        <i className="iconfont icon-minzu_xiankuang" style={{ marginRight: '5px' }}/>少数民族情况TOP10
				</div>}
                    headStyle={{ border: "none", maxHeight: "40px", minHeight: "0" }}
                    bodyStyle={{ padding: 0 }}
                >
                    <NoData height={260} data={'nodata'} />
                </Card>
            );
        }
    }
}
export default MingZu;



