import React, {Component, Fragment} from 'react';
import {Row, Col, Card} from 'antd';

import GangStudent from "./GangStudent";
import {postAction} from "../../../../../axios";


export default class GangCard extends Component {

    constructor(props){
        super(props);
        this.state = {
            total:0,
            xianggang:0,
            aomen:0,
            taiwan:0,
            huaqiao:0
        }
        this.getData = this.getData.bind(this);
    }

    getData() {
        const {curYear,curCollege,curMajor,curGrade} = this.props.filterValue;
        postAction(
            "/bigdata/student/getGOTStudentDist",
            {
                year: curYear,
                collegeCode: curCollege,
                majorCode: curMajor,
                grade: curGrade,
            }
        ).then(
            (res) => {
                if (res.success){
                    this.setState({
                        total: res.obj.count.total,
                        xianggang: res.obj.count.xianggang,
                        aomen: res.obj.count.aomen,
                        taiwan: res.obj.count.taiwan,
                        huaqiao: res.obj.count.huaqiao
                    })
                } else {
                    this.setState({
                        total: 0,
                        xianggang: 0,
                        aomen: 0,
                        taiwan: 0,
                        huaqiao:0
                    })
                }
            }
        )
    }

    render () {
        return (
            <Fragment>
                <Card
                    title={<div className="common-title">港澳台侨学生分析</div>}
                    extra={<span className="title-font">总人数
                        <span className="title-number">{this.state.total}</span>
                        香港
                        <span className="title-number">{this.state.xianggang}</span>
                        澳门
                        <span className="title-number">{this.state.aomen}</span>
                        台湾
                        <span className="title-number">{this.state.taiwan}</span>
                        华侨
                        <span className="title-number">{this.state.huaqiao}</span>
                    </span>}
                    bodyStyle={{display:"none"}}
                />

                <Row>
                    <Col span={24} className="only-top">
                        <GangStudent height={400} filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>
                </Row>
            </Fragment>
        )
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)
        }
    }
}


