import React, {Component} from 'react';
import {Tabs, Row, Col} from 'antd';
import { connect } from 'react-redux';
import {xueqiAction} from '../../../redux/actions/xueqiAction';
import {biyeAction} from "../../../redux/actions/biyeAction";

import Huaxiang from "./huaxiang";
import Student from "./student";
import Xueye from "./xueye";
import Graduation from "./graduation";
import Consumption from './consumption';
import Internet from './internet';
import Shangke from "./shangke";
import Library from "./library";
import Filter from "./Filter";
import {getLocal} from '../../../utils';
import {postAction} from '../../../axios';
import './style.less';

const TabPane = Tabs.TabPane;


class All extends Component{
	constructor(props) {
		super(props);
		this.state= {
			filterValue: null,
            filterName: null,
            tabs:[]
		};
		this.callback = this.callback.bind(this);
		// 获取筛选条件中的value用请求， 获取筛选条件的name用于标注生效条件
		this.getFilterValueAndName = this.getFilterValueAndName.bind(this); 
	}
	
	// 获取筛选条件中的value用请求， 获取筛选条件的name用于标注生效条件
	getFilterValueAndName(a, b) {
		this.setState({
			filterValue: a,
			filterName: b
		})
	}

    callback(key){
	    
    	if (key === "113") { //学业显示学期筛选条件
            this.props.toggleXueqi(true);
            this.props.toggleBiye(true);
        }else if (key === "114"){   //毕业隐藏年级和班级筛选条件
            this.props.toggleBiye(false);
            this.props.toggleXueqi(false);
        }else {
            this.props.toggleBiye(true);
            this.props.toggleXueqi(false);
        }
    }
    
    componentDidMount() {
        this.props.toggleBiye(true);
        this.props.toggleXueqi(false);

        const userinfo=JSON.parse(getLocal('userInfo'));
        let uid=userinfo.id;
        let data={
            sourceCode:"pc",
            menuId:11,
            userId:uid            
        }
        postAction('/bigdata/system/menu/labelList',data,0,1).then(res=>{
           if(res.success){
               this.setState({
                  tabs:res.obj
               })
           }
        })
    }
    
    getTabs = id => {
        switch(id){
            case 111:
            return <Huaxiang filterName = {this.state.filterName} filterValue = { this.state.filterValue }/>
            case 112:
            return <Student filterName = {this.state.filterName} filterValue = { this.state.filterValue }/>
            case 113:
            return <Xueye filterName = {this.state.filterName} filterValue = { this.state.filterValue }/>
            case 114:
            return <Graduation filterName = {this.state.filterName} filterValue = { this.state.filterValue }/>
            case 115:
            return  <Consumption filterName = {this.state.filterName} filterValue = { this.state.filterValue }/>
            case 116:
            return <Internet filterName = {this.state.filterName} filterValue = { this.state.filterValue }/>
            case 117:
            return <Shangke filterName = {this.state.filterName} filterValue = { this.state.filterValue }/>
            case 118:
            return <Library filterName = {this.state.filterName} filterValue = { this.state.filterValue }/>
            default:
            return null
        }
    }
    
	render () {

        const {tabs}=this.state;
		return (
			<Row className=" main-content">
				<Col span={24} className="filter-height">
					<Filter getFilterValueAndName = {this.getFilterValueAndName} />
				</Col>
				<Col span={24}>
                    <Tabs defaulActiveKey={111} onChange={this.callback} className="main-tabs">
                    {
                        tabs.map(item=>(
                            <TabPane tab={item.name} key={item.id}>
                                {this.getTabs(item.id)}
                            </TabPane>
                        ))
                    }                    
                    </Tabs>
				</Col>
			</Row>
		)
	}
}

const mapDispatchToProps = (dispatch) => {

    return {
        toggleXueqi (bool) {
        	const action = xueqiAction(bool);
            dispatch(action);
        },
        toggleBiye (graduate) {
            const action = biyeAction(graduate);
            dispatch(action);

        }
    }
}

export default connect(null, mapDispatchToProps)(All)



