import React, {Component} from 'react';
import {Table,Divider} from 'antd';

import PieChart from '../../../../../common/components/BizCharts/PieChart';
import {postAction} from "../../../../../axios";
import MainCard from "../../../../../common/components/MainCard/index";
import ReactDOM from "react-dom";


export default class Address extends Component {

    constructor(props) {
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            pieData : [
                // { item: '标准化', count: 40 }
            ],
            tableData: [
                // { key: '1', sortIndex: '1', address: '九龙湖校区', count: '2901' }
            ],
            pagination: {
                pageSize: 5, // 每页条数
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            }
        }
        this.getData = this.getData.bind(this);
    }

    getData(){
        const {curYear,curCollege,curMajor,curGrade,curClass} = this.props.filterValue;
        postAction(
            "/bigdata/consume/countConsumePlace",
            {
                "year": curYear,
                "collegeCode": curCollege,
                "majorCode": curMajor,
                "grade": curGrade,
                "classCode": curClass
            }
        ).then(
            (res) => {
                if (res.success){
                    const pageInfo = res.obj;
                    let _newData = [];
                    let _tableData = [];

                    pageInfo.forEach(
                        (item, index) => {

                            _newData.push({
                                item: item.name.trim(),
                                count: item.value,
                            })

                            _tableData.push({
                                key: (index + 1),
                                sortIndex: (index + 1),
                                address: item.name.trim(),
                                count: item.value,
                            })
                        }
                    )
                    this.setState({
                        pieData: _newData,
                        tableData: _tableData,
                        pagination: {
                            pageSize: 5, // 每页条数
                            showQuickJumper: true,
                            total: pageInfo.length,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        }
                    })
                } else {
                    this.setState({
                        pieData:[],
                        tableData: [],
                        pagination: {
                            pageSize: 5, // 每页条数
                            showQuickJumper: true,
                            total: null,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        }
                    })
                }
            }
        )
    }

    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'address-excel')
            }
            this.getData()
        })
    }

    render () {

        const columns = [
            {
                title:'排名',
                dataIndex: 'sortIndex',
                width: '10%',
            },{
                title: '消费地点',
                dataIndex: 'address',
            },{
                title: '消费次数',
                dataIndex: 'count',
            }
        ];

        return (
            <MainCard
                className="main-card"
                title={<div className="common-title">消费地点分布TOP5
                    <span>单位：次</span>
                    <div className="filter-text" style={{top:'22px',left:'0px'}}>{(this.props.filterName.curYearName.substring(0,this.props.filterName.curYearName.length - 1)) + "-" + (Number((this.props.filterName.curYearName).substring(0,this.props.filterName.curYearName.length - 1)) + 1) + "学年"}
                        <Divider type="vertical" />{this.props.filterName.curCollegeName}
                        <Divider type="vertical" />{this.props.filterName.curMajorName}
                        <Divider type="vertical" />{this.props.filterName.curGradeName}
                        <Divider type="vertical" />{this.props.filterName.curClassName}
                    </div>
                </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title: "消费地点分布TOP5"
                }}
                tableToExcel={{
                    table: 'address-excel',
                    filename: '消费地点分布TOP5excel下载'
                }}
            >
                {this.state.tab === '1' ? (
                    <PieChart
                        data={this.state.pieData}
                        innerRadius={0.55}
                        padding={[0,40,60,60]}
                        height={this.props.height - 115}
                        colors = {["#3aa1ff","#5ddfcf","#4ecb73","#fbd437","#eaa674","#8a7bd4","#975fe5","#749fea","#36cbcb","#74b1ea","#5254cf","#435188"]}
                        onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                    />
                ) : (
                    <React.Fragment>
                        <Table
                            dataSource={this.state.tableData}
                            columns={columns}
                            bordered
                            size="middle"
                            pagination={this.state.pagination}
                            style={{height:300}}
                        />

                        <Table ref='table' columns={columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                    </React.Fragment>
                )}
            </MainCard>
        )
    }

    componentDidMount(){
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if(JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50);
        }
    }

}


