import React, {Component} from 'react';
import {Card, Popover, Icon} from 'antd';
import {postAction} from "../../../../../../axios";
import ShowChart from './ShowChart';
import ModalTable from './modal';
import NoData from '@/common/components/NoData/NoDataComponent';


export default class XiaoFei extends Component {

    constructor(props){
        super(props);
        this.state = {
            // 消费情况
            consumptionData: [
                // { name: '土豪', value: 22 }
            ],
            visible: false,
            total:0
        }
    }

    // 打开弹框
    handleClick = () => {
        this.setState({
            visible: true
        })
    }
    // 关闭弹窗
    handleClose = () => {
        this.setState({
            visible: false
        })
    }

    //本学期消费情况 1130
    getConsumeLabel() {
        const { curYear, curGrade, curCollege, curMajor, curClass } = this.props.filterValue;
        postAction(
            "/bigdata/portrait/getConsumeLabel",
            {
                year: curYear,
                grade: curGrade,
                collegeCode: curCollege,
                majorCode: curMajor,
                classCode: curClass
            }
        ).then(
            (res) => {
                if (res.success && res.obj.length > 0) {
                    let newData = [];
                    let countTotal = [];
                    res.obj.forEach(({ count, labelName }) => {
                        newData.push({
                            name: labelName,
                            value: count
                        })
                    });
                    res.obj.map(res => countTotal += res.count)
                    this.setState({
                        consumptionData: newData,
                        total: countTotal
                    })
                }
            }
        )
    }

    componentWillReceiveProps(nextProps, nextState) {
        this.setState({
            filterValue: nextProps.filterValue
        }, () => {
            this.getConsumeLabel()
        })
    }

    componentDidMount = () => {
        this.setState({
            filterValue: this.props.filterValue
        }, () => {
            this.getConsumeLabel()
        })
    }

    render () {

        const content = (
            <div>
                <p style={{margin:"0px"}}>0-0.25     省吃俭用</p>
                <p style={{margin:"0px"}}>0.26-0.5   普罗大众</p>
                <p style={{margin:"0px"}}>0.51-0.75   小康人家</p>
                <p style={{margin:"0px"}}>0.76-1     土豪</p>
                <p><strong>消费越大越土豪</strong></p>
            </div>
        );

        if (this.state.consumptionData && this.state.consumptionData[0] && this.state.total > 0) {
            return (
                <React.Fragment>
                    {
                        this.state.visible &&
                        <ModalTable handleClose={this.handleClose} filterValue={this.props.filterValue}
                                    visible={this.state.visible} filterName={this.props.filterName}/>
                    }
                    <Card
                        style={{cursor:'pointer'}}
                        title={<div className="common-title">
                                    <i className="iconfont icon-rongyu" style={{marginRight: '5px'}}/>
                                    本学期消费情况
                                    <span>单位：人</span>
                                </div>
                        }
                        extra={
                            <Popover content={content} title="本学期消费情况标签定义">
                                <Icon type="exclamation-circle" style={{fontSize:"16px", color:"rgba(0, 0, 0, 0.85)"}} />
                            </Popover>
                        }
                        headStyle={{border: "none"}}
                        bodyStyle={{padding: 0}}
                        onClick={
                            this.handleClick
                        }
                    >
                        <ShowChart data={this.state.consumptionData}/>
                    </Card>
                </React.Fragment>
            )
        } else {
            return (
                <Card
                    title={<div className="common-title"><i className="iconfont icon-rongyu" style={{ marginRight: '5px' }}/>本学期消费情况</div>}
                    headStyle={{ border: "none" }}
                    bodyStyle={{ padding: 0 }}
                >
                    <NoData height={260} data={'nodata'} />
                </Card>
            )
        }
    }
}


