import React, {Component} from 'react';
import {Table, Card, Tooltip, Button} from 'antd';
import {postAction} from "../../../../../axios";
import ActualModal from './ActualModal';
import PredictModal from "./PredictModal";
import ReactHTMLTableToExcel from '../../../../../common/components/TableToExcel';
import ReactDOM from "react-dom";


export default class PredictTable extends Component {

    constructor(props){
        super(props);
        this.state = {
            dataSource : [
                // {
                //     key: '1',
                //     sortIndex: '1',
                //     name: '张三',
                //     actual: 0,
                //     predict: 0
                // }
            ],
            pagination: {
                current: 1, // 当前页数，
                pageSize: 10, // 每页条数
                total: 0, // 数据总条数
                showTotal: (total) => `共${total}条数据`, //显示数据总条数
                showQuickJumper: true // 显示页码快速跳转
            },
            actualVisible: false,
            predictVisible: false,
            studentNo:null,
            // 上学期
            dataDetail:[],
            pagination2:{},
            // 本学期
            dataDetail2:[],
            pagination3:{},
            tableToExcel:{
                table: 'subjects-xls',
                filename: '预测挂科人数excel下载'
            }
        }
        this.getData = this.getData.bind(this);
        this.handleTableChange = this.handleTableChange.bind(this);
    }

    handleTableChange = (pagination) => {
        this.setState(
            () => {
                return {
                    pagination: {
                        current: pagination.current, // 当前页数，
                        pageSize:pagination.pageSize, // 每页条数
                        total: pagination.total, // 数据总条数
                        showTotal: (total) => `共${total}条数据`,
                        showQuickJumper: true // 显示页码快速跳转
                    }
                }
            },
            () => {
                this.getData()
            }
        )
    }

    getData () {
        const {curYear,curCollege,curMajor,curClass} = this.props.filterValue;
        const {id} =this.props;
        postAction(
            "/bigdata/score/showDetail",
            {
                year: curYear,
                collegeCode: curCollege,
                majorCode: curMajor,
                classCode:curClass,
                failCount: id,
                pageInfo:{
                    pageNum: this.state.pagination.current,
                    pageSize: this.state.pagination.pageSize
                }
            }
        ).then(
            (res) => {
                if (res.success){
                    const pageInfo = res.obj;
                    let newData = [];
                    pageInfo.list.forEach(
                        (item, index) => {
                            newData.push({
                                key: (index + 1),
                                sortIndex: (index + 1),
                                name: item.student_name,
                                studentNo: item.student_no,
                                actual: item.factCount,
                                predict: item.failCount
                            })
                        }
                    );
                    this.setState({
                        dataSource:newData,
                        pagination: {
                            current: pageInfo.pageNum, // 当前页数，
                            pageSize: pageInfo.pageSize, // 每页条数
                            total: pageInfo.total, // 数据总条数
                            showTotal: (total) => `共${total}条数据`, //显示数据总条数
                            showQuickJumper: true // 显示页码快速跳转
                        }
                    },()=>{
                        if (this.refs['table']) {
                            const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                            const table = tableCon.querySelector('table')
                            table.setAttribute('id', 'subjects-xls')
                        }
                    })
                } else {
                    this.setState({
                        dataSource:[],
                        pagination: {
                            current: 1, // 当前页数，
                            pageSize: 10, // 每页条数
                            total: 0, // 数据总条数
                            showTotal: (total) => `共${total}条数据`, //显示数据总条数
                            showQuickJumper: true // 显示页码快速跳转
                        }
                    })
                }
            }
        )
    }

    // 上学期实际挂科
    goToActual = (record) => {
        this.setState({
            studentNo: record.studentNo,
            actualVisible: true,
        },
            () => {
                postAction(
                    "/bigdata/score/getlastTermDetail",
                    {
                        studentNo:this.state.studentNo
                    }
                ).then(
                    (res) => {
                        if(res.success){
                            let pageInfo = res.obj;
                            let newData = [];
                            pageInfo.list.forEach(
                                (item, index) => {
                                    newData.push({
                                        key: index + 1,
                                        sortIndex: index + 1,
                                        name: item.course_name,
                                        study: item.score
                                    })
                                }
                            )
                            this.setState({
                                dataDetail:newData,
                                pagination2: {
                                    current: pageInfo.pageNum, // 当前页数，
                                    pageSize: pageInfo.pageSize, // 每页条数
                                    total: pageInfo.total, // 数据总条数
                                    showTotal: (total) => `共${total}条数据`, //显示数据总条数
                                    showQuickJumper: true // 显示页码快速跳转
                                }
                            })
                        } else {
                            this.setState({
                                dataDetail:[],
                                pagination2: {
                                    current: 1, // 当前页数，
                                    pageSize: 10, // 每页条数
                                    total: 0, // 数据总条数
                                    showTotal: (total) => `共${total}条数据`, //显示数据总条数
                                    showQuickJumper: true // 显示页码快速跳转
                                }
                            })
                        }
                    }
                )
            }
        );
    }

    handleOkActual = (e) => {
        // console.log(e);
        this.setState({
            actualVisible: false,
        });
    }

    handleCancelActual = (e) => {
        // console.log(e);
        this.setState({
            actualVisible: false,
        });
    }

    // 本学期预计挂科
    goToPredict = (record) => {
        this.setState({
            studentNo: record.studentNo,
            predictVisible: true,
        },
            () => {
                postAction(
                    "/bigdata/score/getForecastDetail",
                    {
                        studentNo:this.state.studentNo
                    }
                ).then(
                    (res) => {
                        if(res.success){
                            let pageInfo = res.obj;
                            let newData = [];
                            pageInfo.list.forEach(
                                (item, index) => {
                                    newData.push({
                                        key: index + 1,
                                        sortIndex: index + 1,
                                        name: item.course_name,
                                        study: item.score
                                    })
                                }
                            )
                            this.setState({
                                dataDetail2:newData,
                                pagination3: {
                                    current: pageInfo.pageNum, // 当前页数，
                                    pageSize: pageInfo.pageSize, // 每页条数
                                    total: pageInfo.total, // 数据总条数
                                    showTotal: (total) => `共${total}条数据`, //显示数据总条数
                                    showQuickJumper: true // 显示页码快速跳转
                                }
                            })
                        } else {
                            this.setState({
                                dataDetail2:[],
                                pagination3: {
                                    current: 1, // 当前页数，
                                    pageSize: 10, // 每页条数
                                    total: 0, // 数据总条数
                                    showTotal: (total) => `共${total}条数据`, //显示数据总条数
                                    showQuickJumper: true // 显示页码快速跳转
                                }
                            })
                        }
                    }
                )
            }
        );
    }

    handleOkPredict = (e) => {
        // console.log(e);
        this.setState({
            predictVisible: false,
        });
    }

    handleCancelPredict = (e) => {
        // console.log(e);
        this.setState({
            predictVisible: false,
        });
    }

    render () {

        const columns = [{
            title: '序号',
            dataIndex: 'sortIndex',
            width: "12%"
        }, {
            title: '姓名',
            dataIndex: 'name',
        }, {
            title: '上学期实际挂科数',
            dataIndex: 'actual',
            render: (text, record) => {
                return (
                    <span style={{ cursor: "pointer", color: "#1890ff", textDecoration: "underline"}} onClick={() => { this.goToActual(record) }}>{text}</span>
                )
            }
        }, {
            title: '本学期预测挂科数',
            dataIndex: 'predict',
            render: (text, record) => {
                return (
                    <span style={{ cursor: "pointer", color: "#1890ff", textDecoration: "underline"}} onClick={() => { this.goToPredict(record) }}>{text}</span>
                )
            }
        }];

        return (
            <Card
                className="box-up"
                title={<div className="common-title">{this.props.item}</div>}
                bodyStyle={{paddingTop:0}}
                headStyle={{paddingBottom:0}}
                style={{boxShadow:"none"}}
                bordered={false}
             extra={
                 <ReactHTMLTableToExcel
                     {...(this.state.tableToExcel || {})}
                     content={
                         <Tooltip placement="top" title='导出'>
                             <Button theme="outlined" style={{padding:"0px 8px"}}>
                                 <i className="iconfont icon-daochu1" />
                             </Button>
                         </Tooltip>
                     }
                 />
             }
            >
                <React.Fragment>
                    <Table
                        dataSource={this.state.dataSource}
                        columns={columns}
                        bordered={true}
                        size="middle"
                        pagination={this.state.pagination}
                        onChange={this.handleTableChange}
                    />

                    <Table ref='table' columns={columns} dataSource={this.state.dataSource}  pagination={false} style={{display:'none'}} />
                </React.Fragment>

                <ActualModal
                    dataDetail={this.state.dataDetail}
                    pagination={this.state.pagination2}
                    visible={this.state.actualVisible}
                    onOk={this.handleOkActual}
                    onCancel={this.handleCancelActual}
                />

                <PredictModal
                    dataDetail={this.state.dataDetail2}
                    pagination={this.state.pagination3}
                    visible={this.state.predictVisible}
                    onOk={this.handleOkPredict}
                    onCancel={this.handleCancelPredict}
                />
            </Card>
        )
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if(JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50);
        }
    }

}


