import React, {Component, Fragment} from 'react';
import {Row, Col} from 'antd';

import Health from "./ShangWangJianKang/index";
import TimeLength from "./ShiDuanFenBu/index";
import TimeTrend from "./ShiChangQuShi/index";
import ShiChangDuiBi from "./ShiChangDuiBi";


export default class Internet extends Component {
    render () {
        return (
            <Fragment>
                <Row>
                    <Col span={12} className="card-padding card-right">
                        <Health height={400} filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>

                    <Col span={12} className="card-padding card-left">
                        <TimeLength height={400} filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>

                    <Col span={24} className="card-padding">
                        <ShiChangDuiBi filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>

                    <Col span={24} className="card-padding card-bottom">
                        <TimeTrend height={400} filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>
                </Row>
            </Fragment>
        )
    }
}


