import React, { Component } from 'react';
import { Chart, Geom, Axis, Tooltip, Coord, Label, Guide } from "bizcharts"
import { DataSet } from '@antv/data-set';
import NoData from '../../../../../../../common/components/NoData/NoDataComponent';
const { Html } = Guide;

// 饼图
class ShowChart extends Component {
    render() {
		const data = this.props.data;
        if (data && data[0]) {
			let field, dimension; // 获取关键字	
            dimension = Object.keys(data[0])[0]; //获取键
            field = Object.keys(data[0])[1]; //获取值
			
			const { DataView } = DataSet; // 数据视图
			const dv = new DataView();
			
			// 初始化参数
			dv.source(data).transform({
				type: 'percent',
				field: field,
				dimension: dimension,
				as: 'percent'
			});
			
			const cols = {
				percent: {
					formatter: val => {
						val = Math.round(val * 100) + '%';
						return val;
					}
				}
			}
			
			return (
				<Chart height={240} data={dv} scale={cols} padding={[20, 60, 30, 80]} forceFit>
					<Coord type='theta' radius={0.7} innerRadius={0.5}/>
					<Axis name="percent" />
					<Tooltip
						showTitle={false}
						itemTpl='<li><span style="background-color:{color};" class="g2-tooltip-marker"></span>{name}: {value}</li>'
					/>
					<Geom
						type="intervalStack"
						position="percent"
						color={dimension}
						tooltip={this.props.geomTooltip || [dimension+'*percent*'+field, (item, percent,val) => {
							percent = Math.round(percent * 100) + '%';
							return {
								name: item,
								value: val + ' (' + percent +')'
							};
						}]}
						style={{ lineWidth: 1, stroke: '#fff' }}
					>
						<Label content='percent' formatter={(val, item) => {
							return item.point[dimension] + ': ' + val;
						}} />
					</Geom>
					<Guide>
						<Html
							position={["50%", "50%"]}
							html={`
								<div style="color:#8c8c8c;font-size:16px;text-align: center;width: 10em;">
									在籍<br>情况
								</div>
								`}
							alignX="middle"
							alignY="middle"
						/>
					</Guide>
				</Chart>
			)
			
        } else {
            // 无数据时
            return (<NoData height={240} data={'nodata'}/>);
        }
    }
}

export default ShowChart;


