import React, {Component, Fragment} from 'react';
import {Row, Col} from 'antd';

import ZhengTi from "./ZhengTi";
import XiaoQu from "./XiaoQu";
import NianJi from "./NianJi";
import NationalCard from "./shaoshumingzu";
import SpecialCard from "./tezhaosheng";
import GangCard from "./gangaotai";
import ZhuangxiangCard from "./zhuanxiang";


export default class Student extends Component {
    render () {
        return (
            <Fragment>
                <Row>
                    {/*学生整体分析*/}
                    <Col span={24} className="card-padding right-20 left-20">
                        <ZhengTi filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>

                    {/*各校区分布*/}
                    <Col span={12} className="card-padding left-20 card-right">
                        <XiaoQu height={430} filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>

                    {/*各年级分布*/}
                    <Col span={12} className="card-padding right-20 card-left">
                        <NianJi height={430} filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>

                    {/*少数民族学生分析*/}
                    <Col span={24} className="card-padding right-20 left-20">
                        <NationalCard filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>

                    {/*内高、预科来源少数民族学生分析*/}
                    <Col span={24} className="card-padding right-20 left-20">
                        <SpecialCard filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>

                    {/*港澳台侨学生分析*/}
                    <Col span={24} className="card-padding right-20 left-20">
                        <GangCard filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>

                    {/*专项招生学生分析（高校专项/国家专项）*/}
                    <Col span={24} className="card-padding right-20 left-20 card-bottom">
                        <ZhuangxiangCard filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>
                </Row>
            </Fragment>
        )
    }
}


