import React, {Component, Fragment} from 'react';
import {Divider, Table, Card, Tooltip, Button} from 'antd';
import {postAction} from "../../../../../axios";
import NoData from "../../../../../common/components/NoData/NoDataComponent";
import ReactHTMLTableToExcel from '../../../../../common/components/TableToExcel';
import ReactDOM from "react-dom";
import ShowList from "./ShowList";
import {hostUrl} from "../../../../../env";


export default class TopTenCity extends Component {

    constructor(props){
        super(props);
        this.state = {
            data : [
                // {"cityName" : "北京", "peopleNum" : 1432}
            ],
            tableData: [
                // { key: '1', sortIndex: '1', city: '九龙湖校区', number: '2901' }
            ],
            pagination: {
                pageSize: 5, // 每页条数
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            },
            tableToExcel:{
                table: 'city-excel',
                filename: '毕业去向前十的城市excel下载'
            }
        }
        this.getData = this.getData.bind(this);
    }

    getData () {
        const {curYear,curCollege,curMajor,curClass,curGrade} = this.props.filterValue;
        postAction(
            "/bigdata/employment/employmentCityAnalyze",
            {
                year: curYear,
                collegeCode: curCollege,
                majorCode: curMajor,
                classCode: curClass,
                grade: curGrade,
                dataType: this.state.tab,
            }
        ).then(
            (res) => {
                if (res.success && res.obj.length > 0) {
                    const pageInfo = res.obj;
                    let _newData = [];
                    let _tableData = [];

                    pageInfo.forEach(
                        (item, index) => {

                            _newData.push({
                                cityName: item.cityName,
                                peopleNum: item.peopleNum
                            })

                            _tableData.push({
                                key: (index + 1),
                                sortIndex: (index + 1),
                                city: item.cityName,
                                number: item.peopleNum
                            })

                        }
                    );

                    this.setState({
                        data:_newData,
                        tableData: _tableData,
                        pagination: {
                            pageSize: 5, // 每页条数
                            showQuickJumper: true,
                            total: pageInfo.length,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        }
                    },() => {
                        if (this.refs['table']) {
                            const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                            const table = tableCon.querySelector('table')
                            table.setAttribute('id', 'city-excel')
                        }
                    })
                } else {
                    this.setState({
                        data:[],
                        tableData: [],
                        pagination: {
                            pageSize: 5, // 每页条数
                            showQuickJumper: true,
                            total: null,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        }
                    })
                }
            }
        )
    }

    //导出明细
    handleMingxi = () =>{

        const {curYear,curCollege,curMajor,curGrade,curClass} = this.props.filterValue;
        window.location.href=`${hostUrl}/bigdata/employment/exportEmploymentDist?year=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}&classCode=${curClass}`;
    }

    render () {

        const columns = [
            {
                title:'序号',
                dataIndex: 'sortIndex',
                width: '10%',
            },{
                title: '城市',
                dataIndex: 'city',
            },{
                title: '人数',
                dataIndex: 'number',
            }
        ];

        if (!this.state.data || this.state.data.length <= 0) {
            return(
                <Card
                    title={<div className="common-title">毕业去向前十的城市</div>}
                    extra={
                        <Fragment>
                            <div className="filter-text">{this.props.filterName.curYearName}
                                <Divider type="vertical" />{this.props.filterName.curCollegeName}
                                <Divider type="vertical" />{this.props.filterName.curMajorName}
                            </div>
                            <ReactHTMLTableToExcel
                                {...(this.state.tableToExcel || {})}
                                content={
                                    <Tooltip placement="top" title='导出'>
                                        <Button theme="outlined" style={{padding:"0px 8px"}}>
                                            <i className="iconfont icon-daochu1" />
                                        </Button>
                                    </Tooltip>} />
                            <Tooltip placement="top" title='导出明细'>
                                <Button onClick={this.handleMingxi} style={{marginLeft:10,padding:"0px 8px"}} theme="outlined">
                                    <i className="iconfont icon-daochu" />
                                </Button>
                            </Tooltip>
                        </Fragment>
                    }
                >
                    <div style={{height:310}}>
                        <NoData data="nodata"/>
                    </div>
                </Card>
            )
        } else {
            return (
                <Card
                    bodyStyle={{height:360}}
                    title={<div className="common-title">毕业去向前十的城市</div>}
                    extra={
                        <Fragment>
                            <div className="filter-text">{this.props.filterName.curYearName}
                                <Divider type="vertical" />{this.props.filterName.curCollegeName}
                                <Divider type="vertical" />{this.props.filterName.curMajorName}
                            </div>
                            <ReactHTMLTableToExcel
                                {...(this.state.tableToExcel || {})}
                                content={
                                    <Tooltip placement="top" title='导出'>
                                        <Button theme="outlined" style={{padding:"0px 8px"}}>
                                            <i className="iconfont icon-daochu1" />
                                        </Button>
                                    </Tooltip>
                                }
                            />

                            <Tooltip placement="top" title='导出明细'>
                                <Button onClick={this.handleMingxi} style={{marginLeft:10,padding:"0px 8px"}} theme="outlined">
                                    <i className="iconfont icon-daochu" />
                                </Button>
                            </Tooltip>
                        </Fragment>
                    }
                >
                    <ShowList
                        data={this.state.data}
                    />
                    <Table
                        ref='table'
                        columns={columns}
                        dataSource={this.state.tableData}
                        pagination={false}
                        style={{display:'none'}}
                    />
                </Card>
            )
        }
    }


    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)
        }
    }

}


