import React from 'react'
import { Chart, Coord, Tooltip, Geom, Label} from 'bizcharts'
import NoData from '../../../../../../common/components/NoData/NoDataComponent';

class ShowChart extends React.Component {
	render () {
		if (this.props.data && this.props.data[0]) {
			return (
				<Chart
					height={240}
					data={this.props.data}
					forceFit
					padding={[40, 100, 20, 180]}
				>
					<Coord
						type="theta"
						startAngle={Math.PI * (3 / 2)}
						endAngle={Math.PI * 2}
					/>
					<Tooltip showTitle={false} />
					<Geom 
						type="intervalStack" 
						position="value" 
						color={['name',["#1890FF","#13C2C2","#2FC25B","#F5A623","#8543E0","#3436C7","#223273"]]}>
						<Label content="name" formatter={(val, item) => {
								return item.point.name + ": " + item.point.value
						}}/>
					</Geom>
				</Chart>
			)
		} else{
			return (<NoData height={260} data={'nodata'}/>);
		}
	}
}

export default ShowChart


