import React, {Component} from 'react';


export default class ShowList extends Component {

    // 第一次加载完触发事件
    onFirstLoad(ev){
        if(this.props.onFirstLoad){
            this.props.onFirstLoad(ev);
        }
    }

    render () {

        return (
            <ul className="go-city">
                {this.props.data.map((item, index) => {
                    if (index < 3) {
                        return (
                            <li key={index}>
                                <span className="number select-active">{index + 1}</span>
                                <span className="city">{item.cityName}</span>
                                <span className="count">{item.peopleNum}人</span>
                            </li>
                        )
                    } else {
                        return (
                            <li key={index}>
                                <span className="number">{index + 1}</span>
                                <span className="city">{item.cityName}</span>
                                <span className="count">{item.peopleNum}人</span>
                            </li>
                        )
                    }
                })}
            </ul>
        )
    }
}


