import React, {Component} from 'react';
import {Table, Divider, message} from 'antd';
import {postAction} from "../../../../../axios";
import NoData from "../../../../../common/components/NoData/NoDataComponent";
import MainCard from "../../../../../common/components/MainCard/index";
import ReactDOM from "react-dom";
import ShowChart from "./ShowChart";


export default class OnTime extends Component {

    constructor(props) {
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            data : [
                // { month: '1月',  "未迟到": 3, "迟到": 0 },
                // { month: '2月',  "未迟到": 0, "迟到": -5 }
            ],
            tableData: [
                // { key: '1', sortIndex: '1', time: '九龙湖校区', count: '2901' }
            ],
            pagination: {
                pageSize: 5, // 每页条数
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            }
        }
        this.getData = this.getData.bind(this);
    }

    getData () {
        const {curYear,curCollege,curMajor,curGrade,curClass} = this.props.filterValue;
        postAction(
            "/bigdata/attendClass/lateMinute",
            {
                year: curYear,
                collegeCode: curCollege,
                majorCode: curMajor,
                grade: curGrade,
                classCode: curClass
            }
        ).then(
            (res) => {
                if (res.success) {
                    const pageInfo = res.obj;
                    let _newData = [];
                    let _tableData = [];

                    pageInfo.forEach(
                        (item, index) => {

                            _newData.push({
                                month: item.month,
                                "未迟到": item.ontime,
                                "迟到": item.tolate
                            })

                            _tableData.push({
                                key: (index + 1),
                                sortIndex: (index + 1),
                                item: item.ontime,
                                count: item.tolate
                            })
                        }
                    );
                    this.setState({
                        data:_newData,
                        tableData: _tableData,
                        pagination: {
                            pageSize: 5, // 每页条数
                            showQuickJumper: true,
                            total: pageInfo.length,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        }
                    })
                } else {
                    this.setState({
                        data:[],
                        tableData: [],
                        pagination: {
                            pageSize: 5, // 每页条数
                            showQuickJumper: true,
                            total: null,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        }
                    })
                }
            }
        )
    }

    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'zhunshi-excel')
            }
            this.getData()
        })
    }

    //导出明细
    getDetail () {

        message.warning('暂无数据');
        // const {curYear,curCollege,curMajor,curGrade} = this.props.filterValue;
        // window.location.href=`${hostUrl}/bigdata/student/exportStudentDist?year=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}`;
    }

    render () {

        const columns = [
            {
                title:'序号',
                dataIndex: 'sortIndex',
                width: '10%',
            },{
                title: '未迟到',
                dataIndex: 'item',
            },{
                title: '迟到',
                dataIndex: 'count',
            }
        ];

        if (!this.state.data || this.state.data.length <= 0) {
            return(
                <MainCard
                    className="main-card"
                    title={<div className="common-title">准时上课情况
                        <div className="filter-text">{`${this.props.filterName.curYearName.substring(0,this.props.filterName.curYearName.length - 1)}学年-${Number((this.props.filterName.curYearName).substring(0,this.props.filterName.curYearName.length - 1)) + 1}学年`}
                            <Divider type="vertical" />{this.props.filterName.curCollegeName}
                            <Divider type="vertical" />{this.props.filterName.curMajorName}
                            <Divider type="vertical" />{this.props.filterName.curGradeName}
                            <Divider type="vertical" />{this.props.filterName.curClassName}
                        </div>
                    </div>}
                    tab={this.state.tab}
                    changeTab={this.changeTab}
                    downloadChart={{
                        chartIns: this.state.chartIns,
                        title: "准时上课情况"
                    }}
                    detailCallback={this.getDetail.bind(this)}
                    tableToExcel={{
                        table: 'zhunshi-excel',
                        filename: '准时上课情况excel下载'
                    }}
                >
                    <div style={{height:310}}>
                        <NoData data="nodata"/>
                    </div>
                </MainCard>
            )
        } else {
            return (
                <MainCard
                    className="main-card"
                    title={<div className="common-title">准时上课情况
                        <div className="filter-text" style={{top:20, left:0}}>{`${this.props.filterName.curYearName.substring(0,this.props.filterName.curYearName.length - 1)}学年-${Number((this.props.filterName.curYearName).substring(0,this.props.filterName.curYearName.length - 1)) + 1}学年`}
                            <Divider type="vertical" />{this.props.filterName.curCollegeName}
                            <Divider type="vertical" />{this.props.filterName.curMajorName}
                            <Divider type="vertical" />{this.props.filterName.curGradeName}
                            <Divider type="vertical" />{this.props.filterName.curClassName}
                        </div>
                    </div>}
                    tab={this.state.tab}
                    changeTab={this.changeTab}
                    downloadChart={{
                        chartIns: this.state.chartIns,
                        title: "准时上课情况"
                    }}
                    detailCallback={this.getDetail.bind(this)}
                    tableToExcel={{
                        table: 'zhunshi-excel',
                        filename: '准时上课情况excel下载'
                    }}
                >

                    {this.state.tab === '1' ? (
                    <ShowChart
                        data={this.state.data}
                        height={this.props.height}
                        padding={[10,30,60,60]}
                        onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                    />
                    ) : (
                        <React.Fragment>
                            <Table
                                dataSource={this.state.tableData}
                                columns={columns}
                                bordered
                                size="middle"
                                pagination={this.state.pagination}
                                style={{height:300}}
                            />

                            <Table ref='table' columns={columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                        </React.Fragment>
                    )}
                </MainCard>
            )
        }
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if(JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50);
        }
    }
}


