import React, { Component } from 'react';
import { Card, Popover, Icon } from 'antd';
import { postAction } from "../../../../../../axios";
import ShowChart from './ShowChart';
import NoData from '@/common/components/NoData/NoDataComponent';
import ModalTable from './modal';


export default class XueXiChengJi extends Component {
    constructor(props) {
        super(props);
        this.state = {
            filterValue: {
                curYear: '',
                curGrade: '',
                curCollege: '',
                curMajor: '',
                curClass: ''
            },
            // 学习成绩
            studyData: [
                // { name: "学霸", "上学期": 12, "下学期": 23 }
            ],
            visible: false,
        }
    }

    // 打开弹框
    handleClick = () => {
        this.setState({
            visible: true
        })
    }
    // 关闭弹窗
    handleClose = () => {
        this.setState({
            visible: false
        })
    }

    //学习成绩 1130
    getStudyLabel() {
        const { curYear, curGrade, curCollege, curMajor, curClass } = this.state.filterValue
        postAction(
            "/bigdata/portrait/getStudyLabel",
            {
                year: curYear,
                grade: curGrade,
                collegeCode: curCollege,
                majorCode: curMajor,
                classCode: curClass
            }
        ).then(
            (res) => {
                if (res.success) {
                    let newData = []
                    res.obj.forEach((item) => {
                        newData.push(
                            {
                                name: item.semester,
                                "学霸": item.xueba,
                                "学优": item.xueyou,
                                "学中": item.xuezhong,
                                "学良": item.xueliang,
                                "学差": item.xuecha,
                            }
                        )
                    })
                    this.setState({ studyData: newData });
                }
            }
        )
    }

    componentWillReceiveProps(nextProps, nextState) {
        this.setState({
            filterValue: nextProps.filterValue
        }, () => {
            this.getStudyLabel()
        })
    }

    componentDidMount = () => {
        this.setState({
            filterValue: this.props.filterValue
        }, () => {
            this.getStudyLabel()
        })
    }

    render() {

        const content = (
            <div>
                <p style={{margin:"0px"}}>0-20分     学差</p>
                <p style={{margin:"0px"}}>21-40分    学中</p>
                <p style={{margin:"0px"}}>41-60分    学良</p>
                <p style={{margin:"0px"}}>61-80分    学优</p>
                <p style={{margin:"0px"}}>81-100分   学霸</p>
            </div>
        );

        if (this.state.studyData && this.state.studyData[0]) {
            return (
                <React.Fragment>
                    {
                        this.state.visible &&<ModalTable handleClose={this.handleClose} filterValue={this.props.filterValue}  visible={this.state.visible} filterName={this.props.filterName} />
                    }

                    <Card
                        style={{cursor:'pointer'}}
                        title={<div className="common-title">
                            <i className="iconfont icon-chengjizhongxin" style={{ marginRight: '5px' }}/>学习成绩
                            <span>单位：人</span>
                        </div>}
                        extra={
                            <Popover content={content} title="学习成绩标签定义">
                                <Icon type="exclamation-circle" style={{fontSize:"16px", color:"rgba(0, 0, 0, 0.85)"}} />
                            </Popover>
                        }
                        headStyle={{ border: "none", maxHeight: "40px", minHeight: "0" }}
                        bodyStyle={{ padding: 0 }}
                        onClick={
                            this.handleClick
                        }
                    >
                        <ShowChart
                            data={this.state.studyData}
                            height={260}
                            padding={[40, 30, 60, 60]}
                            colors={["#3aa1ff", "#5ddfcf", "#4ecb73"]}
                        />
                    </Card>
                </React.Fragment>

            );
        } else {
            return (
                <Card
                    title={<div className="common-title">
                        <i className="iconfont icon-chengjizhongxin" style={{ marginRight: '5px' }}/>学习成绩
                    </div>}
                    headStyle={{ border: "none", maxHeight: "40px", minHeight: "0" }}
                    bodyStyle={{ padding: 0 }}
                >
                    <NoData height={260} data={'nodata'} />
                </Card>
            );
        }
    }
}





