import React, {Component} from 'react';
import {Divider, Table} from 'antd';

import PieChart from "../../../../../common/components/BizCharts/PieChart";
import {postAction} from "../../../../../axios";
import MainCard from "../../../../../common/components/MainCard/index";
import ReactDOM from "react-dom";
import {hostUrl} from "../../../../../env";


export default class GraduationTo extends Component {

    constructor(props){
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            pieData : [
                // { item: '就业', count: 40 },
            ],
            totalNum:null,
            tableData: [
                // { key: '1', sortIndex: '1', goTo: '九龙湖校区', number: '2901' }
            ],
            pagination: {
                pageSize: 5, // 每页条数
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            }
        }
        this.getData = this.getData.bind(this);
    }

    getData () {
        const {curYear,curCollege,curMajor,curClass,curGrade} = this.props.filterValue;
        postAction(
            "/bigdata/employment/employmentTypeAnalyze",
            {
                year: curYear,
                collegeCode: curCollege,
                majorCode: curMajor,
                classCode: curClass,
                grade: curGrade,
                dataType: this.state.tab,
            }
        ).then(
            (res) => {
                if (res.success) {
                    const pageInfo = res.obj[0].unitProperties;
                    let _chartData = [];
                    let _tableData = [];

                    pageInfo.forEach(
                        (item, index) => {

                            _chartData.push({
                                item: item.name,
                                count: item.peopleNum
                            })

                            _tableData.push({
                                key: (index + 1),
                                sortIndex: (index + 1),
                                goTo: item.name,
                                number: item.peopleNum
                            })

                        }
                    );
                    this.setState({
                        pieData: _chartData,
                        tableData: _tableData,
                        totalNum: res.obj[0].total,
                        pagination: {
                            pageSize: 5, // 每页条数
                            showQuickJumper: true,
                            total: pageInfo.length,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        }
                    })
                } else {
                    this.setState({
                        pieData: [],
                        tableData: [],
                        totalNum:null,
                        pagination: {
                            pageSize: 5, // 每页条数
                            showQuickJumper: true,
                            total: null,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        }
                    })
                }
            }
        )
    }

    //导出明细
    getDetail () {
        const {curYear,curCollege,curMajor,curGrade,curClass} = this.props.filterValue;
        window.location.href=`${hostUrl}/bigdata/employment/exportEmploymentDist?year=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}&classCode=${curClass}`;
    }

    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'goto-excel')
            }
            this.getData()
        })
    }

    render () {

        const columns = [
            {
                title:'序号',
                dataIndex: 'sortIndex',
                width: '10%',
            },{
                title: '毕业去向',
                dataIndex: 'goTo',
            },{
                title: '人数',
                dataIndex: 'number',
            }
        ];

        return (
            <MainCard
                className="main-card"
                title={<div className="common-title">毕业去向
                        <span>单位：人</span>
                        <span style={{float:"right",color:"#4a4a4a",fontSize:"14px",marginRight:"10px"}}>总人数：
                        <span style={{fontSize:"16px",color:"#1890ff"}}>{this.state.totalNum}</span>
                    </span>
                    <div className="filter-text" style={{top:'22px',left:'0px'}}>{this.props.filterName.curYearName}
                        <Divider type="vertical" />{this.props.filterName.curCollegeName}
                        <Divider type="vertical" />{this.props.filterName.curMajorName}
                        {/*<Divider type="vertical" />{this.props.filterName.curGradeName}*/}
                        {/*<Divider type="vertical" />{this.props.filterName.curClassName}*/}
                    </div>
                </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title: "毕业去向"
                }}
                tableToExcel={{
                    table: 'goto-excel',
                    filename: '毕业去向excel下载'
                }}
                detailCallback={this.getDetail.bind(this)}
            >
                {this.state.tab === '1' ? (
                    <PieChart
                        data={this.state.pieData}
                        innerRadius={0.55}
                        height={this.props.height - 90}
                        padding={[0,0,50,0]}
                        colors = {["#3aa1ff","#5ddfcf","#4ecb73","#fbd437","#eaa674","#8a7bd4","#975fe5","#749fea","#36cbcb","#74b1ea","#5254cf","#435188"]}
                        onFirstLoad={(chartIns) => {
                            this.setState({ chartIns })
                        }}
                    />
                ) : (
                    <React.Fragment>
                        <Table
                            dataSource={this.state.tableData}
                            columns={columns}
                            bordered
                            size="middle"
                            pagination={this.state.pagination}
                            style={{height:300}}
                        />

                        <Table ref='table' columns={columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                    </React.Fragment>
                )}

            </MainCard>
        )
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)
        }
    }

}


