import React, { Component } from 'react';
import { Card } from 'antd'
import Modal from './modal'
import { postAction } from "@/axios";
import ShenFen from './chart'
class ShenFenTeDian extends Component {
    state = {
        shenfenVisible: false,
        // 身份特点
        identityData: {
            // 来源 
            from: [
                // { name: '来源:', "香港": 15, "澳门": 35, "台湾": 40, "华侨": 10 } //数据是百分比
            ],
            // 招生 数据是百分比
            recruit: [
                // {
                //     name: '招生:',
                //     "国家专项": 15,
                //     "高校专项": 15,
                //     "预科入学": 10,
                //     "内地新疆班": 10,
                //     "自主招生": 10,
                //     "高水平运动员": 10,
                //     "少年生": 10,
                //     "高水平运动队": 5,
                //     "高水平艺术团": 5,
                //     "外语保送生": 5,
                //     "综合评价": 5,
                // }
            ],
            //其他 数据是百分比
            others: [
                // { name: '其他:', "家庭经济困难学生": 100 }
            ],
        },
    }
    //身份特点 1130
    getIdentityLabel() {
        const { curYear, curGrade, curCollege, curMajor, curClass } = this.props.filterValue
        postAction(
            "/bigdata/portrait/getIdentityLabel",
            {
                year: curYear,
                grade: curGrade,
                collegeCode: curCollege,
                majorCode: curMajor,
                classCode: curClass
            }
        ).then(
            (res) => {
                if (res.success) {
                    const { other, source, special } = res.obj
                    if (special && other && source && other.length > 0 && source.length > 0) {
                        let otherAll = [], peculiarAll = [], sourceAll = []
                        let obj = { name: '招生' }, otherObj = { name: '其他' }, sourceObj = { name: '来源' }
                        for (let i = 0; i < other.length; i++) { //第一组
                            otherObj[other[i].labelName] = other[i].count
                        }
                        for (let i = 0; i < special.length; i++) {//第二组
                            obj[special[i].labelName] = special[i].count
                        }
                        for (let i = 0; i < source.length; i++) {//第三组
                            sourceObj[source[i].labelName] = source[i].count
                        }
                        peculiarAll.push(obj)
                        otherAll.push(otherObj)
                        sourceAll.push(sourceObj)
                        this.setState({
                            identityData: {
                                from: sourceAll,
                                recruit: peculiarAll,
                                others: otherAll
                            }
                        });
                    } else {
                        this.setState({
                            identityData: {
                                from: [],
                                recruit: [],
                                others: []
                            }
                        })
                    }
                }
            }
        )
    }
    componentWillReceiveProps(nextProps, nextState) {
        this.setState({
            filterValue: nextProps.filterValue
        }, () => {
            this.getIdentityLabel()
        })
    }
    componentDidMount = () => {
        this.setState({
            filterValue: this.props.filterValue
        }, () => {
            this.getIdentityLabel()
        })
    }
    // 打开弹框
    handleClick = () => {
        this.setState({
            shenfenVisible: true
        })
    }
    // 关闭弹窗 
    handleClose = () => {
        this.setState({
            shenfenVisible: false
        })
    }
    render() {
        return (
            <React.Fragment>
                {
                    this.state.shenfenVisible && <Modal handleClose={this.handleClose} filterValue={this.props.filterValue} visible={this.state.shenfenVisible} filterName={this.props.filterName} />
                }
                <Card
                    title={<div className="common-title">
                        <i className="iconfont icon-ziliaoguanli" style={{ marginRight: '5px' }}/>身份特点
                        <span>单位：人</span>
            </div>}
                    headStyle={{ border: "none" }}
                    bodyStyle={{ padding: 0 }}
                    onClick={this.handleClick}
                    style={{ cursor: 'pointer' }}
                >

                    <ShenFen
                        data={this.state.identityData.from}
                        padding={[15, 30, 50, 60]}
                        height={105}
                    />
                    <div className="identity-recruit">
                        <ShenFen
                            data={this.state.identityData.recruit}
                            padding={[15, 30, 100, 60]}
                            height={155}
                        />
                    </div>
                    <ShenFen
                        data={this.state.identityData.others}
                        padding={[15, 30, 60, 60]}
                        height={115}
                    />
                </Card>
            </React.Fragment>

        )
    }
}
export default ShenFenTeDian;





