import React, {Component, Fragment} from 'react';
import {Row, Col, Card} from 'antd';

import ZhuangxiangDistribution from "./ZhuangxiangDistribution";
import {postAction} from "../../../../../axios";


export default class ZhuangxiangCard extends Component {

    constructor(props){
        super(props);
        this.state = {
            total: 0,
            gaoxiao: 0,
            guojia: 0
        }
        this.getData = this.getData.bind(this);
    }

    getData() {
        const {curYear,curCollege,curMajor,curGrade} = this.props.filterValue;
        postAction(
            "/bigdata/student/getSpeStudentDist",
            {
                year: curYear,
                collegeCode: curCollege,
                majorCode: curMajor,
                grade: curGrade,
            }
        ).then(
            (res) => {
                if (res.success){
                    this.setState({
                        total: res.obj.count.total,
                        gaoxiao: res.obj.count.gaoxiao,
                        guojia: res.obj.count.guojia
                    })
                } else {
                    this.setState({
                        total: 0,
                        gaoxiao: 0,
                        guojia: 0
                    })
                }
            }
        )
    }

    render () {
        return (
            <Fragment>
                <Card
                    title={<div className="common-title">专项招生学生分析（高校专项/国家专项）</div>}
                    extra={<span className="title-font">全部
                        <span className="title-number">{this.state.total}</span>
                        高校专项
                        <span className="title-number">{this.state.gaoxiao}</span>
                        国家专项
                        <span className="title-number">{this.state.guojia}</span>
                    </span>}
                    bodyStyle={{display:"none"}}
                />

                <Row>
                    <Col span={24} className="only-top">
                        <ZhuangxiangDistribution height={400} filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>
                </Row>
            </Fragment>
        )
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)
        }
    }
}


