import React, {Component} from 'react';
import {postAction} from "../../../../../../../axios";
import ShowChart from './ShowChart';
import ModalTable from './modal';
import NoData from '@/common/components/NoData/NoDataComponent';


export default class ZaiJi extends Component {

    constructor(props){
        super(props);
        this.state = {
            // 在籍情况
            studentStatus: [
                // { name: '在籍不在校', value: 6 }
            ],
            visible: false,
            total: 0
        }
    }

    // 打开弹框
    handleClick = () => {
        this.setState({
            visible: true
        })
    }
    // 关闭弹窗
    handleClose = () => {
        this.setState({
            visible: false
        })
    }

    //在籍情况 1130
    getSchStatusLabel() {
        const { curYear, curGrade, curCollege, curMajor, curClass } = this.props.filterValue;
        postAction(
            "/bigdata/portrait/getSchStatusLabel",
            {
                year: curYear,
                grade: curGrade,
                collegeCode: curCollege,
                majorCode: curMajor,
                classCode: curClass
            }
        ).then(
            (res) => {
                if (res.success) {
                    const zj = res.obj
                    if (zj && zj.length > 0 ) {
                        let newData = [];
                        let countTotal = [];
                        zj.forEach(({ count, labelName }) => {
                            newData.push({
                                name: labelName,
                                value: count
                            })
                        });
                        zj.map(res => countTotal += res.count)

                        this.setState({
                            studentStatus: newData,
                            total: countTotal
                        })
                    } else {
                        this.setState({
                            studentStatus: [],
                            total: 0
                        })
                    }

                }
            }
        )
    }

    componentWillReceiveProps(nextProps, nextState) {
        this.setState({
            filterValue: nextProps.filterValue
        }, () => {
            this.getSchStatusLabel()
        })
    }

    componentDidMount = () => {
        this.setState({
            filterValue: this.props.filterValue
        }, () => {
            this.getSchStatusLabel()
        })
    }

    render () {

        if (this.state.studentStatus && this.state.studentStatus[0] && this.state.total > 0) {
            return (
                <React.Fragment>
                    {
                        this.state.visible &&
                        <ModalTable handleClose={this.handleClose} filterValue={this.props.filterValue}
                                    visible={this.state.visible} filterName={this.props.filterName}/>
                    }
                    <div
                        onClick={
                            this.handleClick
                        }
                        style={{cursor:'pointer'}}
                    >
                        <ShowChart data={this.state.studentStatus}/>
                    </div>
                </React.Fragment>
            )
        } else {
            return (
                <div>
                    <NoData height={260} data={'nodata'} />
                </div>
            )
        }
    }
}


