import React, {Component, Fragment} from 'react';
import {Row, Col} from 'antd';

import ZhanBi from "./ZhanBi";
import OnTime from "./ZhunShiShangKe/index";
import TaoKe from "./TaoKe";


export default class Shangke extends Component {
    render () {
        return (
            <Fragment>
                <Row>
                    <Col span={12} className="card-padding card-right">
                        <ZhanBi height={400} filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>

                    <Col span={12} className="card-padding card-left">
                        <OnTime height={400} filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>

                    <Col span={24} className="card-padding card-bottom">
                        <TaoKe height={620} filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>
                </Row>
            </Fragment>
        )
    }
}


