import React, {Component} from 'react';
import {Table, Divider} from 'antd';

import PieChart from "../../../../common/components/BizCharts/PieChart";
import {postAction} from "../../../../axios";
import MainCard from "../../../../common/components/MainCard/index";
import ReactDOM from "react-dom";


export default class Classification extends Component {

    constructor(props) {
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            pieData : [
                // { item: '计算机', count: 40 },
            ],
            tableData: [
                // { key: '1', sortIndex: '1', type:'1', count: '九龙湖校区'}
            ],
            pagination: {
                pageSize: 5, // 每页条数
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            }
        }
        this.getData = this.getData.bind(this);
    }

    getData () {
        const {curYear,curCollege,curMajor,curClass,curGrade} = this.props.filterValue;
        postAction(
            "/bigdata/book/bookBorrowTypeAnalyze",
            {
                year:curYear,
                collegeCode:curCollege,
                majorCode:curMajor,
                classCode:curClass,
                grade:curGrade
            }
        ).then(
            (res) => {
                if (res.success){
                    const pageInfo = res.obj;
                    let _newData = [];
                    let _tableData = [];

                    pageInfo.forEach(
                        (item, index) => {

                            _newData.push({
                                item: item.bookType,
                                count: item.typeBorrowTimes
                            })

                            _tableData.push({
                                key: (index + 1),
                                sortIndex: (index + 1),
                                type: item.bookType,
                                count: item.typeBorrowTimes,
                            })
                        }
                    );
                    this.setState({
                        pieData: _newData,
                        tableData: _tableData,
                        pagination: {
                            pageSize: 5, // 每页条数
                            showQuickJumper: true,
                            total: pageInfo.length,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        }
                    })
                } else {
                    this.setState({
                        pieData:[],
                        tableData: [],
                        pagination: {
                            pageSize: 5, // 每页条数
                            showQuickJumper: true,
                            total: null,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        }
                    })
                }
            }
        )
    }

    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'borrow-excel')
            }
            this.getData()
        })
    }

    render () {

        const columns = [
            {
                title:'序号',
                dataIndex: 'sortIndex',
                width: '10%',
            },{
                title: '借阅图书分类',
                dataIndex: 'type',
            },{
                title: '数量',
                dataIndex: 'count',
            }
        ];

        return (
            <MainCard
                className="main-card"
                title={<div className="common-title">借阅图书分类
                    <span>单位：本</span>
                    <div className="filter-text" style={{top:'22px',left:'0px'}}>{this.props.filterName.curYearName}
                        <Divider type="vertical" />{this.props.filterName.curCollegeName}
                        <Divider type="vertical" />{this.props.filterName.curMajorName}
                        <Divider type="vertical" />{this.props.filterName.curGradeName}
                        <Divider type="vertical" />{this.props.filterName.curClassName}
                    </div>
                </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title: "借阅图书分类"
                }}
                tableToExcel={{
                    table: 'borrow-excel',
                    filename: '借阅图书分类excel下载'
                }}
            >
                {this.state.tab === '1' ? (
                    <PieChart
                        data={this.state.pieData}
                        innerRadius={0.55}
                        height={this.props.height - 100}
                        padding={[0,20,60,20]}
                        colors = {["#3aa1ff","#5ddfcf","#4ecb73","#fbd437","#eaa674","#8a7bd4","#975fe5","#749fea","#36cbcb","#74b1ea","#5254cf","#435188"]}
                        onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                    />
                ) : (
                    <React.Fragment>
                        <Table
                            dataSource={this.state.tableData}
                            columns={columns}
                            bordered
                            size="middle"
                            pagination={this.state.pagination}
                            style={{height:300}}
                        />

                        <Table ref='table' columns={columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                    </React.Fragment>
                )}
            </MainCard>
        )
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if(JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)
        }
    }

}


