import React, {Component} from 'react';
import {Table, Divider} from 'antd';

import ShowChart from './ShowChart';
import {postAction} from "../../../../../axios";
import MainCard from "../../../../../common/components/MainCard/index";
import ReactDOM from "react-dom";


export default class EmploymentAnalysis extends Component {

    constructor(props){
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            barData : [
                // {
                //     label: "动力工程学院",
                //     "百分比": 28
                // }
            ],
            tableData: [
                // { key: '1', sortIndex: '1', college: '九龙湖校区', rate: '2901' }
            ],
            pagination: {
                pageSize: 5, // 每页条数
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            }
        }
        this.getData = this.getData.bind(this);
    }

    getData() {
        const {curYear,curCollege,curMajor} = this.props.filterValue;
        postAction(
            "/bigdata/employment/employmentPercentTrendAnalyze",
            {
                year: curYear,
                collegeCode: curCollege,
                majorCode: curMajor
            }
        ).then(
            (res) => {
                if (res.success && res.obj.length > 0) {
                    const pageInfo = res.obj;
                    let _newData = [];
                    let _tableData = [];

                    pageInfo.forEach(
                        (item, index) => {

                            _newData.push({
                                label: item.showName,
                                "百分比": Number(item.percent.substring(0, item.percent.length - 1))
                            })

                            _tableData.push({
                                key: (index + 1),
                                sortIndex: (index + 1),
                                college: item.showName,
                                rate: Number(item.percent.substring(0, item.percent.length - 1))
                            })
                        }
                    );
                    this.setState({
                        barData: _newData,
                        tableData: _tableData,
                        pagination: {
                            pageSize: 5, // 每页条数
                            showQuickJumper: true,
                            total: pageInfo.length,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        }
                    })
                } else {
                    this.setState({
                        barData:[],
                        tableData: [],
                        pagination: {
                            pageSize: 5, // 每页条数
                            showQuickJumper: true,
                            total: null,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        }
                    })
                }
            }
        )
    }

    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'employment-excel')
            }
            this.getData()
        })
    }

    render () {

        const columns = [
            {
                title:'序号',
                dataIndex: 'sortIndex',
                width: '10%',
            },{
                title: '学院/专业',
                dataIndex: 'college',
            },{
                title: '就业率（%）',
                dataIndex: 'rate',
            }
        ];

        return (
            <MainCard
                className="main-card"
                title={<div className="common-title">就业率分析
                    <div className="filter-text" style={{top:'22px',left:'0px'}}>{this.props.filterName.curYearName}
                        <Divider type="vertical" />{this.props.filterName.curCollegeName}
                        <Divider type="vertical" />{this.props.filterName.curMajorName}
                    </div>
                </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title: "就业率分析"
                }}
                tableToExcel={{
                    table: 'employment-excel',
                    filename: '就业率分析excel下载'
                }}
            >
                {this.state.tab === '1' ? (
                    <ShowChart
                        data={this.state.barData}
                        transpose
                        height={this.props.height - 100}
                        padding={[10,40,50,150]}
                        geomSize={10}
                        colors = {["#3aa1ff","#5ddfcf","#4ecb73","#fbd437","#eaa674","#8a7bd4","#975fe5","#749fea","#36cbcb","#74b1ea","#5254cf","#435188"]}
                        onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                        scales={{
                            value: {
                                formatter(text, item, index) {
                                    return `${text}%`;
                                },
                            }
                        }}
                    />
                ) : (

                    <React.Fragment>
                        <Table
                        dataSource={this.state.tableData}
                        columns={columns}
                        bordered
                        size="middle"
                        pagination={this.state.pagination}
                        style={{height:300}}
                        />

                        <Table ref='table' columns={columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                    </React.Fragment>
                )}
            </MainCard>
        )
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)
        }
    }

}


