import React, {Component} from 'react';
import {postAction} from "../../../../../../axios";
import {Card, Popover, Icon} from 'antd';
import ShowChart from './ShowChart';
import ModalTable from './modal';
import NoData from '@/common/components/NoData/NoDataComponent';


export default class ShangWang extends Component {

    constructor(props){
        super(props);
        this.state = {
            // 上网情况
            internetData: [
                // { name: '网名', value: 3 }
            ],
            visible: false,
        }
    }

    // 打开弹框
    handleClick = () => {
        this.setState({
            visible: true
        })
    }
    // 关闭弹窗
    handleClose = () => {
        this.setState({
            visible: false
        })
    }

    //上网情况 1130
    getNetworkLabel() {
        const { curYear, curGrade, curCollege, curMajor, curClass } = this.props.filterValue;
        postAction(
            "/bigdata/portrait/getNetworkLabel",
            {
                year: curYear,
                grade: curGrade,
                collegeCode: curCollege,
                majorCode: curMajor,
                classCode: curClass
            }
        ).then(
            (res) => {
                if (res.success && res.obj.length > 0) {
                    let newData = []
                    res.obj.forEach(({ count, labelName }) => {
                        newData.push(
                            {
                                name: labelName,
                                value: count,
                            }
                        )
                    })
                    this.setState({ internetData: newData });
                } else {
                    this.setState({
                        internetData:[]
                    })
                }

            }
        )
    }

    componentWillReceiveProps(nextProps) {
        this.setState({
            filterValue: nextProps.filterValue
        }, () => {
            this.getNetworkLabel()
        })
    }

    componentDidMount () {
        this.setState({
            filterValue: this.props.filterValue
        }, () => {
            this.getNetworkLabel()
        })
    }

    render () {

        const content = (
            <div>
                <p style={{margin:"0px"}}>0-0.25     网瘾</p>
                <p style={{margin:"0px"}}>0.26-0.5   网迷</p>
                <p style={{margin:"0px"}}>0.51-0.75   网虫</p>
                <p style={{margin:"0px"}}>0.76-1     网民</p>
            </div>
        );

        if (this.state.internetData && this.state.internetData[0]) {
            return (
                <React.Fragment>
                    {
                        this.state.visible &&
                        <ModalTable handleClose={this.handleClose} filterValue={this.props.filterValue}
                                    visible={this.state.visible} filterName={this.props.filterName}/>
                    }
                    <Card
                        title={
                            <div className="common-title">
                                <i className="iconfont icon-iconset0402"
                                   style={{marginRight: '5px'}}/>
                                上网情况
                                <span>单位：人</span>
                            </div>
                        }
                        extra={
                            <Popover content={content} title="上网情况标签定义">
                                <Icon type="exclamation-circle" style={{fontSize:"16px", color:"rgba(0, 0, 0, 0.85)"}} />
                            </Popover>
                        }
                        headStyle={{border: "none"}}
                        bodyStyle={{padding: 0}}
                        onClick={
                            this.handleClick
                        }
                        style={{cursor:'pointer'}}
                    >
                        <ShowChart data={this.state.internetData}/>
                    </Card>
                </React.Fragment>
            )
        } else {
            return (
                <Card
                    title={<div className="common-title"> <i className="iconfont icon-iconset0402" style={{ marginRight: '5px' }}/>上网情况 </div>}
                    headStyle={{ border: "none" }}
                    bodyStyle={{ padding: 0 }}
                >
                    <NoData height={260} data={'nodata'} />
                </Card>
            )
        }
    }
}


