import React, { Component } from 'react'
import { Modal, Divider, Table, Button,Select } from 'antd';
import { hostUrl } from '@/env'
import { postAction } from "@/axios";
const Option=Select.Option
export default class ModalDetail extends Component {
    state = {
        tableData: [],
        pagination: {
            current: 1, // 当前页数，
            pageSize: 10, // 每页条数
            total: 0, // 数据总条数
            showTotal: (total) => `共${total}条数据`, //显示数据总条数
            showQuickJumper: true // 显示页码快速跳转
        },
        typeList:[],
        areaList:[],
        type:'',
        area:''
    }
    getData() {
        const { curYear, curCollege, curMajor, curClass, curGrade } = this.props.filterValue;
        const {type,area}=this.state
        postAction(
            "/bigdata/portrait/getStudentIdentifyPager",
            {
                year: curYear,
                collegeCode: curCollege,
                majorCode: curMajor,
                classCode: curClass,
                grade: curGrade,
                labelType:type,
                labelNo:area,
                "pageInfo": {
                    "pageNum": this.state.pagination.current,
                    "pageSize": this.state.pagination.pageSize
                }
            }
        ).then(
            (res) => {
                if (res.success) {
                    let data=res.obj.list.map((item,index)=>{
                        item.key=index+1
                        item.sortIndex=index+1
                        return item
                    })
                    this.setState({
                        tableData: data,
                        pagination: {
                            current: res.obj.pageNum, // 当前页数，
                            pageSize: res.obj.pageSize, // 每页条数
                            total: res.obj.total, // 数据总条数
                            showTotal: (total) => `共${total}条数据`, //显示数据总条数
                            showQuickJumper: true // 显示页码快速跳转
                        }
                    })
                } else {
                    this.setState({
                        tableData: [],
                        pagination: {
                            current: 1, // 当前页数，
                            pageSize: 10, // 每页条数
                            total: 0, // 数据总条数
                            showTotal: (total) => `共${total}条数据`, //显示数据总条数
                            showQuickJumper: true // 显示页码快速跳转
                        }
                    })
                }
            }
        )
    }
    handleExport = () => {
        const { curYear, curCollege, curMajor, curClass, curGrade } = this.props.filterValue;
        const {type,area}=this.state
        window.location.href = `${hostUrl}/bigdata/portrait/exportStudentIdentifyInfos?year=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}&classCode=${curClass}&labelType=${type}&labelNo=${area}`
        
    }
    getTypeList=()=>{
        postAction(
            '/bigdata/portrait/getIdentifyType',
            {}
        ).then(res=>{
            if(res.success){
                this.setState({
                    typeList:res.obj
                })
            }
        })
    }
    getAreaList=()=>{
        postAction(
            '/bigdata/portrait/getIdentifyLabelCondition',
            {
                labelType:this.state.type
            }
        ).then(res=>{
            if(res.success){
                this.setState({
                    areaList:res.obj
                })
            }
        })
    }
    handleTableChange = (pagination) => {
        this.setState(
            () => {
                return {
                    pagination: {
                        current: pagination.current, // 当前页数，
                        pageSize: pagination.pageSize, // 每页条数
                        total: pagination.total, // 数据总条数
                        showTotal: (total) => `共${total}条数据`,
                        showQuickJumper: true // 显示页码快速跳转
                    }
                }
            },
            () => {
                this.getData();
            }
        )
    };
    handleTypeChange=(value)=>{
        this.setState({
            type:value
        },()=>{
            this.getAreaList()
        })
    }
    // 表格搜索
    handleSearch=()=>{
        this.setState({
            pagination: {
                current: 1, // 当前页数，
                pageSize: 10, // 每页条数
                total: 0, // 数据总条数
                showTotal: (total) => `共${total}条数据`, //显示数据总条数
                showQuickJumper: true // 显示页码快速跳转
            }
        },()=>{
            this.getData()
        })
        
    }
    handleAreaChange=(value)=>{
        this.setState({
            area:value
        })
    }
    componentDidMount = () => {
        this.getTypeList()
        this.getData()
    }
    
    render() {
        const columns = [
            {
                title: '序号',
                dataIndex: 'sortIndex',
                width: '6%'
            },
            {
                title: '学号',
                dataIndex: 'studentId',
                width: '12%',
            },
            {
                title: '一卡通号',
                dataIndex: 'studentNo',
                width: '13%',
            },
            {
                title: '姓名',
                dataIndex: 'studentName',
                width: '10%'
            },           
            {
                title: '学院',
                dataIndex: 'collegeName',
                width: '15%',
                render: (text) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '专业',
                dataIndex: 'majorName',
                width: '15%',
                render: (text) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '所属班级',
                dataIndex: 'className',
                width: '10%'
            },
            {
                title: '标签',
                dataIndex: 'labelName',
            },
        ]
        return (
            <Modal centered width={800} visible={this.props.visible} footer={null} onCancel={this.props.handleClose} title='查看详情'>
                <div className="filter-text" style={{ top: '22px', left: '0px', height: 32, lineHeight: '32px' }}>
                    {this.props.filterName.curYearName}
                    <Divider type="vertical" />{this.props.filterName.curCollegeName}
                    <Divider type="vertical" />{this.props.filterName.curMajorName}
                    <Divider type="vertical" />{this.props.filterName.curGradeName}
                    <Divider type="vertical" />{this.props.filterName.curClassName}
                    <Divider type="vertical" />
                    身份特点                    
                </div>
                <div style={{marginTop:10}}>
                    选择类别：
                    <Select onChange={this.handleTypeChange} defaultValue='' style={{width:150,marginRight:10}}>
                        <Option key='' value=''>全部</Option>
                        {
                            this.state.typeList.map(item=><Option key={item.labelNo} value={item.labelNo}>{item.labelName}</Option>)
                        }
                    </Select>
                    <Select  onChange={this.handleAreaChange}  defaultValue='' style={{width:150,marginRight:10}}>
                        <Option key='' value=''>全部</Option>
                        {
                            this.state.areaList.map(item=><Option key={item.labelNo} value={item.labelNo}>{item.labelName}</Option>)
                        }
                    </Select>
                    <Button style={{marginRight:10}} onClick={this.handleSearch} type='primary'>搜索</Button>
                    <Button onClick={this.handleExport} type='primary'>导出</Button>
                </div>
                <Table
                    size='small'
                    columns={columns}
                    dataSource={this.state.tableData}
                    style={{ marginTop: 15 }}
                    pagination={this.state.pagination}
                    onChange={this.handleTableChange}
                >

                </Table>
            </Modal>
        )
    }
}



