import React, {Component, Fragment} from 'react';
import {Card, Divider, Tooltip, Button, Popover, Icon} from 'antd';
import {Chart, Geom, Axis, Coord, Guide, Shape} from 'bizcharts';
import {postAction} from "../../../../../axios";
import NoData from "../../../../../common/components/NoData/NoDataComponent";
import DownloadChart from '../../../../../common/components/BizCharts/DownloadChart';


export default class Health extends Component {

    constructor(props) {
        super(props);
        this.state = {
            chartIns: undefined,
            data : [{
                internetHealth: 0
            }],
        }
        this.getData = this.getData.bind(this);
    }

    getData () {
        const {curYear,curCollege,curMajor,curGrade,curClass} = this.props.filterValue;
        postAction(
            "/bigdata/netTime/surfHealthByGroup",
            {
                year: curYear,
                collegeCode: curCollege,
                majorCode: curMajor,
                grade: curGrade,
                classCode: curClass
            }
        ).then(
            (res) => {
                if (res.success) {
                    this.setState({
                        data: [
                            {
                                internetHealth:( res.obj.internetHealth * 10 ) / 10
                            }
                        ]
                    })
                } else {
                    this.setState({
                        data:[]
                    })
                }
            }
        )
    }

    onFirstLoad(ev){
        if(this.props.onFirstLoad){
            this.props.onFirstLoad(ev);
        }
    }

    render () {

        const {chartIns} = this.state;
        
        const { Html, Arc } = Guide;
        Shape.registerShape('point', 'pointer', {
            drawShape(cfg, group) {
                let point = cfg.points[0]; // 获取第一个标记点

                point = this.parsePoint(point);
                const center = this.parsePoint({
                    // 获取极坐标系下画布中心点
                    x: 0,
                    y: 0
                }); // 绘制指针

                group.addShape('line', {
                    attrs: {
                        x1: center.x,
                        y1: center.y,
                        x2: point.x,
                        y2: point.y - 20,
                        stroke: cfg.color,
                        lineWidth: 5,
                        lineCap: 'round'
                    }
                });
                return group.addShape('circle', {
                    attrs: {
                        x: center.x,
                        y: center.y,
                        r: 12,
                        stroke: cfg.color,
                        lineWidth: 4.5,
                        fill: '#fff'
                    }
                });
            }

        });
        const cols = {  //调整仪表盘刻度
            internetHealth: {
                min: 0,
                max: 9,
                tickInterval: 1,
                nice: false
            }
        };

        const content = (
            <div>
                <p style={{margin:"0px"}}>根据年日均上网时长进行计算，值越大越健康</p>
            </div>
        );

        if (!this.state.data || this.state.data.length <= 0) {
            return(
                <Card
                    title={<div style={{fontSize:"bold"}}>上网健康度</div>}
                    headStyle={{border:"none"}}
                    bodyStyle={{paddingTop:"0px"}}
                    extra={<Fragment>
                                <div className="filter-text">{this.props.filterName.curYearName}
                                    <Divider type="vertical" />{this.props.filterName.curCollegeName}
                                    <Divider type="vertical" />{this.props.filterName.curMajorName}
                                    <Divider type="vertical" />{this.props.filterName.curGradeName}
                                    <Divider type="vertical" />{this.props.filterName.curClassName}
                                </div>
                                <DownloadChart chartIns= {chartIns} title = {"上网健康度"}>
                                    <Tooltip placement="top" title='导出'>
                                        <Button theme="outlined" style={{padding:"0px 8px"}}>
                                            <i className="iconfont icon-daochu1" />
                                        </Button>
                                    </Tooltip>
                                </DownloadChart>
                            </Fragment>
                    }
                >
                    <div style={{height:310}}>
                        <NoData data="nodata"/>
                    </div>
                </Card>
            )
        } else {
            return (
                <Card
                    title={<div className="common-title">上网健康度</div>}
                    headStyle={{border:"none"}}
                    bodyStyle={{paddingTop:"0px"}}
                    extra={<Fragment>
                        <div className="filter-text">{this.props.filterName.curYearName}
                            <Divider type="vertical" />{this.props.filterName.curCollegeName}
                            <Divider type="vertical" />{this.props.filterName.curMajorName}
                            <Divider type="vertical" />{this.props.filterName.curGradeName}
                            <Divider type="vertical" />{this.props.filterName.curClassName}
                        </div>
                        <Popover content={content} title="上网健康度">
                            <Icon type="exclamation-circle" style={{fontSize:"16px", color:"rgba(0, 0, 0, 0.85)", paddingRight:10}} />
                        </Popover>
                        <DownloadChart chartIns= {chartIns} title = {"上网健康度"}>
                            <Tooltip placement="top" title='导出'>
                                <Button theme="outlined" style={{padding:"0px 8px"}}>
                                    <i className="iconfont icon-daochu1" />
                                </Button>
                            </Tooltip>
                        </DownloadChart>
                    </Fragment>
                    }
                >
                    <Chart
                        height={this.props.height - 90}
                        data={this.state.data}
                        scale={cols}
                        padding={[0, 0, 0, 0]}
                        onGetG2Instance={(chartIns) => { this.setState({ chartIns }) }}
                        forceFit>
                        <Coord type="polar" startAngle={-9 / 8 * Math.PI} endAngle={1 / 8 * Math.PI} radius={0.75} />
                        <Axis name="internetHealth" zIndex={2} line={null} label={{
                            offset: -16,
                            textStyle: {
                                fontSize: 18,
                                textAlign: 'center',
                                textBaseline: 'middle'
                            }
                        }} subTickCount={4} subTickLine={{
                            length: -8,
                            stroke: '#fff',
                            strokeOpacity: 1
                        }} tickLine={{
                            length: -18,
                            stroke: '#fff',
                            strokeOpacity: 1
                        }} />
                        <Axis name="1" visible={false} />
                        <Guide>
                            <Arc zIndex={0} start={[0, 0.965]} end={[9, 0.965]} style={{
                                // 渐变底色
                                stroke: 'l(0) 0:#FF5430 0.3:#887EDF 0.6:#53A0FD 1:#B4EC51',
                                lineWidth: 22
                            }} />
                            <Arc zIndex={1} start={[0, 0.965]} end={[this.state.data[0].internetHealth, 0.965]} style={{
                                // 指针指过之后的颜色
                                stroke: 'transparent',
                                lineWidth: 22
                            }} />
                            <Html position={['50%', '95%']} html={() => `<div style="width: 300px;text-align: center;font-size: 12px!important;"><p style="font-size: 3em;color: rgba(0,0,0,0.85);margin: 0px;">${(this.state.data[0].internetHealth * 10).toFixed(2)}%</p></div>`} />
                        </Guide>
                        <Geom type="point" position="internetHealth*1" shape="pointer" color="#1890FF" active={false} style={{
                            stroke: '#fff',
                            lineWidth: 11
                        }} />
                    </Chart>
                </Card>
            )
        }
    }



    componentDidMount() {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if(JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50);
        }
    }

}


