import React, { Component } from 'react';
import { Card } from 'antd';
import { postAction } from "../../../../../../../axios";
import NoData from '@/common/components/NoData/NoDataComponent';
import ModalTable from './modal';
import ShowChart from './ShowChart';


export default class JiangXueJin extends Component {
    constructor(props) {
        super(props);
        this.state = {
            filterValue: {
                curYear: '',
                curGrade: '',
                curCollege: '',
                curMajor: '',
                curClass: ''
            },
            visible: false,
            // 奖学金
            data: [
                // { name: '社会', value: 22 }
            ]
        }
    }

    // 打开弹框
    handleClick = () => {
        this.setState({
            visible: true
        })
    }
    // 关闭弹窗
    handleClose = () => {
        this.setState({
            visible: false
        })
    }

    //奖学金
    getHonorLabel() {
        const { curYear, curGrade, curCollege, curMajor, curClass } = this.state.filterValue;
        postAction(
            "/bigdata/portrait/getScholarshipCount",
            {
                year: curYear,
                grade: curGrade,
                collegeCode: curCollege,
                majorCode: curMajor,
                classCode: curClass
            }
        ).then(
            (res) => {
                if (res.success) {
                    if(res.obj && res.obj.length > 0){
                        let newData = [];
                        res.obj.forEach(({ count, name }) => {
                            newData.push(
                                {
                                    name: name,
                                    value: count,
                                }
                            )
                        })
                        this.setState({ data: newData });
                    }else{
                        this.setState({ data: [] });
                    }
                }
            }
        )
    }

    componentWillReceiveProps(nextProps, nextState) {
        this.setState({
            filterValue: nextProps.filterValue
        }, () => {
            this.getHonorLabel()
        })
    }

    componentDidMount = () => {
        this.setState({
            filterValue: this.props.filterValue
        }, () => {
            this.getHonorLabel()
        })
    }

    render() {

        if (this.state.data && this.state.data[0]) {
            return (
                <React.Fragment>
                    {
                        this.state.visible &&
                        <ModalTable handleClose={this.handleClose} filterValue={this.props.filterValue}
                                    visible={this.state.visible} filterName={this.props.filterName}/>
                    }

                    {/* 奖学金 */}
                    <Card
                        title={
                            <div className="common-title">
                                <i className="iconfont icon-rongyu" style={{marginRight: '5px'}}/>
                                奖学金
                                <span>单位：人</span>
                            </div>
                        }
                        headStyle={{border: "none"}}
                        bodyStyle={{padding: 0}}
                        style={{cursor: 'pointer'}}
                        onClick={
                            this.handleClick
                        }
                    >
                        <ShowChart
                            data={this.state.data}
                            padding={[10,30,40,30]}
                        />
                    </Card>
                </React.Fragment>
            )
        } else {
            return (
                <React.Fragment>
                    {/* 奖学金 */}
                    <Card
                        title={<div className="common-title"><i className="iconfont icon-rongyu" style={{ marginRight: '5px' }}/>奖学金</div>}
                        headStyle={{ border: "none" }}
                        bodyStyle={{ padding: 0 }}
                    >
                        <NoData height={260} data={'nodata'} />
                    </Card>
                </React.Fragment>
            )
        }
    }
}




