import React, { Component } from 'react';
import { Chart, Geom, Axis, Tooltip, Coord, Legend} from "bizcharts"
import { DataSet } from '@antv/data-set';
import NoData from '@/common/components/NoData/NoDataComponent';
// 柱状图
class ShenFen extends Component {  
    
    changeLegend = (e) => {
        window.event.stopPropagation();
    }
    render() {
        const height = this.props.height || 350; // 高度设置，默认值
        const data = this.props.data || []; // 数据
        let fields = [], nameKey; // 获取关键字
        if (data && data[0]) {
            nameKey = Object.keys(data[0])[0];// 获取json第一个对象的第一个键名称
            // 获取json对象子节点除去第一个键的其他键字段集合
            Object.keys(data[0]).forEach((item, index) => {
                if (index !== 0) {
                    fields.push(item);
                }
            })
            // 分组图
            const ds = new DataSet(); // dateset数据集方法
            const dv = ds.createView().source(data);// 数据转换
            // 初始化参数
            dv.source(data).transform({
                type: 'fold',
                fields: fields.length > 0 ? fields : ['null'],
                key: 'type',
                value: 'value'
            });
            return (
                <React.Fragment>
                    <Chart height={height} data={dv} padding={this.props.padding} forceFit>
                        <Coord transpose />
                        <Axis name={nameKey} />
                        <Axis name="value" />
                        <Legend marker={'circle'} onClick={this.changeLegend}/>
                        <Tooltip crosshairs={{ type: "y" }} />
                        <Geom
                            type={'intervalStack'}
                            position={nameKey + "*value"}
                            size={40}
                            color={'type'}
                        />
                    </Chart>
                </React.Fragment>

            )
        } else {
            // 无数据时
            return (<NoData height={height} data={'nodata'} />);
        }
    }
}

export default ShenFen


