import React from 'react'
import { Table, Card, Popover, Icon } from 'antd';
import { postAction } from "@/axios";
import YuJingModal from './modal';


const columns = [{
    title: '预警类型',
    dataIndex: 'type',
    key: 'type',
}, {
    title: '红色预警',
    dataIndex: 'red',
    key: 'red',
}, {
    title: '橙色预警',
    dataIndex: 'orange',
    key: 'orange',
}, {
    title: '黄色预警',
    dataIndex: 'yellow',
    key: 'yellow',
}, {
    title: '蓝色预警',
    dataIndex: 'blue',
    key: 'blue',
}, {
    title: '合计',
    dataIndex: 'total',
    key: 'total',
}];

class YuJing extends React.Component {
    state = {
        // 预警情况
        warningData: [
            // { type: '失联', red: 0, orange: 0, yellow: 0, blue: 0 }
        ],
        yujingVisible: false
    }
    //预警情况 1130
    getAlarmAmount() {
        const { curGrade, curCollege, curMajor, curClass } = this.props.filterValue
        postAction(
            "/bigdata/alarmTrigger/getAlarmAmount",
            {
                grade: curGrade,
                collegeCode: curCollege,
                majorCode: curMajor,
                classCode: curClass
            }
        ).then(
            (res) => {
                if (res.success && res.obj.length > 0) {

                    let newData = []
                    for (let i = 0; i < res.obj.length; i++) {
                        let obj = { type: '', red: '', orange: '', yellow: '', blue: '' }
                        if (res.obj[i].alarmType === 1) {
                            obj.type = '失联'
                        } else if (res.obj[i].alarmType === 2) {
                            obj.type = '网络'
                        } else if (res.obj[i].alarmType === 3) {
                            obj.type = '挂科'
                        } else if (res.obj[i].alarmType === 4) {
                            obj.type = '消费'
                        } else if (res.obj[i].alarmType === 5) {
                            obj.type = '夜归'
                        } else if (res.obj[i].alarmType === 6) {
                            obj.type = '逃课'
                        } else if (res.obj[i].alarmType === 7) {
                            obj.type = '合计'
                        }
                        obj.red = res.obj[i].red
                        obj.orange = res.obj[i].orange
                        obj.yellow = res.obj[i].yellow
                        obj.blue = res.obj[i].blue
                        obj.total = res.obj[i].red + res.obj[i].orange + res.obj[i].yellow + res.obj[i].blue
                        newData.push(obj)
                    }
                    this.setState({ warningData: newData })
                }
            }
        )
    }
    componentWillReceiveProps(nextProps, nextState) {
        this.setState({
            filterValue: nextProps.filterValue
        }, () => {
            this.getAlarmAmount()
        })
    }
    componentDidMount = () => {
        this.setState({
            filterValue: this.props.filterValue
        }, () => {
            this.getAlarmAmount()
        })
    }

    // 打开弹框
    handleClick = () => {
        this.setState({
            yujingVisible: true
        })
    }
    // 关闭弹窗 
    handleClose = () => {
        this.setState({
            yujingVisible: false
        })
    }
    render() {
        // 预警table添加key值
        let warningData = this.state.warningData.map((item, i) => {
            item.key = i;
            return item;
        });

        const content = (
            <div>
                <p style={{margin:"0px"}}>1.此模块数据为截止目前最新数据。
                    <br/>2.此模块不能查询历史数据。</p>
            </div>
        );

        return (
            <React.Fragment>
                {
                    this.state.yujingVisible && <YuJingModal   handleClose={this.handleClose} filterValue={this.props.filterValue}  filterName={this.props.filterName} visible={this.state.yujingVisible} />
                }
                <Card
                    title={<div className="common-title">
                        <i className="iconfont icon-yujing" style={{ marginRight: '5px' }} />预警情况
                        <span>单位：条</span>
                            </div>}
                    extra={
                        <Popover content={content} title="预警情况说明">
                            <Icon type="exclamation-circle" style={{fontSize:"16px", color:"rgba(0, 0, 0, 0.85)"}} />
                        </Popover>
                    }
                    headStyle={{ border: "none" }}
                    bodyStyle={{ padding: "0 15px 20px" }}
                    onClick={this.handleClick}
                    style={{ cursor: 'pointer' }}
                >
                    <Table dataSource={warningData} columns={columns} size="small" pagination={false} />
                </Card>
            </React.Fragment>

        )
    }
}

export default YuJing


