import React, {Component, Fragment} from 'react';
import {Row, Col, Card} from 'antd';

import SpecialTop from "./SpecialTop";
import SpecialStudent from "./SpecialStudent";
import SpecialDistribution from "./SpecialDistribution";
import {postAction} from "../../../../../axios";


export default class SpecialCard extends Component {

    constructor(props){
        super(props);
        this.state = {
            total:0,
            boyNum:0,
            girlNum:0,
        }
        this.getData = this.getData.bind(this);
    }

    getData () {
        const {curYear,curCollege,curMajor,curGrade,curClass} = this.props.filterValue;
        postAction(
            "/bigdata/student/getMinoritySpeStudent",
            {
                year: curYear,
                collegeCode: curCollege,
                majorCode: curMajor,
                grade: curGrade,
                classCode: curClass
            }
        ).then(
            (res) => {
                if (res.success){
                    this.setState({
                        total: res.obj.count.total,
                        boyNum: res.obj.count.boy,
                        girlNum: res.obj.count.girl
                    })
                } else {
                    this.setState({
                        total:0,
                        boyNum:0,
                        girlNum:0,
                    })
                }
            }
        )
    }

    render () {
        return (
            <Fragment>
                <Card
                    title={<div className="common-title">内高、预科来源少数民族学生分析</div>}
                    extra={<span className="title-font">全部
                        <span className="title-number">{this.state.total}</span>
                        男生
                        <span className="title-number">{this.state.boyNum}</span>
                        女生
                        <span className="title-number">{this.state.girlNum}</span>
                    </span>}
                    bodyStyle={{display:"none"}}
                />

                <Row>
                    <Col span={10} className="only-top card-right">
                        <SpecialTop height={430} filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>

                    <Col span={14} className="only-top card-left">
                        <SpecialStudent height={430} filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>

                    <Col span={24} className="only-top">
                        <SpecialDistribution height={400} filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>
                </Row>
            </Fragment>
        )
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)
        }
    }
}


