import React, {Component} from 'react';
import {Table} from 'antd';

import ShowChart from "./ShowChart";
import MainCard from "../../../../../common/components/MainCard/index";
import ReactDOM from "react-dom";
import {postAction} from "../../../../../axios";


export default class ShiChangDuiBi extends Component {

    constructor(props){
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            data : [
                // { label: '计算机院', '上网时长': 38 }
            ],
            tableData: [
                // {
                //     key: '1',
                //     sortIndex: '1',
                //     college: '计算机院',
                //     time: '38'
                // }
            ],
            pagination: {
                pageSize: 5, // 每页条数
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            }
        }
        this.getData = this.getData.bind(this);
    }

    getData () {
        const {curYear} = this.props.filterValue;
        postAction(
            "/bigdata/netTime/surfDurationByCollege",
            {
                year:curYear
            }
        ).then(
            (res) => {
                if (res.success) {
                    const pageInfo = res.obj;
                    let _newData = [];
                    let _tableData = [];

                    pageInfo.forEach(
                        (item, index) => {

                            _newData.push({
                                name: item.collegeName,
                                '上网时长': item.surfDuration
                            })

                            _tableData.push({
                                key: (index + 1),
                                sortIndex: (index + 1),
                                college: item.collegeName,
                                time: item.surfDuration,
                            })
                        }
                    );
                    this.setState({
                        data: _newData,
                        tableData: _tableData,
                        pagination: {
                            pageSize: 5, // 每页条数
                            showQuickJumper: true,
                            total: pageInfo.length,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        }
                    })
                }  else {
                    this.setState({
                        data:[],
                        tableData: [],
                        pagination: {
                            pageSize: 5, // 每页条数
                            showQuickJumper: true,
                            total: null,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        }
                    })
                }
            }
        )
    }

    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'duibi-excel')
            }
            // this.getData()
        })
    }

    render () {

        const columns = [
            {
                title:'序号',
                dataIndex: 'sortIndex',
                width: '10%',
            },{
                title: '学院',
                dataIndex: 'college',
            },{
                title: '上网时长',
                dataIndex: 'time',
            }
        ];

        return (
            <MainCard
                className="main-card"
                title={<div className="common-title">各学院上网时长对比
                    <span>单位：小时</span>
                    <div className="filter-text" style={{top:'22px',left:'0px'}}>{this.props.filterName.curYearName}
                        {/*<Divider type="vertical" />{this.props.filterName.curCollegeName}*/}
                    </div>
                </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title: "各学院上网时长对比"
                }}
                tableToExcel={{
                    table: 'duibi-excel',
                    filename: '各学院上网时长对比excel下载'
                }}
            >

                {this.state.tab === '1' ? (
                    <ShowChart
                        data={this.state.data}
                        onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                    />
                ) : (
                    <React.Fragment>
                        <Table
                            dataSource={this.state.tableData}
                            columns={columns}
                            bordered
                            size="middle"
                            pagination={this.state.pagination}
                            style={{height:300}}
                        />

                        <Table ref='table' columns={columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                    </React.Fragment>
                )}
            </MainCard>
        )
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if(JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50);
        }
    }
}


