import React, {Component, Fragment} from 'react';
import {Card, Table, Divider, Tooltip, Button} from 'antd';
import {postAction} from "../../../../axios";
import NoDataComponent from "../../../../common/components/NoData/NoDataComponent";
import ReactHTMLTableToExcel from '../../../../common/components/TableToExcel';
import ReactDOM from "react-dom";
import {hostUrl} from "../../../../env";


export default class HotBook extends Component {

    constructor(props){
        super(props);
        this.state = {
            data: [
                // {
                //     key: '1',
                //     sortIndex: 1,
                //     bookName: '计算机理论',
                //     bookIndex: "B27689",
                //     writer: "张三",
                //     public: '华中科技大学出版社',
                //     borrowTimes: "1111"
                // }
            ],
            tableToExcel:{
                table: 'hot-xls',
                filename: '热门图书排行（TOP10）excel下载'
            }
        }
        this.getData = this.getData.bind(this);
    }

    getData () {
        const {curYear,curCollege,curMajor,curClass,curGrade} = this.props.filterValue;
        postAction(
            "/bigdata/book/countPopularBook",
            {
                year:curYear,
                collegeCode:curCollege,
                majorCode:curMajor,
                classCode:curClass,
                grade: curGrade
            }
        ).then(
            (res) => {
                if (res.success) {
                    let newData = [];
                    res.obj.forEach(
                        (item, index) => {
                            newData.push({
                                key: (index + 1),
                                sortIndex: (index + 1),
                                bookName: item.bookName,
                                bookIndex: item.bookCode,
                                public: item.bookPress,
                                borrowTimes: item.bookTimes
                            })
                        }
                    );
                    this.setState({
                        data: newData
                    },()=>{
                        if (this.refs['table']) {
                            const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                            const table = tableCon.querySelector('table')
                            table.setAttribute('id', 'hot-xls')
                        }
                    })
                } else {
                    this.setState({
                        data:[]
                    })
                }
            }
        )
    }

    handleExport=(record)=>{
        const {curYear} = this.props.filterValue;
        let bookIndex = record.bookIndex;
        window.location.href=`${hostUrl}/bigdata/book/exportBookBorrowDist?year=${curYear}&bookCode=${bookIndex}`
    };

    render () {

        // 表头设置
        const columns = [
            {
                title: '排名',
                dataIndex: 'sortIndex',
                width: '8%',
            },
            {
                title: '图书名称',
                dataIndex: 'bookName',
                render: (text) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '索书号',
                dataIndex: 'bookIndex',
                width: "20%",
                render: (text) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '出版社',
                dataIndex: 'public',
                width: "20%",
                render: (text) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '借阅次数',
                dataIndex: 'borrowTimes',
                width: "10%",
                render: (text,record) => {
                    return (
                        <Fragment>
                            <span title={text} style={{textAlign:"center"}}>
                            {text}
                            </span>
                            <Tooltip placement="top" title='导出明细'>
                                <Button onClick={() => this.handleExport(record)} style={{padding:"0px 8px",float: "right"}} theme="outlined">
                                    <i className="iconfont icon-daochu" />
                                </Button>
                            </Tooltip>
                        </Fragment>
                    )
                }
            }
        ];



        if (!this.state.data || this.state.data.length <= 0) {
            return (
                <Card
                    title={<div className="common-title">热门图书排行（TOP10）</div>}
                    headStyle={{border:"none"}}
                    bodyStyle={{paddingTop:"0px"}}
                    extra={<Fragment>
                                <div className="filter-text">{this.props.filterName.curYearName}
                                <Divider type="vertical" />{this.props.filterName.curCollegeName}
                                <Divider type="vertical" />{this.props.filterName.curMajorName}
                                <Divider type="vertical" />{this.props.filterName.curGradeName}
                                <Divider type="vertical" />{this.props.filterName.curClassName}
                            </div>
                            <ReactHTMLTableToExcel
                                {...(this.state.tableToExcel || {})}
                                content={
                                    <Tooltip placement="top" title='导出'>
                                        <Button theme="outlined" style={{padding:"0px 8px"}}>
                                            <i className="iconfont icon-daochu1" />
                                        </Button>
                                    </Tooltip>
                                }
                            />
                    </Fragment>
                    }
                >
                    <div style={{height:310}}>
                        <NoDataComponent data="nodata"/>
                    </div>
                </Card>
            )
        } else {
            return (
                <Card
                    title={<div className="common-title">热门图书排行（TOP10）</div>}
                    headStyle={{border:"none"}}
                    bodyStyle={{paddingTop:"0px"}}
                    extra={<Fragment>
                        <div className="filter-text">{this.props.filterName.curYearName}
                            <Divider type="vertical" />{this.props.filterName.curCollegeName}
                            <Divider type="vertical" />{this.props.filterName.curMajorName}
                            <Divider type="vertical" />{this.props.filterName.curGradeName}
                            <Divider type="vertical" />{this.props.filterName.curClassName}
                        </div>
                        <ReactHTMLTableToExcel
                            {...(this.state.tableToExcel || {})}
                            content={
                                <Tooltip placement="top" title='导出'>
                                    <Button theme="outlined" style={{padding:"0px 8px"}}>
                                        <i className="iconfont icon-daochu1" />
                                    </Button>
                                </Tooltip>
                            }
                        />
                    </Fragment>
                    }
                >
                    <Table
                        ref='table'
                        style={{marginTop:15}}
                        columns={columns}
                        dataSource={this.state.data}
                        pagination={false}
                        bordered={true}
                        size="middle"
                    />
                </Card>
            )
        }
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)
        }
    }

}


