import React, {Component, Fragment} from 'react';
import {Row, Col} from 'antd';

import ConsumerTrends from "./XiaoFeiQuShi/index";
import Address from "./XiaoFeiDiDian/index";
import Shops from './ShangPuPaiMing/index';


export default class Consumption extends Component {
    render () {
        return (
            <Fragment>
                <Row>
                    <Col span={24} className="card-padding">
                        <ConsumerTrends height={400} filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>

                    <Col span={12} className="card-padding card-right card-bottom">
                        <Address height={420} filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>

                    <Col span={12} className="card-padding card-left card-bottom">
                        <Shops height={420} filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>
                </Row>
            </Fragment>
        )
    }
}


