import React, {Component} from 'react';
import {Table} from 'antd';
import ReactDOM from 'react-dom';
import { hostUrl } from '../../../../../env'


import MainCard from "../../../../../common/components/MainCard";
import ShowChart from './ShowChart';
import {postAction} from "../../../../../axios";

export default class ZhengTi extends Component {

    constructor(props) {
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            chartData : [
                // { name: '计算机院', "人数": 117 }
            ],
            tableData : [
                // { key: '1', sortIndex:'1', college: '建筑学院', number: 165, rate: '5%' }
            ],
            total:0,
            pagination: {
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            },
            columns: [{
                title:'序号',
                dataIndex: 'sortIndex',
                width: '10%',
            },{
                title: '学院',
                dataIndex: 'college',
                render: (text) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            }, {
                title: '人数',
                dataIndex: 'number'
            }, {
                title: '占学生总数比例',
                dataIndex: 'rate',
            }]

        }
        this.getData = this.getData.bind(this);
    }

    getData () {
        const {curYear,curCollege,curMajor,curGrade} = this.props.filterValue;

        let data = {};
        if ( curGrade ) {
            data = {
                year: curYear,
                collegeCode: curCollege,
                majorCode: curMajor,
                grade: curGrade,
            }
        } else {
            data = {
                year: curYear,
                collegeCode: curCollege,
            }
        }

        postAction(
            "/bigdata/student/getStudentDist",data
        ).then(
            (res) => {
                if (res.success && res.obj.detail.length > 0){
                    const pageInfo = res.obj;

                    let _chartData = [];
                    let _tableData = [];


                    pageInfo.detail.forEach(

                        (item, index) => {

                            _chartData.push({
                                name: item.name,
                                "人数": item.count,
                            })

                            _tableData.push({
                                key: (index + 1),
                                sortIndex: (index + 1),
                                college: item.name,
                                number: item.count,
                                rate: String(item.count / pageInfo.total.count * 100).substring(0, 4) + "%",
                            })
                        }
                    )
                    this.setState({
                        tableData: _tableData,
                        chartData: _chartData,
                        total: pageInfo.total.count,
                        pagination:{
														...this.state.pagination,
                            total:pageInfo.detail.length,
                        }
                    })
                } else {
                    this.setState({
                        tableData: [],
                        chartData:[],
                        total:0,
                        pagination:{
														...this.state.pagination,
                            total:null,
                        }
                    })
                }
            }
        )
    }

    //导出明细
    getDetail () {
        const {curYear,curCollege,curMajor,curGrade} = this.props.filterValue;

        if ( curGrade ) {
            window.location.href=`${hostUrl}/bigdata/student/exportStudentDist?year=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}`;
        } else {
            window.location.href=`${hostUrl}/bigdata/student/exportStudentDist?year=${curYear}&collegeCode=${curCollege}`;
        }
    }

    //tab切换
    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'student-xls')
            }
        })
    }

    render() {

        const {curYearName,curCollegeName,curMajorName,curGradeName} = this.props.filterName;

        let filterName = ``;

        let dataName = `${curYearName} | ${curCollegeName} | ${curMajorName} | ${curGradeName}`;

        let dataName2 = `${curYearName} | ${curCollegeName}`;

        if ( curGradeName === "全部年级" ) {
            filterName = dataName2
        } else {
            filterName = dataName
        }

        return (
            <MainCard
                className="main-card"
                title={<div className="common-title">学生整体分析（学院/专业）
                    <span>单位：人</span>
                    <span style={{float:"right",color:"#4a4a4a",fontSize:"14px",marginRight:"10px"}}>总人数：
                        <span style={{fontSize:"16px",color:"#1890ff"}}>{this.state.total}</span>
                    </span>
                    <div className="filter-text" style={{top:'22px',left:'0px'}}>
                        {filterName}
                    </div>
                </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title: "学生整体分析（学院/专业）"
                }}
                detailCallback={this.getDetail.bind(this)}
                tableToExcel={{
                    table: 'student-xls',
                    filename: '学生整体分析（学院/专业）excel下载'
                }}
            >
                {
                    this.state.tab === '1'
                    ?
                    <ShowChart
                        data={this.state.chartData}
                        onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                    />
                    :
                    <React.Fragment>
                            <Table
                                    dataSource={this.state.tableData}
                                    columns={this.state.columns}
                                    bordered
                                    size="middle"
                                    pagination={this.state.pagination}
                            />

                            <Table ref='table' columns={this.state.columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                    </React.Fragment>
                }
            </MainCard>
        )
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)

            if (nextProps.filterName.curCollegeName === "全部学院"){
                this.setState({
                    columns : [{
                        title:'序号',
                        dataIndex: 'sortIndex',
                        width: '10%',
                    },{
                        title: '学院',
                        dataIndex: 'college',
                        render: (text) => {
                            return (
                                <span title={text}>{text}</span>
                            )
                        }
                    }, {
                        title: '人数',
                        dataIndex: 'number'
                    }, {
                        title: '占学生总数比例',
                        dataIndex: 'rate',
                    }]
                })
            } else {
                this.setState({
                    columns : [{
                        title:'序号',
                        dataIndex: 'sortIndex',
                        width: '10%',
                    },{
                        title: '专业',
                        dataIndex: 'college',
                        render: (text) => {
                            return (
                                <span title={text}>{text}</span>
                            )
                        }
                    }, {
                        title: '人数',
                        dataIndex: 'number'
                    }, {
                        title: '占学生总数比例',
                        dataIndex: 'rate',
                    }]
                })
    
                if (nextProps.filterName.curGradeName !== "全部年级") {
                    this.setState({
                        columns : [{
                            title:'序号',
                            dataIndex: 'sortIndex',
                            width: '10%',
                        },{
                            title: '班级',
                            dataIndex: 'college',
                            render: (text) => {
                                return (
                                    <span title={text}>{text}</span>
                                )
                            }
                        }, {
                            title: '人数',
                            dataIndex: 'number'
                        }, {
                            title: '占学生总数比例',
                            dataIndex: 'rate',
                        }]
                    })
                }
            }
        }
    }

}


