import React, {Component} from 'react';
import {postAction} from "../../../../../../../axios";
import ShowChart from './ShowChart';
import ModalTable from './modal';
import NoData from '@/common/components/NoData/NoDataComponent';


export default class XueJiQingKuang extends Component {

    constructor(props) {
        super(props);
        this.state = {
            // 学籍异动
            studyStatus: [
                // { name: '试读', value: 22 }
            ],
            visible: false,
            total: 0
        }
    }

    // 打开弹框
    handleClick = () => {
        this.setState({
            visible: true
        })
    }
    // 关闭弹窗
    handleClose = () => {
        this.setState({
            visible: false
        })
    }

    //学籍异动 1130
    getSchStatusLabel() {
        const { curYear, curGrade, curCollege, curMajor, curClass } = this.props.filterValue;
        postAction(
            "/bigdata/portrait/getStuStatusChangeCount",
            {
                year: curYear,
                grade: curGrade,
                collegeCode: curCollege,
                majorCode: curMajor,
                classCode: curClass
            }
        ).then(
            (res) => {
                if (res.success) {
                    const xj = res.obj
                    if (xj && xj.length > 0) {
                        let paiData = [];
                        let countTotal = [];
                        xj.forEach(({ count, labelName }) => (
                            paiData.push({
                                name: labelName,
                                value: count
                            })
                        ));

                        xj.map(res => countTotal += res.count)
                        this.setState({
                            studyStatus: paiData,
                            total: countTotal
                        })
                    } else {
                        this.setState({
                            studyStatus: [],
                            total: 0
                        })
                    }

                }
            }
        )
    }

    componentWillReceiveProps(nextProps, nextState) {
        this.setState({
            filterValue: nextProps.filterValue
        }, () => {
            this.getSchStatusLabel()
        })
    }

    componentDidMount = () => {
        this.setState({
            filterValue: this.props.filterValue
        }, () => {
            this.getSchStatusLabel()
        })
    }

    render () {
        if (this.state.studyStatus && this.state.studyStatus[0] && this.state.total > 0) {
            return (
                <React.Fragment>
                    {
                        this.state.visible &&
                        <ModalTable handleClose={this.handleClose} filterValue={this.props.filterValue}
                                    visible={this.state.visible} filterName={this.props.filterName}/>
                    }
                    <div
                        onClick={
                            this.handleClick
                        }
                        style={{cursor: 'pointer'}}
                    >
                        <ShowChart data={this.state.studyStatus}/>
                    </div>
                </React.Fragment>
            )
        } else {
            return (
                <div>
                    <NoData height={260} data={'nodata'} />
                </div>
            )
        }
    }
}


