import React, {Component, Fragment} from 'react';
import {Row, Col} from 'antd';

import HotBook from "./HotBook";
import Reading from "./Reading";
import Classification from "./Classification";
import DifferentTime from "./DifferentTime";


export default class Library extends Component {
    render () {
        return (
            <Fragment>
                <Row>
                    <Col span={24} className="card-padding">
                        <HotBook filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>

                    <Col span={24} className="card-padding">
                        <Reading filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>

                    <Col span={12} className="card-padding card-right card-bottom">
                        <Classification height={400} filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>

                    <Col span={12} className="card-padding card-left card-bottom">
                        <DifferentTime height={400} filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>
                </Row>
            </Fragment>
        )
    }
}


