import React, {Component, Fragment} from 'react';
import {Row, Col} from 'antd';

import GraduationTo from "./BiYeQuXiang/index";
import TopTenCity from './QianShiChengShi/index';
import EmploymentAnalysis from './JiuYeLv/index';


export default class Graduation extends Component {
    render () {
        return (
            <Fragment>
                <Row>
                    <Col span={14} className="card-padding card-right">
                        <GraduationTo height={400} filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>

                    <Col span={10} className="card-padding card-left">
                        <TopTenCity heighy={400} filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>

                    <Col span={24} className="card-padding card-bottom">
                        <EmploymentAnalysis height={400} filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>
                </Row>
            </Fragment>
        )
    }
}


