import React, {Component, Fragment} from 'react';
import Top from './Top';
import Center from './Center';
import Bottom from './Bottom';
import Footer from './Footer';

export default class Huaxiang extends Component {
    render () {
        return (
            <Fragment>
                {/* 上部 */}
                <Top filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                {/* 中部 */}
                <Center  filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                {/* 下部 */}
                <Bottom  filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                {/* 底部 */}
                <Footer  filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
            </Fragment>
        )
    }
}


