import React, { Component } from 'react';
import { Card } from 'antd';
import ShowChart from "./ShowChart";
import { postAction } from "../../../../../../../axios";
import NoData from '@/common/components/NoData/NoDataComponent';
import ModalTable from './modal'


export default class ChuFen extends Component {
    constructor(props) {
        super(props);
        this.state = {
            filterValue: {
                curYear: '',
                curGrade: '',
                curCollege: '',
                curMajor: '',
                curClass: ''
            },
            visible: false,
            // 处分
            honorData: [
                // { name: '警告', value: 22 }
            ]
        }
    }

    // 打开弹框
    handleClick = () => {
        this.setState({
            visible: true
        })
    }
    // 关闭弹窗
    handleClose = () => {
        this.setState({
            visible: false
        })
    }

    //处分
    getHonorLabel() {
        const { curYear, curGrade, curCollege, curMajor, curClass } = this.state.filterValue;
        postAction(
            "/bigdata/portrait/getPunishmentCount",
            {
                year: curYear,
                grade: curGrade,
                collegeCode: curCollege,
                majorCode: curMajor,
                classCode: curClass
            }
        ).then(
            (res) => {
                if (res.success) {
                    if(res.obj && res.obj.length > 0){
                        let newData = []
                        res.obj.forEach(({ count, name }) => {
                            newData.push(
                                {
                                    name: name,
                                    value: count,
                                }
                            )
                        })
                        this.setState({ honorData: newData });
                    }else{
                        this.setState({ honorData: [] });
                    }
                }
            }
        )
    }

    componentWillReceiveProps(nextProps, nextState) {
        this.setState({
            filterValue: nextProps.filterValue
        }, () => {
            this.getHonorLabel()
        })
    }

    componentDidMount = () => {
        this.setState({
            filterValue: this.props.filterValue
        }, () => {
            this.getHonorLabel()
        })
    }

    render() {

        if (this.state.honorData && this.state.honorData[0]) {
            return (
                <React.Fragment>
                    {
                        this.state.visible &&
                        <ModalTable handleClose={this.handleClose} filterValue={this.props.filterValue}
                                    visible={this.state.visible} filterName={this.props.filterName}/>
                    }

                        {/* 处分 */}
                        <Card
                            title={
                                <div className="common-title">
                                    <i className="iconfont icon-rongyu" style={{ marginRight: '5px' }}/>处分
                                    <span>单位：人</span>
                                </div>
                            }
                            headStyle={{ border: "none" }}
                            bodyStyle={{ padding: 0 }}
                            onClick={
                                this.handleClick
                            }
                            style={{cursor:'pointer'}}
                        >
                            <ShowChart
                                data={this.state.honorData}
                                padding={[10,30,40,30]}
                            />
                        </Card>
                </React.Fragment>
            )
        } else {
            return (
                <React.Fragment>
                {/* 处分 */}
                    <Card
                        title={
                            <div className="common-title">
                                <i className="iconfont icon-rongyu" style={{ marginRight: '5px' }}/>处分
                            </div>
                        }
                        headStyle={{ border: "none" }}
                        bodyStyle={{ padding: 0 }}
                    >
                        <NoData height={260} data={'nodata'} />
                    </Card>
                </React.Fragment>
            )
        }
    }
}




