import React, {Component} from 'react';
import {Table, Divider} from 'antd';
import ReactDOM from 'react-dom';

import MainCard from "../../../../../common/components/MainCard";
import ShowChart from './ShowChart';
import {postAction} from "../../../../../axios";
import {hostUrl} from "../../../../../env";



const columns = [
    {
        title: '序号',
        dataIndex: 'sortIndex',
        width: '10%'
    }, {
        title: '民族成分',
        dataIndex: 'national',
    }, {
        title: '男生',
        dataIndex: 'boy',
    }, {
        title: '女生',
        dataIndex: 'girl',
    }, {
        title: '总人数',
        dataIndex: 'total',
    }];

export default class SpecialStudent extends Component {

    constructor(props){
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            chartData : [
                // { name: '白族', "男生": 53, "女生": 12 }
            ],
            tableData : [
                // { key: '1', sortIndex: '1', national: '白族', boy: 284, girl: 180, total: 464 }
            ],
            pagination: {
                pageSize: 5, // 每页条数
                total: 0, // 数据总条数
                showTotal: (total) => `共${total}条数据`, //显示数据总条数
                showQuickJumper: true // 显示页码快速跳转
            },
        }
        this.getData = this.getData.bind(this);
    }

    getData () {
        const {curYear,curCollege,curMajor,curGrade,curClass} = this.props.filterValue;
        postAction(
            "/bigdata/student/getMinoritySpeStudent",
            {
                year: curYear,
                collegeCode: curCollege,
                majorCode: curMajor,
                grade: curGrade,
                classCode: curClass
            }
        ).then(
            (res) => {
                if (res.success && res.obj.nation.length > 0) {
                    const pageInfo = res.obj;
                    let _chartData = [];
                    let _tableData = [];

                    pageInfo.nation.forEach(
                        (item, index) => {
                            _chartData.push({
                                name: item.nation,
                                "男生": item.boyAmount,
                                "女生": item.girlAmount
                            })

                            _tableData.push({
                                key: (index + 1),
                                sortIndex: (index + 1),
                                national: item.nation,
                                boy: item.boyAmount,
                                girl: item.girlAmount,
                                total: item.boyAmount + item.girlAmount,
                                pagination: {
                                    pageSize: 5, // 每页条数
                                    total: pageInfo.nation.length, // 数据总条数
                                    showTotal: (total) => `共${total}条数据`, //显示数据总条数
                                    showQuickJumper: true // 显示页码快速跳转
                                }
                            })
                        }
                    )
                    this.setState({
                        chartData: _chartData,
                        tableData: _tableData
                    })
                } else {
                    this.setState({
                        chartData: [],
                        tableData: [],
                        pagination: {
                            pageSize: 5, // 每页条数
                            total: 0, // 数据总条数
                            showTotal: (total) => `共${total}条数据`, //显示数据总条数
                            showQuickJumper: true // 显示页码快速跳转
                        }
                    })
                }
            }
        )
    }

    //导出明细
    getDetail () {
        const {curYear,curCollege,curMajor,curGrade,curClass} = this.props.filterValue;
        window.location.href = `${hostUrl}/bigdata/student/exportMinoritySpeStuInfos?year=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}&classCode=${curClass}`;
    }

    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'SpecialStudent-xls')
            }
        })
    }

    render() {

        var myArray = new Array(this.state.tableData.length);

        var func = (data, key) => {
            //保存上一个name
            var x = "";
            //相同name出现的次数
            var count = 0;
            //该name第一次出现的位置
            var startindex = 0;

            for (var i = 0; i < data.length; i++) {
                //这里是合并name列，根据各自情况大家可以自己完善
                var val = data[(i)][key];
                if (i === 0) {
                    x = val;
                    count = 1;
                    myArray[0] = 1
                } else {
                    if (val === x) {
                        count++;
                        myArray[startindex] = count;
                        myArray[i] = 0
                    } else {
                        count = 1;
                        x = val;
                        startindex = i;
                        myArray[i] = 1
                    }
                }
            }
        }

        func(this.state.tableData, 'boy')

        return (
            <MainCard
                title={<div>
                    <div className="common-title">内高、预科来源少数民族学生民族成分<span>单位：人</span></div>
                    <div className="filter-text">{this.props.filterName.curYearName}
                        <Divider type="vertical" />{this.props.filterName.curCollegeName}
                        <Divider type="vertical" />{this.props.filterName.curMajorName}
                        <Divider type="vertical" />{this.props.filterName.curGradeName}
                        <Divider type="vertical" />{this.props.filterName.curClassName}
                    </div>
                </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title: "少数民族特招生学生民族成分"
                }}
                tableToExcel={{
                    table: 'SpecialStudent-xls',
                    filename: '少数民族特招生学生民族成分excel下载'
                }}
                detailCallback={this.getDetail.bind(this)}
            >
                {
                	this.state.tab === '1'
	                ?
	                <ShowChart data={this.state.chartData}
						height={this.props.height - 100}
						padding={[10,30,40,50]}
						noLegend
						geomType="intervalStack"
						onFirstLoad={(chartIns) => {
								this.setState({ chartIns })
						}} 
					/>
	                : 
	                <React.Fragment>
	                    <Table
	                        dataSource={this.state.tableData}
	                        columns={columns}
	                        bordered
	                        size="middle"
	                        pagination={this.state.pagination}
	                    />
	
	                    <Table ref='table' columns={columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
	                </React.Fragment>
    			}
            </MainCard>
        )
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)
        }
    }
}


