import React from "react";
import { Card, Popover, Icon } from 'antd'
import NoData from '@/common/components/NoData/NoDataComponent';
import XinliModal from './modal'
import { postAction } from "@/axios";
import ShowChart from './ShowChart';


class XinLi extends React.Component {

    state = {
        xinliVisible: false,
        // 心理健康
        mentalHealthData: [
            // { name: '红色预警', value: 22 }
        ],
    }
     //心理健康 1130
     getMentalHealthLabel() {
        const { curGrade, curCollege, curMajor, curClass } = this.props.filterValue
        postAction(
            "/bigdata/portrait/getMentalHealthLabel",
            {
                grade: curGrade,
                collegeCode: curCollege,
                majorCode: curMajor,
                classCode: curClass
            }
        ).then(
            (res) => {
                if (res.success && res.obj.length > 0) {
                    let newData = []
                    res.obj.forEach(({ count, labelName }) => {
                        newData.push({
                            name: labelName,
                            value: count,
                        })
                    })
                    this.setState({ mentalHealthData: newData })
                } else {
                    this.setState({
                        mentalHealthData:[]
                    })
                }
            }
        )
    }

    componentWillReceiveProps(nextProps, nextState) {
        this.setState({
            filterValue: nextProps.filterValue
        }, () => {
            this.getMentalHealthLabel()
        })
    }

    componentDidMount = () => {
        this.setState({
            filterValue: this.props.filterValue
        }, () => {
            this.getMentalHealthLabel()
        })
    }

    // 打开弹框
    handleClick = () => {
        this.setState({
            xinliVisible: true
        })
    }
    // 关闭弹窗 
    handleClose = () => {
        this.setState({
            xinliVisible: false
        })
    }
    render() {

        const content = (
            <div>
                <p style={{margin:"0px"}}>1.此模块数据为截止目前最新数据。
                    <br/>2.此模块不能查询历史数据。</p>
            </div>
        );


        if (this.state.mentalHealthData && this.state.mentalHealthData[0]) {
            return (
                <React.Fragment>
                    {
                        this.state.xinliVisible &&<XinliModal  handleClose={this.handleClose} filterValue={this.props.filterValue}  filterName={this.props.filterName} visible={this.state.xinliVisible}>

                        </XinliModal>
                    }
                    
                    <Card
                        title={<div className="common-title">
                            <i className="iconfont icon-xinlizixun" style={{ marginRight: '5px' }}/>心理健康
                            <span>单位：人</span>
													</div>}
                        extra={
                            <Popover content={content} title="心理健康说明">
                                <Icon type="exclamation-circle" style={{fontSize:"16px", color:"rgba(0, 0, 0, 0.85)"}} />
                            </Popover>
                        }
                        headStyle={{ border: "none", maxHeight: "36px", minHeight: "0" }}
                        bodyStyle={{ padding: 0 }}
                        onClick={this.handleClick}
                        style={{ cursor: 'pointer' }}
                    >
                        <ShowChart data={this.state.mentalHealthData} />
                    </Card>
                </React.Fragment>


            );
        } else {
            return (
                <Card
                    title={<div className="common-title">
                        <i className="iconfont icon-xinlizixun" style={{ marginRight: '5px' }}/>心理健康
                                                </div>}
                    headStyle={{ border: "none", maxHeight: "36px", minHeight: "0" }}
                    bodyStyle={{ padding: 0 }}
                ><NoData height={240} data={'nodata'} />
                </Card>
            );
        }
    }
}

export default XinLi;



