import React, {Component} from 'react';
import {Table, Divider} from 'antd';

import ShowChart from './ShowChart';
import {postAction} from "../../../../../axios";
import MainCard from "../../../../../common/components/MainCard/index";
import ReactDOM from "react-dom";


export default class Shops extends Component {

    constructor(props){
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            barData : [
                // {
                //     label: "商户1",
                //     "万元": 280
                // }
            ],
            tableData: [
                // { key: '1', sortIndex: '1', shops: '九龙湖校区', accounts: '2901' }
            ],
            pagination: {
                pageSize: 5, // 每页条数
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            }
        }
        this.getData = this.getData.bind(this);
    }

    getData() {
        const {curYear,curCollege,curMajor,curGrade,curClass} = this.props.filterValue;
        postAction(
            "/bigdata/consume/countConsumeWindow",
            {
                year: curYear,
                collegeCode: curCollege,
                majorCode: curMajor,
                grade: curGrade,
                classCode: curClass
            }
        ).then(
            (res) => {
                if (res.success) {
                    const pageInfo = res.obj;
                    let _newData = [];
                    let _tableData = [];

                    pageInfo.forEach(
                        (item, index) => {

                            _newData.push({
                                label: item.name,
                                "万元": item.value
                            })

                            _tableData.push({
                                key: (index + 1),
                                sortIndex: (index + 1),
                                shops: item.name,
                                accounts: item.value,
                            })
                        }
                    );
                    this.setState({
                        barData: _newData,
                        tableData: _tableData,
                        pagination: {
                            pageSize: 5, // 每页条数
                            showQuickJumper: true,
                            total: pageInfo.length,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        }
                    })
                } else {
                    this.setState({
                        barData:[],
                        tableData: [],
                        pagination: {
                            pageSize: 5, // 每页条数
                            showQuickJumper: true,
                            total: null,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        }
                    })
                }
            }
        )
    }

    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'shops-excel')
            }
            this.getData()
        })
    }

    render () {

        const columns = [
            {
                title:'排序',
                dataIndex: 'sortIndex',
                width: '10%',
            },{
                title: '商铺',
                dataIndex: 'shops',
            },{
                title: '商铺流水',
                dataIndex: 'accounts',
            }
        ];

        return (
            <MainCard
                className="main-card"
                title={<div className="common-title">商铺流水排名TOP5
                    <span>单位：万元</span>
                    <div className="filter-text" style={{top:'22px',left:'0px'}}>{(this.props.filterName.curYearName.substring(0,this.props.filterName.curYearName.length - 1)) + "-" + (Number((this.props.filterName.curYearName).substring(0,this.props.filterName.curYearName.length - 1)) + 1) + "学年"}
                        <Divider type="vertical" />{this.props.filterName.curCollegeName}
                        <Divider type="vertical" />{this.props.filterName.curMajorName}
                        <Divider type="vertical" />{this.props.filterName.curGradeName}
                        <Divider type="vertical" />{this.props.filterName.curClassName}
                    </div>
                </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title: "商铺流水排名TOP5"
                }}
                tableToExcel={{
                    table: 'shops-excel',
                    filename: '商铺流水排名TOP5excel下载'
                }}
            >

                {this.state.tab === '1' ? (
                    <ShowChart
                        data={this.state.barData}
                        transpose
                        height={this.props.height - 110}
                        padding={[10,50,60,120]}
                        onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                        geomTooltip={['label*value', (label, value)=>{
                            return {
                                name: value,
                                value: "万元"
                            }
                        }]}
                    />
                ) : (
                    <React.Fragment>
                        <Table
                            dataSource={this.state.tableData}
                            columns={columns}
                            bordered
                            size="middle"
                            pagination={this.state.pagination}
                            style={{height:300}}
                        />

                        <Table ref='table' columns={columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                    </React.Fragment>
                )}
            </MainCard>
        )
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if(JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50);
        }
    }

}


