import React, {Component} from 'react';
import {Card} from 'antd';
import {postAction} from "../../../../../../axios";
import ShowChart from './ShowChart';
import ModalTable from './modal';
import NoData from '@/common/components/NoData/NoDataComponent';


export default class BiYe extends Component {

    constructor(props){
        super(props);
        this.state = {
            // 毕业情况
            graduationData: [
                // { name: '就业', '数量': 22 }
            ],
            visible: false,
        }
    }

    // 打开弹框
    handleClick = () => {
        this.setState({
            visible: true
        })
    }
    // 关闭弹窗
    handleClose = () => {
        this.setState({
            visible: false
        })
    }

    //毕业情况 1130
    getGraduateLabel() {
        const { curYear, curGrade, curCollege, curMajor, curClass } = this.props.filterValue;
        postAction(
            "/bigdata/employment/employmentTypeAnalyze",
            {
                year: curYear,
                grade: curGrade,
                collegeCode: curCollege,
                majorCode: curMajor,
                classCode: curClass
            }
        ).then(
            (res) => {
                if (res.success && res.obj.length > 0) {
                    const pageInfo = res.obj[0].unitProperties;
                    let newData = []
                    pageInfo.forEach(({ peopleNum, name }) => {
                        newData.push(
                            {
                                name: name,
                                数量: peopleNum,
                            }
                        )
                    })
                    this.setState({ graduationData: newData });
                }
            }
        )
    }

    componentWillReceiveProps(nextProps, nextState) {
        this.setState({
            filterValue: nextProps.filterValue
        }, () => {
            this.getGraduateLabel()
        })
    }

    componentDidMount = () => {
        this.setState({
            filterValue: this.props.filterValue
        }, () => {
            this.getGraduateLabel()
        })
    }

    render () {

        if (this.state.graduationData && this.state.graduationData[0]) {
            return (
                <React.Fragment>
                    {
                        this.state.visible &&
                        <ModalTable handleClose={this.handleClose} filterValue={this.props.filterValue}
                                    visible={this.state.visible} filterName={this.props.filterName}/>
                    }

                    <Card
                        title={<div className="common-title">
                            <i className="iconfont icon-biye" style={{marginRight: '5px'}}/>毕业情况
                            <span>单位：人</span>
                        </div>}
                        headStyle={{border: "none"}}
                        bodyStyle={{padding: 0}}
                        onClick={
                            this.handleClick
                        }
                        style={{cursor:'pointer'}}
                    >
                        <ShowChart data={this.state.graduationData}/>
                    </Card>
                </React.Fragment>
            )
        } else {
            return (
                <Card
                    title={<div className="common-title">
                        <i className="iconfont icon-biye" style={{ marginRight: '5px' }}/>毕业情况
                    </div>}
                    headStyle={{ border: "none" }}
                    bodyStyle={{ padding: 0 }}
                >
                    <NoData height={260} data={'nodata'} />
                </Card>
            )
        }
    }
}


