import React, {Component} from 'react';
import {Chart, Axis, Geom, Tooltip} from 'bizcharts';
import Slider from "bizcharts-plugin-slider";
import {DataSet} from "@antv/data-set/build/data-set";
import NoData from '../../../../../common/components/NoData/NoDataComponent';


let ds;
let initEnd = 0;

export default class ShowChart extends Component {

    onFirstLoad(ev){
        if(this.props.onFirstLoad){
            this.props.onFirstLoad(ev);
        }
    }

    componentWillReceiveProps(nextProps){
        let data = nextProps.data;
        if(data && data[0]) {
            initEnd = Math.round(data.length / 2);
            setTimeout(
                () => {
                    ds.setState("start", 0);
                    ds.setState("end", initEnd);
                }
            )
        }
    }

    render () {

        // const cols = {
        //     '上网时长': {tickInterval: 20},
        // };

        let fields = [],
            data = this.props.data,
            nameKey;

        if (data && data[0]) {
            nameKey = Object.keys(data[0])[0];
            Object.keys(data[0]).forEach((item, index) => {
                if (index !== 0) {
                    fields.push(item);
                }
            })

            // 分组图
            ds = new DataSet({
                state: {
                    start: 0,
                    end: this.props.data.length - 1
                }
            }); // dateset数据集方法
            const dv = ds.createView().source(data);// 数据转换
            // 初始化参数
            dv.source(data).transform({
                type: 'fold',
                fields: fields.length > 0 ? fields : ['null'],
                key: 'type',
                value: '上网时长'
            }).transform({
                type: 'filter',
                callback: obj => {
                    const name = obj.name;
                    let curIndex;
                    this.props.data.forEach(
                        (item, index) => {
                            if (item.name === name) {
                                curIndex = index
                            }
                        }
                    )
                    return curIndex <= ds.state.end && curIndex >= ds.state.start;
                }
            })


            return (
                <React.Fragment>
                    <Chart
                        height={318}
                        padding={[10, 40, 40, 80]}
                        data={dv}
                        // scale={cols}
                        onGetG2Instance={(ev) => this.onFirstLoad(ev)}
                        forceFit
                    >
                        <Axis name={nameKey}/>
                        <Axis name="上网时长"/>
                        <Tooltip crosshairs={{type: "y"}}/>
                        <Geom
                            type='interval'
                            position={nameKey+"*上网时长"}
                            size={[20, 40]}
                            color={['name',["#3aa1ff","#5ddfcf","#4ecb73","#fbd437","#eaa674","#8a7bd4","#975fe5","#749fea","#36cbcb","#74b1ea","#5254cf","#435188"]]}
                            adjust={[{type: 'dodge',marginRatio: 1/32}]} tooltip={this.props.geomTooltip}
                        />
                    </Chart>
                    <Slider
                        width="auto"
                        height={26}
                        start={0}
                        end={initEnd}
                        padding={[20, 80, 95, 80]}
                        xAxis={nameKey}
                        yAxis="上网时长"
                        onChange={(obj) => {
                            const {startValue, endValue} = obj;
                            let _start, _end;
                            this.props.data.forEach(
                                (item, index) => {
                                    if (item.name === startValue) {
                                        _start = index
                                    }
                                    if (item.name === endValue) {
                                        _end = index
                                    }
                                }
                            )
                            ds.setState("start", _start);
                            ds.setState("end", _end);
                        }}
                        data={dv}
                    />
                </React.Fragment>
            )
        } else {
            return (<NoData height={318} data={'nodata'}/>);
        }
    }
}


