import React, { Component } from 'react';
import { Row, Col } from 'antd';
import ShenFen from './ShenFen/index';
import XinLi from './XinLi/index';
import YuJing from './YuJing/index';
import MingZu from './MingZu/index';
import SanCan from './SanCan/index';
import bottomImg from '../img/3.png';
// import ShangKe from './ShangKe/index';

const styles = {
    container: {
        height: '1269px',
        background: `url(${bottomImg}) no-repeat top center`,
        padding: '90px 40px 0px',
        boxSize: 'border-box',
        position: 'relative',
    },
    dashLine: {
        position: 'absolute',
        height: '3px',
        backgroundImage: 'linear-gradient(to right, #ccc 0%, #ccc 50%, transparent 50%)',
        backgroundSize: '20px 3px',
        backgroundRepeat: 'repeat-x',
        top: '50px',
    }
}

export default class Bottom extends Component {

    render() {
        

        return (
            <div style={styles.container}>
                {/* 虚线 */}
                <div style={{
                    ...styles.dashLine,
                    width: 'calc(50% - 200px)',
                    left: '0',
                }}/>
                <div style={{
                    ...styles.dashLine,
                    width: 'calc(50% + 10px)',
                    right: '0',
                }}/>
								
                <Row type="flex" justify="space-between" style={{ height: '370px' }}>
								
                    <Col span={12} style={{ paddingRight: 250 }}>
                        {/* 心理健康 */}
                        
						<XinLi   filterName={this.props.filterName} filterValue={this.props.filterValue} />
                    </Col>
										
                    <Col span={12} style={{ paddingLeft: 60, paddingTop: 38 }}>
                        {/* 预警情况 */}
                        
							<YuJing    filterName={this.props.filterName} filterValue={this.props.filterValue} />
                        
                    </Col>
										
                </Row>
								
                <Row type="flex" justify="space-between" style={{ height: '475px' }}>
                    {/* 身份特点 */}
                    <Col span={12} style={{ paddingRight: 220 }}>
                        <ShenFen  filterName={this.props.filterName} filterValue={this.props.filterValue}/>
                    </Col>
                    <Col span={12} style={{ paddingLeft: 160, paddingTop: 140 }}>
                        {/* 少数民族情况TOP10 */}
                        <MingZu filterName={this.props.filterName} filterValue={this.props.filterValue}/>
                    </Col>
                </Row>
                <Row type="flex" justify="space-between" style={{ height: '334px' }}>
                    <Col span={12} style={{ paddingRight: 310 }}>
                        {/* 上课情况 */}
                        {/*<ShangKe filterName={this.props.filterName} filterValue={this.props.filterValue}/>*/}
                    </Col>
                    <Col span={12} style={{ paddingLeft: 145 }}>
                        {/* 本学期三餐情况 */}
                        <SanCan filterName={this.props.filterName} filterValue={this.props.filterValue} />
                    </Col>
                </Row>
            </div>

        )
    }
}



