import React, {Component} from 'react';
import {Table} from 'antd';
import ReactDOM from 'react-dom';

import MainCard from "../../../../../common/components/MainCard";
import BarChart from "./BarChart";
import {postAction} from "../../../../../axios";
import {hostUrl} from "../../../../../env";

export default class SpecialDistribution extends Component {

    constructor(props){
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            chartData : [
                // { name: '计算机学院', "人数": 53 }
            ],
            tableData : [
                { key: '1', sortIndex: '1', college: '计算机学院', number: 53 }
            ],
            pagination: {
                pageSize: 5, // 每页条数
                total: 0, // 数据总条数
                showTotal: (total) => `共${total}条数据`, //显示数据总条数
                showQuickJumper: true // 显示页码快速跳转
            },
            columns: [
                {
                    title:'序号',
                    dataIndex: 'sortIndex',
                    width: '10%'
                },
                {
                    title: '学院',
                    dataIndex: 'college',
                }, {
                    title: '人数',
                    dataIndex: 'number',
                }
            ]
        }
        this.getData = this.getData.bind(this);
    }

    getData () {
        const {curYear,curCollege,curMajor,curGrade} = this.props.filterValue;

        let data = {};
        if ( curGrade ) {
            data = {
                year: curYear,
                collegeCode: curCollege,
                majorCode: curMajor,
                grade: curGrade,
            }
        } else {
            data = {
                year: curYear,
                collegeCode: curCollege,
            }
        }

        postAction(
            "/bigdata/student/getMinoritySpeStudent",data
       ).then(
            (res) => {
                if (res.success && res.obj.college.length > 0) {
                    const pageInfo = res.obj;
                    let _chartData = [];
                    let _tableData = [];

                    pageInfo.college.forEach(
                        (item, index) => {
                            _chartData.push({
                                name: item.name,
                                "人数": item.count
                            })

                            _tableData.push({
                                key: (index + 1),
                                sortIndex: (index + 1),
                                college: item.name,
                                number: item.count
                            })
                        }
                    )
                    this.setState({
                        chartData: _chartData,
                        tableData: _tableData,
                        pagination: {
                            pageSize: 5, // 每页条数
                            total: pageInfo.college.length, // 数据总条数
                            showTotal: (total) => `共${total}条数据`, //显示数据总条数
                            showQuickJumper: true // 显示页码快速跳转
                        }
                    })
                } else {
                    this.setState({
                        chartData: [],
                        tableData: [],
                        pagination: {
                            pageSize: 5, // 每页条数
                            total: 0, // 数据总条数
                            showTotal: (total) => `共${total}条数据`, //显示数据总条数
                            showQuickJumper: true // 显示页码快速跳转
                        },
                    })
                }
            }
        )
    }

    //导出明细
    getDetail () {
        const {curYear,curCollege,curMajor,curGrade} = this.props.filterValue;
        if ( curGrade ) {
            window.location.href=`${hostUrl}/bigdata/student/exportMinorityStuDist?year=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}`;
        } else {
            window.location.href=`${hostUrl}/bigdata/student/exportMinorityStuDist?year=${curYear}&collegeCode=${curCollege}`;
        }
    }

    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'distribution-xls')
            }
        })
    }
    render() {

        const {curYearName,curCollegeName,curMajorName,curGradeName} = this.props.filterName;

        let filterName = ``;

        let dataName = `${curYearName} | ${curCollegeName} | ${curMajorName} | ${curGradeName}`;

        let dataName2 = `${curYearName} | ${curCollegeName}`;

        if ( curGradeName === "全部年级" ) {
            filterName = dataName2
        } else {
            filterName = dataName
        }

        return (
            <MainCard
                title={<div>
                    <div className="common-title">内高、预科来源少数民族学生院系分布<span>单位：人</span></div>
                    <div className="filter-text">
                        {filterName}
                    </div>
                </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title: "少数民族特招生院系分布"
                }}
                tableToExcel={{
                    table: 'distribution-xls',
                    filename: '少数民族特招生院系分布excel下载'
                }}
                detailCallback={this.getDetail.bind(this)}
            >
                {this.state.tab === '1' ? (
                    <BarChart data={this.state.chartData}
                              height={this.props.height - 100}
                              padding={[10,40,40,80]}
                              geomSize={50}
                              noLegend
                              onFirstLoad={(chartIns) => {
                                  this.setState({ chartIns })
                              }} />
                ) : (
                    <React.Fragment>
                        <Table
                            dataSource={this.state.tableData}
                            columns={this.state.columns}
                            bordered
                            size="middle"
                            pagination={this.state.pagination}
                        />

                        <Table ref='table' columns={this.state.columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                    </React.Fragment>
                )}
            </MainCard>
        )
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)

            if (nextProps.filterName.curCollegeName === "全部学院"){
                this.setState({
                    columns : [
                        {
                            title:'序号',
                            dataIndex: 'sortIndex',
                            width: '10%'
                        },
                        {
                            title: '学院',
                            dataIndex: 'college',
                        }, {
                            title: '人数',
                            dataIndex: 'number',
                        }
                    ]
                })
            } else {
                this.setState({
                    columns : [
                        {
                            title:'序号',
                            dataIndex: 'sortIndex',
                            width: '10%'
                        },
                        {
                            title: '专业',
                            dataIndex: 'college',
                        }, {
                            title: '人数',
                            dataIndex: 'number',
                        }
                    ]
                })
    
                if (nextProps.filterName.curGradeName !== "全部年级") {
                    this.setState({
                        columns : [
                            {
                                title:'序号',
                                dataIndex: 'sortIndex',
                                width: '10%'
                            },
                            {
                                title: '班级',
                                dataIndex: 'college',
                            }, {
                                title: '人数',
                                dataIndex: 'number',
                            }
                        ]
                    })
                }
            }
        }
    }
}


