import React, {Component} from 'react';
import {Table, Divider} from 'antd';

import LineChart from '../../../../common/components/BizCharts/LineChart';
import {postAction} from "../../../../axios";
import MainCard from "../../../../common/components/MainCard/index";
import ReactDOM from "react-dom";


export default class DifferentTime extends Component {

    constructor(props) {
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            lineData : [
                // { month: "1月", "总借阅本数": 7.0, "人均借阅本数": 3.9 }
            ],
            tableData: [
                // { key: '1', sortIndex: '1', month:'1', total:'22', count: '九龙湖校区'}
            ],
            pagination: {
                pageSize: 5, // 每页条数
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            }
        }
        this.getData = this.getData.bind(this);
    }

    getData () {
        const {curYear,curCollege,curMajor,curClass,curGrade} = this.props.filterValue;
        postAction(
            "/bigdata/book/bookBorrowTendency",
            {
                year:curYear,
                collegeCode:curCollege,
                majorCode:curMajor,
                classCode:curClass,
                grade:curGrade
            }
        ).then(
            (res) => {
                if (res.success) {
                    const pageInfo = res.obj;
                    let _newData = [];
                    let _tableData = [];

                    pageInfo.forEach(
                        (item, index) => {

                            _newData.push({
                                month: item.borrowMonth,
                                "总借阅本数": item.borrowTimesCount,
                                "人均借阅本数": item.borrowTimesAvg
                            })

                            _tableData.push({
                                key: (index + 1),
                                sortIndex: (index + 1),
                                month: item.borrowMonth,
                                total: item.borrowTimesCount,
                                count: item.borrowTimesAvg
                            })
                        }
                    );
                    this.setState({
                        lineData: _newData,
                        tableData: _tableData,
                        pagination: {
                            pageSize: 5, // 每页条数
                            showQuickJumper: true,
                            total: pageInfo.length,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        }
                    })
                } else {
                    this.setState({
                        lineData:[],
                        tableData: [],
                        pagination: {
                            pageSize: 5, // 每页条数
                            showQuickJumper: true,
                            total: null,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        }
                    })
                }
            }
        )
    }

    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'different-excel')
            }
            this.getData()
        })
    }

    render () {

        const columns = [
            {
                title:'序号',
                dataIndex: 'sortIndex',
                width: '10%',
            },{
                title: '月份',
                dataIndex: 'month',
            },{
                title: '总借阅本数',
                dataIndex: 'total',
            },{
                title: '人均借阅本数',
                dataIndex: 'count',
            }
        ];

        return (
            <MainCard
                className="main-card"
                title={<div className="common-title">不同时间借阅趋势
                    <div className="filter-text" style={{top:'22px',left:'0px'}}>{this.props.filterName.curYearName}
                        <Divider type="vertical" />{this.props.filterName.curCollegeName}
                        <Divider type="vertical" />{this.props.filterName.curMajorName}
                        <Divider type="vertical" />{this.props.filterName.curGradeName}
                        <Divider type="vertical" />{this.props.filterName.curClassName}
                    </div>
                </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title: "不同时间借阅趋势"
                }}
                tableToExcel={{
                    table: 'different-excel',
                    filename: '不同时间借阅趋势excel下载'
                }}
            >
                {this.state.tab === '1' ? (
                    <LineChart
                        data={this.state.lineData}
                        padding={[10,40,60,60]}
                        height={this.props.height - 100}
                        colors={["#1890ff","#2FC25B"]}
                        onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                    />
                ) : (
                    <React.Fragment>
                        <Table
                            dataSource={this.state.tableData}
                            columns={columns}
                            bordered
                            size="middle"
                            pagination={this.state.pagination}
                            style={{height:300}}
                        />

                        <Table ref='table' columns={columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                    </React.Fragment>
                )}
            </MainCard>
        )
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)
        }
    }

}


