import React, {Component, Fragment} from 'react';
import {Card, Row, Col, Divider, Popover, Icon, Tooltip, Button} from 'antd';
import NoData from '@/common/components/NoData/NoDataComponent';
import PieChart from "../../../../../common/components/BizCharts/PieChart";
import PredictTable from "./PredictTable";
import {postAction} from "../../../../../axios";
import DownloadChart from '../../../../../common/components/BizCharts/DownloadChart';
import { hostUrl } from '../../../../../env';


export default class Predict extends Component {

    constructor(props){
        super(props);
        this.state = {
            chartIns: undefined,
            pieData : [
                // { item: '预测无挂科', count: 40, id: "1" }
            ],
            id:"",
            item:"预测无挂科人数",
            total: 0
        }
        this.getData = this.getData.bind(this);
    }

    getData () {
        const {curYear,curCollege,curMajor,curGrade,curClass} = this.props.filterValue;
        postAction(
            "/bigdata/score/countNumber",
            {
                year: curYear,
                collegeCode: curCollege,
                majorCode: curMajor,
                currentGrade: curGrade,
                classCode:curClass,
            }
        ).then(
            (res) => {
                if (res.success) {
                    const yc = res.obj;
                    if (yc && yc.length > 0) {
                        let newData = [];
                        let countTotal = [];
                        yc.forEach(
                            (item) => {
                                newData.push({
                                    item: item.typeName,
                                    count: item.failNum,
                                    id: item.typeCode
                                })
                            }
                        );
                        yc.map(res => countTotal += res.failNum)
                        this.setState({
                            pieData: newData,
                            id: newData[0].id,
                            total: countTotal
                        })
                    } else {
                        this.setState({
                            pieData: [],
                            id: "",
                            total: 0
                        })
                    }
                }
            }
        )
    }

    onFirstLoad(ev){
        if(this.props.onFirstLoad){
            this.props.onFirstLoad(ev);
        }
    }

    handleMingxi=()=>{
        // message.warning('暂无数据');
        // const {curYear,curCollege,curMajor,curGrade} = this.props.filterValue;
        window.location.href=`${hostUrl}/bigdata/score/exportForecastDetail`;
    }

    render () {

        const {chartIns} = this.state;

        const content = (
            <div>
                <p style={{margin:"0px"}}>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                依据于成绩预测算法，计算预测成绩绝对值，<br/>
                再根据训练集方差，获取置信区间，其中置信区间<br/>
                用P值作为特殊事件的风险水平，以挂科作为风险事<br/>
                件前提下，我们用 P=0.01，成绩方差，成绩预测值，<br/>
                构建了置信区间，例如结果就是刚刚说的[59,65]，<br/>
                由于59小于60，所以存在挂科风险，因为他的成绩<br/>
                少于59分的概率是1%，所以，少于60的概率肯定大<br/>
                于1%， 我们最终会给出所有人少于60分的概率，只<br/>
                要大于1%，就是存在挂科风险
                </p>
            </div>
        );

        if (this.state.pieData && this.state.pieData[0] && this.state.total > 0) {
            return (
                <Card
                    title={<div className="common-title">预测挂科情况<span>单位：人次</span></div>}
                    headStyle={{border:"none"}}
                    bodyStyle={{paddingTop:0}}
                    extra={
                        <Fragment>
                            <div className="filter-text">
                                {this.props.filterName.curCollegeName}
                                <Divider type="vertical" />{this.props.filterName.curMajorName}
                                <Divider type="vertical" />{this.props.filterName.curGradeName}
                                <Divider type="vertical" />{this.props.filterName.curClassName}
                            </div>
                            <Popover content={content} title="预测挂科的算法说明">
                                <Icon type="exclamation-circle" style={{fontSize:"16px", color:"rgba(0, 0, 0, 0.85)",cursor: "pointer"}} />
                            </Popover>
                            <Tooltip placement="top" title='导出明细'>
                                <Button onClick={this.handleMingxi} style={{marginLeft:10,padding:"0px 8px"}} theme="outlined">
                                    <i className="iconfont icon-daochu" />
                                </Button>
                            </Tooltip>
                        </Fragment>
                    }
                >
                    <Row>
                        <Col span={12}>
                            <Card
                                style={{boxShadow:"none"}}
                                bordered={false}
                                bodyStyle={{paddingTop:0}}
                                extra={
                                    <DownloadChart chartIns= {chartIns} title = {"预测挂科情况"}>
                                        <Tooltip placement="top" title='导出'>
                                            <Button theme="outlined" style={{padding:"0px 8px"}}>
                                                <i className="iconfont icon-daochu1" />
                                            </Button>
                                        </Tooltip>
                                    </DownloadChart>                            }
                            >
                                <PieChart
                                    data={this.state.pieData}
                                    innerRadius={0.55}
                                    height={this.props.height - 60}
                                    padding={[0,20,40,20]}
                                    onFirstLoad={(chartIns) => {
                                        this.setState({ chartIns })
                                    }}
                                    onClickAction={(ev)=>{
                                        // console.log(ev,2222)
                                        if (ev.data) {
                                            // console.log(ev.data,3333)
                                            // 点击获取当前行item的id
                                            this.setState({
                                                id: ev.data._origin.id,
                                                item: ev.data._origin.item
                                            })
                                        }
                                    }}
                                />
                            </Card>
                        </Col>

                        <Col span={12}>
                            <PredictTable id={this.state.id} item={this.state.item} filterValue={this.props.filterValue}/>
                        </Col>
                    </Row>
                </Card>
            )
        } else {
            return(
                <Card
                    title={<div className="common-title">预测挂科情况<span>单位：人次</span></div>}
                    headStyle={{ border: "none" }}
                    bodyStyle={{ padding: 0 }}
                >
                    <NoData height={260} data={'nodata'} />
                </Card>
            )
        }

    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)
        }
    }
}


