import React, { Component } from 'react';
import { Card } from 'antd';
import { Chart, Geom, Tooltip, Coord, Label, View } from "bizcharts"
import { postAction } from "../../../../../../../axios";
import {DataSet} from "@antv/data-set/build/data-set";
import NoData from '@/common/components/NoData/NoDataComponent';
import ModalTable from './modal'



export default class RongYuChengHao extends Component {
    constructor(props) {
        super(props);
        this.state = {
            filterValue: {
                curYear: '',
                curGrade: '',
                curCollege: '',
                curMajor: '',
                curClass: ''
            },
            visible: false,
            data : [
            //     {
            //     value: 251,
            //     type: '大事例一',
            //     name: '子事例一',
            //     child: '小小事例1'
            // },{
            //     value: 201,
            //     type: '大事例一',
            //     name: '子事例一',
            //     child: '小小事例2'
            // }, {
            //     value: 148,
            //     type: '大事例一',
            //     name: '子事例二',
            //     child: '小小事例3'
            // }, {
            //     value: 1048,
            //     type: '大事例一',
            //     name: '子事例二',
            //     child: '小小事例4'
            // }, {
            //     value: 610,
            //     type: '大事例二',
            //     name: '子事例三',
            //     child: '小小事例5'
            // }, {
            //     value: 434,
            //     type: '大事例二',
            //     name: '子事例四',
            //     child: '小小事例6'
            // }, {
            //     value: 335,
            //     type: '大事例三',
            //     name: '子事例五',
            //     child: '小小事例7'
            // }, {
            //     value: 250,
            //     type: '大事例三',
            //     name: '子事例六',
            //     child: '小小事例8'
            // }
            ]
        }
    }

    // 打开弹框
    handleClick = () => {
        this.setState({
            visible: true
        })
    }
    // 关闭弹窗
    handleClose = () => {
        this.setState({
            visible: false
        })
    }

    //荣誉称号
    getHonorLabel() {
        const { curYear, curGrade, curCollege, curMajor, curClass } = this.state.filterValue
        postAction(
            "/bigdata/portrait/getHonoraryTitleCount",
            {
                year: curYear,
                grade: curGrade,
                collegeCode: curCollege,
                majorCode: curMajor,
                classCode: curClass
            }
        ).then(
            (res) => {
                if (res.success) {
                    this.setState({
                        data:res.obj
                    })
                } else {
                    this.setState({
                        data:[]
                    })
                }
            }
        )
    }

    componentWillReceiveProps(nextProps, nextState) {
        this.setState({
            filterValue: nextProps.filterValue
        }, () => {
            this.getHonorLabel()
        })
    }

    componentDidMount = () => {
        this.setState({
            filterValue: this.props.filterValue
        }, () => {
            this.getHonorLabel()
        })
    }

    render() {

        const { DataView } = DataSet;

        const dv = new DataView();
        dv.source(this.state.data).transform({
            type: 'percent',
            field: 'value',
            dimension: 'type',
            as: 'percent'
        });
        const cols = {
            percent: {
                formatter: val => {
                    val = `${(val * 100).toFixed(2)}%`;
                    return val;
                }
            }
        };
        const dv1 = new DataView();
        dv1.source(this.state.data).transform({
            type: 'percent',
            field: 'value',
            dimension: 'name',
            as: 'percent'
        });

        const dv2 = new DataView();
        dv2.source(this.state.data).transform({
            type: 'percent',
            field: 'value',
            dimension: 'child',
            as: 'percent'
        });

        if (this.state.data && this.state.data[0]) {
            return (
                <React.Fragment>
                    {
                        this.state.visible &&
                        <ModalTable handleClose={this.handleClose} filterValue={this.props.filterValue}
                                    visible={this.state.visible} filterName={this.props.filterName}/>
                    }


                    {/* 荣誉称号 */}
                    <Card
                        title={<div className="common-title">
                                    <i className="iconfont icon-rongyu" style={{marginRight: '5px'}}/>
                                    荣誉称号
                                    <span>单位：人</span>
                                </div>
                        }
                        headStyle={{border: "none"}}
                        bodyStyle={{padding: 0}}
                        style={{cursor:'pointer'}}
                        onClick={
                            this.handleClick
                        }
                    >
                        <Chart height={400} data={dv} scale={cols} padding={[30, 100, 60, 150]} forceFit>
                            <Coord type="theta" radius={0.4}/>
                            <Tooltip showTitle={false}
                                     itemTpl="<li><span style=&quot;background-color:{color};&quot; class=&quot;g2-tooltip-marker&quot;></span>{name}: {value}</li>"/>
                            <Geom type="intervalStack" position="percent" 
                                  color={['child', ['#1890FF', '#13C2C2', '#2FC25B']]}
                                  tooltip={['type*percent*value', (item, percent, value) => {
                                      percent = `${(percent * 100).toFixed(2)}%`;
                                      return {
                                          name: item,
                                          value: value + "(" + percent + ")"
                                      };
                                  }]} style={{
                                lineWidth: 1,
                                stroke: '#fff'
                            }} select={false}>
                                <Label content="type" offset={-10}/>
                            </Geom>


                            <View data={dv1} scale={cols}>
                                <Coord type="theta" radius={0.75} innerRadius={0.4 / 0.75}/>
                                <Tooltip showTitle={false}
                                         itemTpl="<li><span style=&quot;background-color:{color};&quot; class=&quot;g2-tooltip-marker&quot;></span>{name}: {value}</li>"/>
                                <Geom type="intervalStack" position="percent"
                                      color={['name', ['#13C2C2', '#F5A623', '#F04864', '#8543E0', '#3436C7', '#223273']]}
                                      tooltip={['name*percent*value', (item, percent, value) => {
                                          percent = `${(percent * 100).toFixed(2)}%`;
                                          return {
                                              name: item,
                                              value: value + "(" + percent + ")"
                                          };
                                      }]} style={{
                                    lineWidth: 1,
                                    stroke: '#fff'
                                }} select={false}>
                                    <Label content="name" offset={-20}/>
                                </Geom>
                            </View>


                            <View data={dv2} scale={cols}>
                                <Coord type="theta" radius={1} innerRadius={0.5 / 0.75 / 1}/>
                                <Geom type="intervalStack" position="percent"
                                      color={['child', ['#13C2C2', '#F5A623', '#F04864', '#8543E0', '#3436C7', '#223273']]}
                                      tooltip={['child*percent*value', (child, percent, value) => {
                                          percent = `${(percent * 100).toFixed(2)}%`;
                                          return {
                                              name: child,
                                              value: value + "(" + percent + ")"
                                          };
                                      }]} style={{
                                    lineWidth: 1,
                                    stroke: '#fff'
                                }} select={false}>
                                    <Label content="child"/>
                                </Geom>
                            </View>
                        </Chart>

                    </Card>
                </React.Fragment>
            )
        } else {
            return (
                <React.Fragment>
                    {/* 荣誉称号 */}
                    <Card
                        title={<div className="common-title"><i className="iconfont icon-rongyu"
                                                                style={{marginRight: '5px'}}/>荣誉称号</div>}
                        headStyle={{border: "none"}}
                        bodyStyle={{padding: 0}}
                    >
                        <NoData height={260} data={'nodata'} />

                    </Card>
                </React.Fragment>

            )
        }
    }
}




