import React, {Component, Fragment} from 'react';
import {Card, Table, Progress, Divider, Button, Tooltip} from 'antd';
import {postAction} from "../../../../axios";
import NoDataComponent from "../../../../common/components/NoData/NoDataComponent";
import ReactHTMLTableToExcel from '../../../../common/components/TableToExcel/index';
import ReactDOM from "react-dom";
import {hostUrl} from "../../../../env";


export default class Reading extends Component {

    constructor(props) {
        super(props);
        this.state = {
            data: [
                // {
                //     key: '1',
                //     sortIndex: 1,
                //     cardId: 'U20173847',
                //     borrower: "张三",
                //     department: "理学院计算机专业",
                //     borrowTimes: "80"
                // }
            ],
            pagination: {
                current: 1, // 当前页数，
                pageSize: 10, // 每页条数
                total: 0, // 数据总条数
                showTotal: (total) => `共${total}条数据`, //显示数据总条数
                showQuickJumper: true // 显示页码快速跳转
            },
            averageReading: 0,
            tableToExcel:{
                table: 'read-xls',
                filename: '阅读量排行（TOP20）excel下载'
            }
        }
        this.handleTableChange = this.handleTableChange.bind(this);
        this.getAvgData = this.getAvgData.bind(this);
        this.getData = this.getData.bind(this);
    }

    handleTableChange = (pagination) => {
        this.setState(
            () => {
                return {
                    pagination: {
                        current: pagination.current, // 当前页数，
                        pageSize: pagination.pageSize, // 每页条数
                        total: pagination.total, // 数据总条数
                        showTotal: (total) => `共${total}条数据`,
                        showQuickJumper: true // 显示页码快速跳转
                    }
                }
            },
            () => {
                this.getData();
                this.getAvgData()
            }
        )
    };

    getAvgData () {
        const {curYear,curCollege,curMajor,curClass,curGrade} = this.props.filterValue;
        postAction(
            "/bigdata/book/bookCountByStudentAVG",
            {
                year:curYear,
                collegeCode:curCollege,
                majorCode:curMajor,
                classCode:curClass,
                grade: curGrade
            }
        ).then(
            (res) => {
                if (res.success && res.obj !== "" && res.obj !== null) {
                    if (res.obj.avgCount){
                        this.setState({
                            averageReading: Number(res.obj.avgCount)
                        },()=>{
                            if (this.refs['table']) {
                                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                                const table = tableCon.querySelector('table')
                                table.setAttribute('id', 'read-xls')
                            }
                        })
                    } else {
                        this.setState({
                            averageReading:0
                        })
                    }
                } else {
                    this.setState({
                        averageReading:0
                    })
                }
            }
        )
    }

    getData () {
        const {curYear,curCollege,curMajor,curClass,curGrade} = this.props.filterValue;
        postAction(
            "/bigdata/book/bookCountByStudent",
            {
                year:curYear,
                collegeCode:curCollege,
                majorCode:curMajor,
                classCode:curClass,
                grade: curGrade,
                pageInfo:{
                    pageNum:this.state.pagination.current,
                    pageSize:this.state.pagination.pageSize
                }
            }
        ).then(
            (res) => {
                if (res.success){
                    const pageInfo = res.obj;
                    let newData = [];
                    pageInfo.list.forEach(
                        (item, index) => {
                            newData.push({
                                key: (index + 1),
                                sortIndex: item.sortIndex,
                                cardId: item.borrowCode,
                                borrower: item.studentName,
                                department: item.unitName,
                                borrowTimes: item.bookCount
                            })
                        }
                    );
                    this.setState({
                        data:newData,
                        pagination: {
                            current: pageInfo.pageNum, // 当前页数，
                            pageSize: pageInfo.pageSize, // 每页条数
                            total: pageInfo.total, // 数据总条数
                            showTotal: (total) => `共${total}条数据`, //显示数据总条数
                            showQuickJumper: true // 显示页码快速跳转
                        }
                    },()=>{
                        if (this.refs['table']) {
                            const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                            const table = tableCon.querySelector('table')
                            table.setAttribute('id', 'read-xls')
                        }
                    })
                } else {
                    this.setState({
                        data:[],
                        pagination: {
                            current: 1, // 当前页数，
                            pageSize: 10, // 每页条数
                            total: 0, // 数据总条数
                            showTotal: (total) => `共${total}条数据`, //显示数据总条数
                            showQuickJumper: true // 显示页码快速跳转
                        }
                    })
                }
            }
        )
    }

    handleExport=(record)=>{
        const {curYear} = this.props.filterValue;
        let cardId = record.cardId;
        window.location.href=`${hostUrl}/bigdata/book/exportBookBorrowDist?year=${curYear}&borrowCode=${cardId}`
    };

    render () {

        // 表头设置
        const columns = [
            {
                title: '排名',
                dataIndex: 'sortIndex',
                width: '8%'
            },
            {
                title: '借书证号',
                dataIndex: 'cardId',
                width: '15%',
            },
            {
                title: '姓名',
                dataIndex: 'borrower',
                width: '10%'
            },
            {
                title: '单位',
                dataIndex: 'department',
                width: '25%',
                render: (text) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '外借数量（本）',
                dataIndex: 'borrowTimes',
                width: '42%',
                render: (text, record, index) => {
                    return (
                        <div key={index}>
                            <Progress percent={Math.round((record.borrowTimes / 120) * 100)} showInfo={false}
                                      style={{width: "70%", float: "left"}}/>
                            <Tooltip placement="top" title='导出明细'>
                                <Button onClick={() => this.handleExport(record)} style={{padding:"0px 8px",float: "right"}} theme="outlined">
                                    <i className="iconfont icon-daochu" />
                                </Button>
                            </Tooltip>
                            <span style={{float: "right",marginRight:"5%",lineHeight:"30px"}} >
                                {record.borrowTimes}
                            </span>
                        </div>
                    )
                }
            }
        ];

        if (!this.state.data || this.state.data.length <= 0) {
            return (
                <Card
                    className="progress-border"
                    title={<div className="common-title">阅读量排行（TOP20）</div>}
                    headStyle={{border:"none"}}
                    bodyStyle={{paddingTop:"0px"}}
                    extra={
                        <Fragment>
                            <div className="filter-text">{this.props.filterName.curYearName}
                                <Divider type="vertical" />{this.props.filterName.curCollegeName}
                                <Divider type="vertical" />{this.props.filterName.curMajorName}
                                <Divider type="vertical" />{this.props.filterName.curGradeName}
                                <Divider type="vertical" />{this.props.filterName.curClassName}
                            </div>
                            <ReactHTMLTableToExcel
                                {...(this.state.tableToExcel || {})}
                                content={
                                    <Tooltip placement="top" title='导出'>
                                        <Button theme="outlined" style={{padding:"0px 8px"}}>
                                            <i className="iconfont icon-daochu1" />
                                        </Button>
                                    </Tooltip>
                                }
                            />
                        </Fragment>
                    }
                >
                    <div style={{height:310}}>
                        <NoDataComponent data="nodata"/>
                    </div>
                </Card>
            )
        } else {
            return (
                <Card
                    className="progress-border"
                    title={<div className="common-title">阅读量排行（TOP20）</div>}
                    headStyle={{border:"none"}}
                    bodyStyle={{paddingTop:"0px"}}
                    extra={
                        <Fragment>
                            <div className="filter-text">{this.props.filterName.curYearName}
                                <Divider type="vertical" />{this.props.filterName.curCollegeName}
                                <Divider type="vertical" />{this.props.filterName.curMajorName}
                                <Divider type="vertical" />{this.props.filterName.curGradeName}
                                <Divider type="vertical" />{this.props.filterName.curClassName}
                            </div>
                            <ReactHTMLTableToExcel
                                {...(this.state.tableToExcel || {})}
                                content={
                                    <Tooltip placement="top" title='导出'>
                                        <Button theme="outlined" style={{padding:"0px 8px"}}>
                                            <i className="iconfont icon-daochu1" />
                                        </Button>
                                    </Tooltip>
                                }
                            />
                        </Fragment>
                    }
                >
                    <Table
                        ref='table'
                        style={{marginTop:15}}
                        columns={columns}
                        dataSource={this.state.data}
                        pagination={this.state.pagination}
                        onChange={this.handleTableChange}
                        bordered={true}
                        size="middle"
                        footer={
                            (currentPageData) => {
                                return (
                                    <div>
                                        <span>平均阅读量</span>
                                        <div style={{width: "42%", float: "right", paddingLeft: "15px"}}>
                                            <Progress
                                                percent={Math.round(((this.state.averageReading - 0) / 120) * 100)}
                                                showInfo={false} style={{width: "70%", float: "left"}}/>
                                            <span style={{float: "right"}}>{this.state.averageReading}</span>
                                        </div>
                                    </div>
                                )
                            }
                        }
                    />
                </Card>
            )
        }
    }

    componentDidMount () {
        this.getAvgData();
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.setState(
                    () => {
                        return {
                            data: [],
                            pagination: {
                                current: 1, // 当前页数，
                                pageSize: 10, // 每页条数
                                total: 0, // 数据总条数
                                showTotal: (total) => `共${total}条数据`,
                                showQuickJumper: true // 显示页码快速跳转
                            },
                            averageReading: 0
                        }
                    },
                    () => {
                        this.getData();
                        this.getAvgData();
                    }
                )
            })
        }
    }

}


