import React, {Component} from 'react';
import {Table} from 'antd';
import ReactDOM from 'react-dom';

import MainCard from "../../../../../common/components/MainCard";
import BarChart from "./BarChart";
import {postAction} from "../../../../../axios";
import {hostUrl} from "../../../../../env";

export default class GangStudent extends Component {

    constructor(props){
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            chartData: [
                // { name: '计算机科学概论', "香港": 53, "澳门": 43, "台湾": 56, "华侨": 0 }
            ],
            tableData: [
                // { key: '1', sortIndex: '1', college: '计算机科学概论', Hongkong: 53, macau: 43, Taiwan: 56, Huaqiao: 0}
            ],
            pagination: {
                pageSize: 5, // 每页条数
                total: 0, // 数据总条数
                showTotal: (total) => `共${total}条数据`, //显示数据总条数
                showQuickJumper: true // 显示页码快速跳转
            },
            columns: [{
                title:'序号',
                dataIndex: 'sortIndex',
                width: '10%'
            }, {
                title: '学院',
                dataIndex: 'college'
            }, {
                title: '香港',
                dataIndex: 'Hongkong',
            }, {
                title: '澳门',
                dataIndex: 'macau',
            }, {
                title: '台湾',
                dataIndex: 'Taiwan',
            }, {
                title: '华侨',
                dataIndex: 'Huaqiao',
            }]
        }
        this.getData = this.getData.bind(this);
    }

    getData () {
        const {curYear,curCollege,curMajor,curGrade} = this.props.filterValue;

        let data = {};
        if ( curGrade ) {
            data = {
                year: curYear,
                collegeCode: curCollege,
                majorCode: curMajor,
                grade: curGrade,
            }
        } else {
            data = {
                year: curYear,
                collegeCode: curCollege,
            }
        }
        postAction(
            "/bigdata/student/getGOTStudentDist",data
        ).then(
            (res) => {
                if (res.success && res.obj.detail.length > 0) {
                    const pageInfo = res.obj;
                    let _chartData = [];
                    let _tableData = [];

                    pageInfo.detail.forEach(
                        (item, index) => {
                            _chartData.push({
                                name: item.name,
                                "香港": item.xianggang,
                                "澳门": item.aomen,
                                "台湾": item.taiwan,
                                "华侨":item.huaqiao
                            })

                            _tableData.push({
                                key: (index + 1),
                                sortIndex: (index + 1),
                                college: item.name,
                                Hongkong: item.xianggang,
                                macau: item.aomen,
                                Taiwan: item.taiwan,
                                Huaqiao: item.huaqiao
                            })
                        }
                    )
                    this.setState({
                        chartData: _chartData,
                        tableData: _tableData,
                        pagination: {
                            pageSize: 5, // 每页条数
                            total: pageInfo.detail.length, // 数据总条数
                            showTotal: (total) => `共${total}条数据`, //显示数据总条数
                            showQuickJumper: true // 显示页码快速跳转
                        }
                    })
                } else {
                    this.setState({
                        chartData: [],
                        tableData: [],
                        pagination: {
                            pageSize: 5, // 每页条数
                            total: 0, // 数据总条数
                            showTotal: (total) => `共${total}条数据`, //显示数据总条数
                            showQuickJumper: true // 显示页码快速跳转
                        }
                    })
                }
            }
        )
    }

    //导出明细
    getDetail () {
        const {curYear,curCollege,curMajor,curGrade} = this.props.filterValue;
        if ( curGrade ) {
            window.location.href=`${hostUrl}/bigdata/student/exportGatqStuDist?year=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}`;
        } else {
            window.location.href=`${hostUrl}/bigdata/student/exportGatqStuDist?year=${curYear}&collegeCode=${curCollege}`;
        }
    }

    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'GangStudent-xls')
            }
        })
    }

    render () {

        const {curYearName,curCollegeName,curMajorName,curGradeName} = this.props.filterName;

        let filterName = ``;

        let dataName = `${curYearName} | ${curCollegeName} | ${curMajorName} | ${curGradeName}`;

        let dataName2 = `${curYearName} | ${curCollegeName}`;

        if ( curGradeName === "全部年级" ) {
            filterName = dataName2
        } else {
            filterName = dataName
        }

        return (
            <MainCard
                title={<div>
                    <div className="common-title">港澳台侨学生院系分布<span>单位：人</span></div>
                    <div className="filter-text">
                        {filterName}
                    </div>
                </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title: "港澳台侨学生院系分布"
                }}
                tableToExcel={{
                    table: 'GangStudent-xls',
                    filename: '港澳台侨学生院系分布excel下载'
                }}
                detailCallback={this.getDetail.bind(this)}
            >
                {this.state.tab === '1' ? (
                    <BarChart data={this.state.chartData}
                              height={this.props.height - 100}
                              padding={[10,40,60,80]}
                              onFirstLoad={(chartIns) => {
                                  this.setState({ chartIns })
                              }}
                              colors={['#1890FF','#2FC25B','#F5A623','#8543E0']}
                    />
                ) : (
                    <React.Fragment>
                        <Table
                            dataSource={this.state.tableData}
                            columns={this.state.columns}
                            bordered
                            size="middle"
                            pagination={this.state.pagination}
                        />

                        <Table ref='table' columns={this.state.columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                    </React.Fragment>
                )}
            </MainCard>
        )
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)

            if (nextProps.filterName.curCollegeName === "全部学院"){
                this.setState({
                    columns : [{
                        title:'序号',
                        dataIndex: 'sortIndex',
                        width: '10%'
                    }, {
                        title: '学院',
                        dataIndex: 'college'
                    }, {
                        title: '香港',
                        dataIndex: 'Hongkong',
                    }, {
                        title: '澳门',
                        dataIndex: 'macau',
                    }, {
                        title: '台湾',
                        dataIndex: 'Taiwan',
                    }, {
                        title: '华侨',
                        dataIndex: 'Huaqiao',
                    }]
                })
            } else {
                this.setState({
                    columns : [{
                        title:'序号',
                        dataIndex: 'sortIndex',
                        width: '10%'
                    }, {
                        title: '专业',
                        dataIndex: 'college'
                    }, {
                        title: '香港',
                        dataIndex: 'Hongkong',
                    }, {
                        title: '澳门',
                        dataIndex: 'macau',
                    }, {
                        title: '台湾',
                        dataIndex: 'Taiwan',
                    }, {
                        title: '华侨',
                        dataIndex: 'Huaqiao',
                    }]
                })
    
                if (nextProps.filterName.curGradeName !== "全部年级") {
                    this.setState({
                        columns : [{
                            title:'序号',
                            dataIndex: 'sortIndex',
                            width: '10%'
                        }, {
                            title: '班级',
                            dataIndex: 'college'
                        }, {
                            title: '香港',
                            dataIndex: 'Hongkong',
                        }, {
                            title: '澳门',
                            dataIndex: 'macau',
                        }, {
                            title: '台湾',
                            dataIndex: 'Taiwan',
                        }, {
                            title: '华侨',
                            dataIndex: 'Huaqiao',
                        }]
                    })
                }
            }
        }
    }
}


