import React, {Component} from 'react';
import {Card, Row, Col} from 'antd';

import ZaiJi from './ZaiJi/index';
import XueJiQingKuang from "./XueJiQingKuang/index";


export default class XueJi extends Component {
    render () {
        return (
            <Card
                title={<div className="common-title">
                    <i className="iconfont icon-chengjizhongxin" style={{ marginRight: '5px' }}/>学籍情况
                    <span>单位：人</span>
                </div>}
                headStyle={{ border: "none" }}
                bodyStyle={{ padding: 0 }}>
                <Row>
                    <Col span={12}>
                        {/* 在籍情况 */}
                        <ZaiJi filterName={this.props.filterName} filterValue={this.props.filterValue}/>
                    </Col>

                    <Col span={12}>
                        {/* 学籍异动 */}
                        <XueJiQingKuang filterName={this.props.filterName} filterValue={this.props.filterValue}/>
                    </Col>
                </Row>
            </Card>
        )
    }
}


