import React, {Component} from 'react';
import {Table,Divider} from 'antd';

import {postAction} from "../../../../../axios";
import ShowChart from "./ShowChart";
import MainCard from "../../../../../common/components/MainCard/index";
import ReactDOM from "react-dom";


export default class ConsumerTrends extends Component {

    constructor(props) {
        super(props);
        this.state={
            tab: '1',
            chartIns: undefined,
            lineData : [
                // { month: "1月", "学院平均消费金额": 1.0, "全校平均消费金额": 3 },
            ],
            tableData: [
                // { key: '1', sortIndex: '1', college: '九龙湖校区', rate: '2901' }
            ],
            pagination: {
                pageSize: 5, // 每页条数
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            }
        }
        this.getData = this.getData.bind(this);
    }

    getData() {
        const {curYear,curCollege,curMajor,curGrade,curClass} = this.props.filterValue;
        postAction (
            "/bigdata/consume/countConsumeTrend",
            {
                year: curYear,
                grade: curGrade,
                collegeCode: curCollege,
                majorCode: curMajor,
                classCode: curClass
            }
        ).then(
            (res) => {
                if (res.success) {
                    const pageInfo = res.obj;
                    let _newData = [];
                    let _tableData = [];

                    pageInfo.data.forEach(
                        (item, index) => {

                            _newData.push({
                                month:item.month + "月",
                                "平均消费金额":item.avg,
                                "全校平均消费金额":item.avgquanxiao,
                            })

                            _tableData.push({
                                key: (index + 1),
                                sortIndex: (index + 1),
                                month: item.month + "月",
                                avg: item.avg,
                                avgquanxiao: item.avgquanxiao,
                            })

                        }
                    );
                    this.setState({
                        lineData: _newData,
                        tableData: _tableData,
                        pagination: {
                            pageSize: 5, // 每页条数
                            showQuickJumper: true,
                            total: pageInfo.data.length,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        }
                    })
                } else {
                    this.setState({
                        lineData:[],
                        tableData: [],
                        pagination: {
                            pageSize: 5, // 每页条数
                            showQuickJumper: true,
                            total: null,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        }
                    })
                }
            }
        )
    }

    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'trend-excel')
            }
            this.getData()
        })
    }

    render () {

        const columns = [
            {
                title:'序号',
                dataIndex: 'sortIndex',
                width: '10%',
            },{
                title: '月份',
                dataIndex: 'month',
            },{
                title: '平均消费金额',
                dataIndex: 'avg',
            },{
                title: '全校平均消费金额',
                dataIndex: 'avgquanxiao',
            }
        ];

        return (
            <MainCard
                className="main-card"
                title={<div className="common-title">消费趋势
                    <span>单位：元</span>
                    <div className="filter-text" style={{top:'22px',left:'0px'}}>{(this.props.filterName.curYearName.substring(0,this.props.filterName.curYearName.length - 1)) + "-" + (Number((this.props.filterName.curYearName).substring(0,this.props.filterName.curYearName.length - 1)) + 1) + "学年"}
                        <Divider type="vertical" />{this.props.filterName.curCollegeName}
                        <Divider type="vertical" />{this.props.filterName.curMajorName}
                        <Divider type="vertical" />{this.props.filterName.curGradeName}
                        <Divider type="vertical" />{this.props.filterName.curClassName}
                    </div>
                </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title: "消费趋势"
                }}
                tableToExcel={{
                    table: 'trend-excel',
                    filename: '消费趋势excel下载'
                }}
            >
                {this.state.tab === '1' ? (
                    <ShowChart
                        data={this.state.lineData}
                        padding={[10,40,60,60]}
                        height={this.props.height - 100}
                        colors={["#1890ff","#2FC25B"]}
                        onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                    />
                ) : (
                    <React.Fragment>
                        <Table
                            dataSource={this.state.tableData}
                            columns={columns}
                            bordered
                            size="middle"
                            pagination={this.state.pagination}
                            style={{height:300}}
                        />

                        <Table ref='table' columns={columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                    </React.Fragment>
                )}
            </MainCard>
        )
    }

    componentDidMount() {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if(JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50);
        }
    }

}


