import React, { Component } from 'react';
import { Row, Col } from 'antd';

import XiaoFei from './XiaoFei/index';
import XueJi from './XueJi/index';

export default class Footer extends Component {

    render() {
        return (
            <Row style={styles.container} type="flex">
			
                <Col span={9}>
                    {/* 本学期消费情况 */}
                    <XiaoFei filterName={this.props.filterName} filterValue={this.props.filterValue}/>
                </Col>
				
                <Col span={15} style={{ paddingLeft: '100px' }}>
                    {/* 学籍情况 */}
                    <XueJi filterName={this.props.filterName} filterValue={this.props.filterValue}/>
                </Col>
            </Row>
        )
    }
}

const styles = {
    container: {
        height: '360px',
        padding: '40px 30px 20px',
        boxSize: 'border-box',
    },

}



