import React, {Component} from 'react';
import {Table, Card, Tooltip, Button} from 'antd';
import {postAction} from "../../../../axios";
import ReactHTMLTableToExcel from '../../../../common/components/TableToExcel';
import ReactDOM from "react-dom";


export default class TaoKeTable extends Component {

    constructor(props) {
        super(props);
        this.state = {
            dataSource : [
                // {
                //     key: '1',
                //     sortIndex: '1',
                //     name: '张三',
                //     count: 0
                // }
            ],
            pagination: {
                current: 1, // 当前页数，
                pageSize: 10, // 每页条数
                total: 0, // 数据总条数
                showTotal: (total) => `共${total}条数据`, //显示数据总条数
                showQuickJumper: true // 显示页码快速跳转
            },
            tableToExcel:{
                table: 'yisi-xls',
                filename: '疑似逃课情况excel下载'
            }
        }
        this.getData = this.getData.bind(this);
        this.handleTableChange = this.handleTableChange.bind(this);
    }

    handleTableChange = (pagination) => {
        this.setState(
            () => {
                return {
                    pagination: {
                        current: pagination.current, // 当前页数，
                        pageSize:pagination.pageSize, // 每页条数
                        total: pagination.total, // 数据总条数
                        showTotal: (total) => `共${total}条数据`,
                        showQuickJumper: true // 显示页码快速跳转
                    }
                }
            },
            () => {
                this.getData()
            }
        )
    }

    getData () {
        const {curYear,curCollege,curMajor,curGrade,curClass} = this.props.filterValue;
        const {id} =this.props;
        postAction(
            "/bigdata/attendClass/truancyAnalyzeDetail",
            {
                year: curYear,
                collegeCode: curCollege,
                majorCode: curMajor,
                grade: curGrade,
                classCode: curClass,
                id: id,
                pageInfo:{
                    pageNum: this.state.pagination.current,
                    pageSize: this.state.pagination.pageSize
                }
            }
        ).then(
            (res) => {
                if (res.success) {
                    const pageInfo = res.obj;
                    let newData = [];
                    pageInfo.list.forEach(
                        (item, index) => {
                            newData.push({
                                key: (index + 1),
                                sortIndex: (index + 1),
                                name: item.studentName,
                                count: item.truancyTime
                            })
                        }
                    );
                    this.setState({
                        dataSource:newData,
                        pagination: {
                            current: pageInfo.pageNum, // 当前页数，
                            pageSize: pageInfo.pageSize, // 每页条数
                            total: pageInfo.total, // 数据总条数
                            showTotal: (total) => `共${total}条数据`, //显示数据总条数
                            showQuickJumper: true // 显示页码快速跳转
                        }
                    },()=> {
                        if (this.refs['table']) {
                            const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                            const table = tableCon.querySelector('table')
                            table.setAttribute('id', 'yisi-xls')
                        }
                    })
                } else {
                    this.setState({
                        dataSource:[],
                        pagination: {
                            current: 1, // 当前页数，
                            pageSize: 10, // 每页条数
                            total: 0, // 数据总条数
                            showTotal: (total) => `共${total}条数据`, //显示数据总条数
                            showQuickJumper: true // 显示页码快速跳转
                        }
                    })
                }
            }
        )
    }

    render () {
        const columns = [{
            title: '序号',
            dataIndex: 'sortIndex',
            width: "12%"
        }, {
            title: '姓名',
            dataIndex: 'name',
        }, {
            title: '疑似逃课次数',
            dataIndex: 'count',
        }];

        return (

            <Card
                className="box-up"
                bodyStyle={{paddingTop:0}}
                headStyle={{paddingBottom:0}}
                style={{boxShadow:"none"}}
                bordered={false}
                extra={
                    <ReactHTMLTableToExcel
                        {...(this.state.tableToExcel || {})}
                        content={
                            <Tooltip placement="top" title='导出'>
                                <Button theme="outlined" style={{padding:"0px 8px"}}>
                                    <i className="iconfont icon-daochu1" />
                                </Button>
                            </Tooltip>
                        }
                    />
                }
            >

                <React.Fragment>
                    <Table
                        dataSource={this.state.dataSource}
                        columns={columns}
                        bordered={true}
                        size="middle"
                        pagination={this.state.pagination}
                        onChange={this.handleTableChange}
                    />

                    <Table ref='table' columns={columns} dataSource={this.state.dataSource}  pagination={false} style={{display:'none'}} />
                </React.Fragment>

            </Card>
        )
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if(JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50);
        }
    }

}


