import React from 'react'
import { Chart, Coord, Tooltip, Geom, Label} from 'bizcharts'
import NoData from '../../../../../../common/components/NoData/NoDataComponent';

class ShowChart extends React.Component {
	render () {
		if (this.props.data && this.props.data[0]) {
			
			let max = 0, //消费情况最大值
				total = 0; //消费情况总数
				
			this.props.data.forEach(function (obj) {
				total += obj.value;
				if (obj.value > max) {
					max = obj.value;
				}
			});
			
			return (
				<Chart height={240} data={ this.props.data} padding={[20, 20, 20, 20]} forceFit>
					<Coord type="theta" radius={0.8} />
					<Tooltip showTitle={false} />
					<Geom
						type="intervalStack"
						position="value"
						color="name"
						shape="sliceShape"
					>
						<Label content="name" formatter={(val, item) => {
							return item.point.name + ": " + (total !== 0 ? (item.point.value / total * 100).toFixed(0) + "%" : '暂无比例');
						}} />
					</Geom>
				</Chart>
			)
		} else{
			return (<NoData height={240} data={'nodata'}/>);
		}
	}
}

export default ShowChart


