import React, {Component} from 'react';
import {Table, Divider} from 'antd';
import ReactDOM from 'react-dom';

import MainCard from "../../../../../common/components/MainCard";
import PieChart from "../../../../../common/components/BizCharts/PieChart";
import {postAction} from "../../../../../axios";
import {hostUrl} from "../../../../../env";

export default class XiaoQu extends Component {

    constructor(props){
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            pieData: [
                // { name: '九龙湖校区', count: 40 }
            ],
            tableData: [
                // { key: '1', sortIndex: '1', campus: '九龙湖校区', number: '2901', rate: '31%' }
            ]
        }
        this.getData = this.getData.bind(this);
    }

    getData () {
        const {curYear,curCollege,curMajor,curGrade,curClass} = this.props.filterValue;
        postAction(
            "/bigdata/student/getCampusStudentDist",
            {
                year: curYear,
                collegeCode: curCollege,
                majorCode: curMajor,
                grade: curGrade,
                classCode: curClass,
                dataType: this.state.tab,
            }
        ).then(
            (res) => {
                if (res.success && res.obj.detail.length > 0){
                    const pageInfo = res.obj;
                    let _chartData = [];
                    let _tableData = [];

                        pageInfo.detail.forEach(
                            (item, index) => {

                                _chartData.push({
                                    name: item.name,
                                    count: item.count
                                })

                                _tableData.push({
                                    key: (index + 1),
                                    sortIndex: (index + 1),
                                    campus: item.name,
                                    number: item.count,
                                    rate: (item.count / pageInfo.total.count * 100).toFixed(2) + "%"
                                })
                            }
                        )
                        this.setState({
                            pieData: _chartData,
                            tableData: _tableData,
                        })
                } else {
                    this.setState({
                        pieData: [],
                        tableData: [],
                    })
                }
            }
        )
    }

    //导出明细
    getDetail () {
        const {curYear,curCollege,curMajor,curGrade,curClass} = this.props.filterValue;
        window.location.href = `${hostUrl}/bigdata/student/exportStudentCampusDist?year=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}&classCode=${curClass}`;
    }

    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'campus-excel')
            }
            this.getData()
        })
    }

    render () {

        const columns = [
            {
                title:'序号',
                dataIndex: 'sortIndex',
                width: '10%',
            },{
                title: '校区',
                dataIndex: 'campus',
            },{
                title: '人数',
                dataIndex: 'number',
            },{
                title: '占学生总数比例',
                dataIndex: 'rate'
            }
        ];

        return (
            <MainCard
                title={<div>
                    <div className="common-title">各校区分布<span>单位：人</span></div>
                    <div className="filter-text">{this.props.filterName.curYearName}
                        <Divider type="vertical" />{this.props.filterName.curCollegeName}
                        <Divider type="vertical" />{this.props.filterName.curMajorName}
                        <Divider type="vertical" />{this.props.filterName.curGradeName}
                        <Divider type="vertical" />{this.props.filterName.curClassName}
                    </div>
                </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title:"各校区分布"
                }}
                detailCallback={this.getDetail.bind(this)}
                tableToExcel={{
                    table: "campus-excel",
                    filename: "各校区分布execl下载"
                }}
            >
                {this.state.tab === '1' ? (
                    <PieChart
                        height={this.props.height - 100}
                        data={this.state.pieData}
                        padding={[0,10,60,10]}
                        onFirstLoad={(chartIns) => {
                            this.setState({ chartIns })
                        }} />
                ) : (
                    <Table
                        bodyStyle={{height:333}}
                        dataSource={this.state.tableData}
                        ref='table'
                        columns={columns}
                        bordered
                        size="middle"
                        pagination={false}
                    />
                )}
            </MainCard>
        )
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)
        }
    }
}


