import React, {Component} from 'react';
import {Card, Popover, Icon} from 'antd';
import {postAction} from "../../../../../../axios";
import ShowChart from './ShowChart';
import ModalTable from './modal';
import NoData from '@/common/components/NoData/NoDataComponent';


export default class Sancan extends Component {

    constructor(props){
        super(props);
        this.state = {
            // 本学期三餐情况
            mealsSitionData: [
                // { name: '不规律', value: 22 }
            ],
            visible: false,
            total:0
        }
    }

    // 打开弹框
    handleClick = () => {
        this.setState({
            visible: true
        })
    }
    // 关闭弹窗
    handleClose = () => {
        this.setState({
            visible: false
        })
    }

    //本学期三餐情况 1130
    getMealsLabel() {
        const { curYear, curGrade, curCollege, curMajor, curClass } = this.props.filterValue
        postAction(
            "/bigdata/portrait/getMealsLabel",
            {
                year: curYear,
                grade: curGrade,
                collegeCode: curCollege,
                majorCode: curMajor,
                classCode: curClass
            }
        ).then(
            (res) => {
                if (res.success && res.obj.length > 0) {
                    let newData = [];
                    let countTotal = 0;
                    res.obj.forEach(({ count, labelName }) => {
                        newData.push({
                            name: labelName,
                            value: count
                        })
                    });
                    res.obj.map(res => countTotal += res.count)

                    this.setState({
                        mealsSitionData: newData,
                        total: countTotal
                    })
                }
            }
        )
    }

    componentWillReceiveProps(nextProps, nextState) {
        this.setState({
            filterValue: nextProps.filterValue
        }, () => {
            this.getMealsLabel()
        })
    }

    componentDidMount = () => {
        this.setState({
            filterValue: this.props.filterValue
        }, () => {
            this.getMealsLabel()
        })
    }

    render () {

        const content = (
            <div>
                <p style={{margin:"0px"}}>0-0.2     三餐不规律</p>
                <p style={{margin:"0px"}}>0.21-0.4   三餐较不规律</p>
                <p style={{margin:"0px"}}>0.41-0.6   三餐规律一般</p>
                <p style={{margin:"0px"}}>0.61-0.8   三餐较规律</p>
                <p style={{margin:"0px"}}>0.81-1     三餐很规律</p>
                <p><strong>三餐规律越大越规律</strong></p>
            </div>
        );

        if (this.state.mealsSitionData && this.state.mealsSitionData[0] && this.state.total > 0) {
            return (
                <React.Fragment>
                    {
                        this.state.visible &&
                        <ModalTable handleClose={this.handleClose} filterValue={this.props.filterValue}
                                    visible={this.state.visible} filterName={this.props.filterName}/>
                    }
                    <Card
                        style={{cursor:'pointer'}}
                        title={<div className="common-title">
                            <i className="iconfont icon-canyin" style={{marginRight: '5px'}}/>本学期三餐情况
                            <span>单位：人</span>
                        </div>}
                        extra={
                            <Popover content={content} title="本学期三餐情况标签定义">
                                <Icon type="exclamation-circle" style={{fontSize:"16px", color:"rgba(0, 0, 0, 0.85)"}} />
                            </Popover>
                        }
                        headStyle={{border: "none"}}
                        bodyStyle={{padding: 0}}
                        onClick={
                            this.handleClick
                        }
                    >
                        <ShowChart data={this.state.mealsSitionData}/>
                    </Card>
                </React.Fragment>
            )
        } else {
            return (
                <Card
                    title={<div className="common-title">
                        <i className="iconfont icon-canyin" style={{ marginRight: '5px' }}/>本学期三餐情况
                    </div>}
                    headStyle={{ border: "none" }}
                    bodyStyle={{ padding: 0 }}

                >
                    <NoData height={260} data={'nodata'} />
                </Card>
            )
        }
    }
}


