import React, {Component} from 'react';
import {Table, Select} from 'antd';
import LineChart from './LineChart';
import {postAction} from "../../../../../axios";
import MainCard from "../../../../../common/components/MainCard/index";
import ReactDOM from "react-dom";
import '../../style.less';

const Option=Select.Option

let defaultColumns=[
    {
        title:'序号',
        dataIndex: 'key',
    },{
        title: '月份',
        dataIndex: 'surfMonth',
    }
]

export default class TimeTrend extends Component {

    constructor(props){
        super(props);
        this.state = {
            columns:[],
            tab: '1',
            chartIns: undefined,
            data:[],
            lineData : [
                // { month: "1月", "学院平均上网时长": 7.0, "全校平均上网时长": 3.9 }
            ],
            tableData: [
                // { key: '1', sortIndex: '1', month:'1', school: '九龙湖校区', college: '2901' }
            ],
            pagination: {
                pageSize: 5, // 每页条数
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            },
            fields:[],
            selectKeys:[],
           
        }
        this.getData = this.getData.bind(this);
    }

    getData () {
        const {curYear} = this.props.filterValue;
        postAction(
            "/bigdata/netTime/surfDurationAnalyze",
            {
                year: curYear,
            }
        ).then(
            (res) => {
                if (res.success){
                    let pageInfo = res.obj.dataList;
                    let fields=res.obj.fields

                    this.setState({
                        data: pageInfo,
                        lineData:[],
                        tableData: [],
                        fields,
                        selectKeys:['全校平均上网时长'],
                        pagination: {
                            pageSize: 5, // 每页条数
                            showQuickJumper: true,
                            total: pageInfo.length,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        }
                    },()=>{
                        this.filterData()
                    })
                } else {
                    this.setState({
                        lineData:[],
                        tableData: [],
                        pagination: {
                            pageSize: 5, // 每页条数
                            showQuickJumper: true,
                            total: null,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        }
                    })
                }
            }
        )
    }

    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'timelong-excel')
            }
        })
    }
    handleSelectChange=(value)=>{
        this.setState({
            selectKeys:value
        },()=>{
            this.filterData()
        })
    }
    filterData=()=>{
        // 根据当前选择的项过滤数据
        let origin=JSON.parse(JSON.stringify(this.state.data))
        let keys=this.state.selectKeys        
        if(keys.length === 0){
            this.setState({
                lineData:[],
                tableData:[],
                columns:[]
            })
        }else{
            let newData=origin.map(item=>{
                for(let i in item){
                    if(keys.indexOf(i) <0 && i !=='surfMonth'){
                        delete item[i]
                    }
                }
                return item
            })

            //将折线图的值转换为Number类型
            newData = newData.map((item,index) => {
                for(let i in item){
                   if (i !== 'surfMonth') {
                       item[i] = Number(item[i])
                   }
                }
                return item
            })

            let _tableData = JSON.parse(JSON.stringify(newData)).map((item,index)=>{
                item.key=index+1
                return item
            });
            // 拼接表头
            let _columns=[]
            keys.forEach(item=>{
                _columns.push({
                    title:item,
                    dataIndex:item,
                })
            })
            let tmp=[...defaultColumns,..._columns]
            this.setState({
                lineData:newData,
                tableData:_tableData,
                columns:tmp
            })
        }
        
    }
    render () {
            return (
                <MainCard
                    className="main-card-paddingtop"
                    title={<div className="common-title">上网时长趋势
                        <span>单位：小时</span>
                        <div className="filter-text" style={{top:'22px',left:'0px'}}>{this.props.filterName.curYearName}
                        </div>
                    </div>}
                    tab={this.state.tab}
                    changeTab={this.changeTab}
                    downloadChart={{
                        chartIns: this.state.chartIns,
                        title: "上网时间段分布"
                    }}
                    tableToExcel={{
                        table: 'timelong-excel',
                        filename: '上网时间段分布excel下载'
                    }}
                >

                    {this.state.tab === '1' ? (

                        <React.Fragment>
                            <Select
                                mode="multiple"
                                style={{width:500}}
                                onChange={this.handleSelectChange}
                                value={this.state.selectKeys}
                            >   
                                {
                                    this.state.fields.map((item,index)=><Option key={index} value={item}>{item}</Option>)
                                }
                            </Select>
                            <LineChart
                                data={this.state.lineData}
                                padding={[20,40,60,60]}
                                height={this.props.height - 100}
                                colors={["#3aa1ff","#5ddfcf","#4ecb73","#fbd437","#eaa674","#8a7bd4","#975fe5","#749fea","#36cbcb","#74b1ea","#5254cf","#435188","#3b1ff","#5adfcf","#4ecb71","#fbd457","#eab674","#8b7bd4","#925fe5","#749feb","#36cacb","#74b1eb","#5251cf","#435288","#36caca","#74b2eb"]}
                                onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                            />
                        </React.Fragment>
                        
                    ) : (
                        <React.Fragment>
                            <Table
                                dataSource={this.state.tableData}
                                columns={this.state.columns}
                                bordered
                                size="middle"
                                pagination={this.state.pagination}
                                style={{height:300}}
                            />

                            <Table ref='table' columns={this.state.columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                        </React.Fragment>
                    )}
                </MainCard>
            )
        }

    componentDidMount() {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if(JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50);
        }
    }

}


