import React, { Component } from 'react';
import { Chart, Geom, Axis, Tooltip } from "bizcharts"
import { DataSet } from '@antv/data-set';
import NoData from '../../../../../../common/components/NoData/NoDataComponent';

// 柱状图
class ShowChart extends Component {
    
    render() {
        const data = this.props.data; // 数据
        let fields=[], nameKey; // 获取关键字
        if (data && data[0]) {
            nameKey = Object.keys(data[0])[0];// 获取json第一个对象的第一个键名称
            // 获取json对象子节点除去第一个键的其他键字段集合
            Object.keys(data[0]).forEach((item,index)=>{
                if(index!==0){
                    fields.push(item);
                }
            })
			
			// 分组图
			const ds = new DataSet(); // dateset数据集方法
			const dv = ds.createView().source(data);// 数据转换
			// 初始化参数
			dv.source(data).transform({
				type: 'fold',
				fields: fields.length>0?fields:['null'],
				key: 'type',
				value: 'value'
			});
			return (
				<Chart height={200} data={dv} padding={[20, 20, 70, 50]} forceFit >
					<Axis name={nameKey} />
					<Axis name="value" />
					<Tooltip crosshairs={{type : "y"}}/>
					<Geom
						type={'interval'}
						position={nameKey+"*value"}
						color={['type', ['#1890ff', '#2FC25B']]}
						adjust={[{type: 'dodge',marginRatio: 1/32}]}
					/>
				</Chart>
			)
			
        } else {
            // 无数据时
            return (<NoData height={200} data={'nodata'}/>);
        }
    }
}

export default ShowChart;


