import React, {Component} from 'react';
import {Table, Divider} from 'antd';
import ReactDOM from 'react-dom';

import MainCard from "../../../../../common/components/MainCard";
import PieChart from "../../../../../common/components/BizCharts/PieChart";
import {postAction} from "../../../../../axios";
import {hostUrl} from "../../../../../env";


const columns = [
    {
        title: '序号',
        dataIndex: 'sortIndex',
        width:'10%'
    },
    {
        title: '地区分布',
        dataIndex: 'region'
    },
    {
        title: '人数',
        dataIndex: 'number'
    }
];


export default class RegionTop extends Component {

    constructor(props){
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            pieData: [
                // { name: '新疆', count: 40 }
            ],
            tableData: [
                // { key: '1', sortIndex:"1", region: '新疆', number: '2901'}
            ]
        }
        this.getData = this.getData.bind(this);
    }

    getData() {
        const {curYear,curCollege,curMajor,curGrade,curClass} = this.props.filterValue;
        postAction(
            "/bigdata/student/getMinorityStudent",
            {
                year: curYear,
                collegeCode: curCollege,
                majorCode: curMajor,
                grade: curGrade,
                classCode: curClass
            }
        ).then(
            (res) => {
                if (res.success && res.obj.source.length > 0){
                    const pageInfo = res.obj;
                    let _pieData = [];
                    let _tableData = [];

                    pageInfo.source.forEach(
                        (item, index) => {
                            _pieData.push({
                                name: item.source,
                                count: item.count
                            })

                            _tableData.push({
                                key: (index + 1),
                                sortIndex: (index + 1),
                                region: item.source,
                                number: item.count
                            })
                        }
                    )
                    this.setState({
                        pieData: _pieData,
                        tableData: _tableData
                    })
                } else {
                    this.setState({
                        pieData: [],
                        tableData: []
                    })
                }
            }
        )
    }

    //导出明细
    getDetail () {
        const {curYear,curCollege,curMajor,curGrade,curClass} = this.props.filterValue;
        window.location.href = `${hostUrl}/bigdata/student/exportMinTop5StuSource?year=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}&classCode=${curClass}`;
    }

    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'region-excel')
            }
        })
    }

    render () {
        return (
            <MainCard
                title={<div>
                    <div className="common-title">少数民族学生地区来源TOP5</div>
                    <div className="filter-text">{this.props.filterName.curYearName}
                        <Divider type="vertical" />{this.props.filterName.curCollegeName}
                        <Divider type="vertical" />{this.props.filterName.curMajorName}
                        <Divider type="vertical" />{this.props.filterName.curGradeName}
                        <Divider type="vertical" />{this.props.filterName.curClassName}
                    </div>
                </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title:"少数民族学生地区来源TOP5"
                }}
                detailCallback={this.getDetail.bind(this)}
                tableToExcel={{
                    table: "region-excel",
                    filename: "少数民族学生地区来源TOP5execl下载"
                }}
            >
                {this.state.tab === '1' ? (
                    <PieChart
                        height={this.props.height - 100}
                        data={this.state.pieData}
                        padding={[0,10,60,10]}
                        colors = {["#3aa1ff","#5ddfcf","#4ecb73","#fbd437","#eaa674","#8a7bd4","#975fe5","#749fea","#36cbcb","#74b1ea","#5254cf","#435188"]}
                        onFirstLoad={(chartIns) => {
                            this.setState({ chartIns })
                        }} />
                ) : (
                    <Table
                        bodyStyle={{height:335}}
                        dataSource={this.state.tableData}
                        ref='table'
                        columns={columns}
                        bordered
                        size="middle"
                        pagination={false}
                    />
                )}
            </MainCard>
        )
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)
        }
    }
}


