import React, {Component} from 'react';
import {Table, Divider} from 'antd';
import ReactDOM from 'react-dom';

import MainCard from "../../../../../common/components/MainCard";
import PieChart from "../../../../../common/components/BizCharts/PieChart";
import {postAction} from "../../../../../axios";
import {hostUrl} from "../../../../../env";


const columns = [
    {
        title: '序号',
        dataIndex: 'sortIndex',
        width: '10%',
    },
    {
        title: '年级',
        dataIndex: 'grade'
    },
    {
        title: '人数',
        dataIndex: 'number'
    },
    {
        title: '占学生总数比例',
        dataIndex: 'rate'
    }
];

export default class NianJi extends Component {

    constructor(props){
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            pieData: [
                // { name: '大一', count: 40 }
            ],
            tableData: [
                // { key: '1', sortIndex: '1', grade: '大一', number: '2901', rate: '40%'}
            ],
            pagination: {
                pageSize: 5, // 每页条数
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            }
        }
        this.getData = this.getData.bind(this);
    }

    getData () {
        const {curYear,curCollege,curMajor} = this.props.filterValue;
        postAction(
            "/bigdata/student/getGradeStudentDist",
            {
                year: curYear,
                collegeCode: curCollege,
                majorCode: curMajor
            }
        ).then(
            (res) => {
                if (res.success && res.obj.detail.length > 0){
                    const pageInfo = res.obj;

                    let _chartData = [];
                    let _tableData = [];

                    pageInfo.detail.forEach(
                        (item, index) => {

                            _chartData.push({
                                name: item.grade,
                                count: item.count
                            })

                            _tableData.push({
                                key: (index + 1),
                                sortIndex: (index + 1),
                                grade: item.grade,
                                number: item.count,
                                rate: String(item.count / pageInfo.total.count * 100).substring(0, 4) + "%"
                            })
                        }
                    )
                    this.setState({
                        pieData: _chartData,
                        tableData: _tableData,
                        pagination: {
                            pageSize: 5, // 每页条数
                            showQuickJumper: true,
                            total: pageInfo.detail.length,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        }
                    })
                } else {
                    this.setState({
                        pieData: [],
                        tableData: [],
                        pagination: {
                            pageSize: 5, // 每页条数
                            showQuickJumper: true,
                            total: null,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        }
                    })
                }
            }
        )
    }

    //导出明细
    getDetail () {
        const {curYear,curCollege,curMajor} = this.props.filterValue;
        window.location.href = `${hostUrl}/bigdata/student/exportStudentGradeDist?year=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}`;
    }

    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'grade-excel')
            }
        })
    }

    render () {

        return (
            <MainCard
                title={<div>
                    <div className="common-title">各年级分布<span>单位：人</span></div>
                    <div className="filter-text">{this.props.filterName.curYearName}
                        <Divider type="vertical" />{this.props.filterName.curCollegeName}
                        <Divider type="vertical" />{this.props.filterName.curMajorName}
                    </div>
                </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title:"各年级分布"
                }}
                detailCallback={this.getDetail.bind(this)}
                tableToExcel={{
                    table: "grade-excel",
                    filename: "各年级分布execl下载"
                }}
            >
                {this.state.tab === '1' ? (
                    <PieChart
                        height={this.props.height - 100}
                        data={this.state.pieData}
                        padding={[0,10,60,10]}
                        colors = {["#3aa1ff","#5ddfcf","#4ecb73","#fbd437","#eaa674","#8a7bd4","#975fe5","#749fea","#36cbcb","#74b1ea","#5254cf","#435188"]}
                        onFirstLoad={(chartIns) => {
                            this.setState({ chartIns })
                        }} />
                ) : (
                    <React.Fragment>
                        <Table
                            dataSource={this.state.tableData}
                            columns={columns}
                            bordered
                            size="middle"
                            pagination={this.state.pagination}
                        />

                        <Table ref='table' columns={columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                    </React.Fragment>
                )}
            </MainCard>
        )
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)
        }
    }
}


