import React, {Component} from 'react';
import {Table, Divider} from 'antd';
import ReactDOM from 'react-dom';
import MainCard from "../../../../../common/components/MainCard";
import ShowChart from './ShowChart';
import {postAction} from "../../../../../axios";
import {hostUrl} from "../../../../../env";

export default class XueYeShunXu extends Component {

    constructor(props) {
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            chartData : [
                // { name: '计算机院', "人数": 117 }
            ],
            tableData : [
                // { key: '1', sortIndex:'1', college: '建筑学院', number: 165, rate: '5%' }
            ],
            total:0,
            pagination: {
                pageSize: 5,
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            },
            columns: [{
                title:'序号',
                dataIndex: 'sortIndex',
                width: '10%',
            },{
                title: '学院',
                dataIndex: 'college',
                render: (text) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            }, {
                title: '平均GPA',
                dataIndex: 'number'
            }]

        }
        this.getData = this.getData.bind(this);
    }

    getData () {
        const {curYear,curCollege,curMajor,curGrade,curClass,curSemester} = this.props.filterValue;
        let data = {
            semesterYear: curYear,
            collegeCode: curCollege,
            majorCode: curMajor,
            grade: curGrade,
            classCode: curClass,
            semester: curSemester,
            reorder: "2"
        };

        postAction(
            "/bigdata/studentScoresWeight/queryStudentStudy",data
        ).then(
            (res) => {
                if (res.success && res.obj.length > 0){
                    let _chartData = [];
                    let _tableData = [];


                    res.obj.forEach(

                        (item, index) => {

                            _chartData.push({
                                name: item.targetObject,
                                GPA: Number(item.gpa),
                            })

                            _tableData.push({
                                key: (index + 1),
                                sortIndex: (index + 1),
                                college: item.targetObject,
                                number: item.gpa,
                            })
                        }
                    )
                    this.setState({
                        tableData: _tableData,
                        chartData: _chartData,
                        pagination: {
                            pageSize: 5,
                            showQuickJumper: true,
                            total: res.obj.length,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        },
                    })
                } else {
                    this.setState({
                        tableData: [],
                        chartData:[],
                        pagination: {
                            pageSize: 5,
                            showQuickJumper: true,
                            total: null,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        },
                    })
                }
            }
        )
    }

    //导出明细()
    getDetail () {
        const {curYear,curCollege,curMajor,curGrade,curClass,curSemester} = this.props.filterValue;
        window.location.href=`${hostUrl}/bigdata/studentScoresWeight/exportStudentScoreInfo?semesterYear=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}&classCode=${curClass}&semester=${curSemester}&reorder=2`;
    }

    //tab切换
    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'xueyeshunxu-xls')
            }
        })
    }

    render() {
        return (
            <MainCard
                className="main-card"
                title={<div>
                    <div className="common-title">学业状态（根据平均GPA进行排序）</div>
                    <div className="filter-text">{(this.props.filterName.curYearName.substring(0,this.props.filterName.curYearName.length - 1)) + "-" + (Number((this.props.filterName.curYearName).substring(0,this.props.filterName.curYearName.length - 1)) + 1) + "学年"}
                        <Divider type="vertical" />{this.props.filterName.curCollegeName}
                        <Divider type="vertical" />{this.props.filterName.curMajorName}
                        <Divider type="vertical" />{this.props.filterName.curGradeName}
                        <Divider type="vertical" />{this.props.filterName.curClassName}
                        <Divider type="vertical" />{this.props.filterName.curSemesterName}
                    </div>
                </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title: "学业状态（根据平均GPA进行排序）"
                }}
                detailCallback={this.getDetail.bind(this)}
                tableToExcel={{
                    table: 'xueyeshunxu-xls',
                    filename: '学业状态（根据平均GPA进行排序）excel下载'
                }}
            >
                {
                    this.state.tab === '1'
                    ?
                    <ShowChart
                        data={this.state.chartData}
                        onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                        geomTooltip={['type*value', (type,item) => {
                            return {
                                name: "平均GPA",
                                value: item
                            };
                        }]}
                    />
                    :
                    <React.Fragment>
                        <Table
                                dataSource={this.state.tableData}
                                columns={this.state.columns}
                                bordered
                                size="middle"
                                pagination={this.state.pagination}
                        />

                        <Table ref='table' columns={this.state.columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                    </React.Fragment>
                }
            </MainCard>
        )
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)

            if (nextProps.filterName.curCollegeName === "全部学院"){
                this.setState({
                    columns : [{
                        title:'序号',
                        dataIndex: 'sortIndex',
                        width: '10%',
                    },{
                        title: '学院',
                        dataIndex: 'college',
                        render: (text) => {
                            return (
                                <span title={text}>{text}</span>
                            )
                        }
                    }, {
                        title: '平均GPA',
                        dataIndex: 'number'
                    }]
                })
            } else {
                this.setState({
                    columns : [{
                        title:'序号',
                        dataIndex: 'sortIndex',
                        width: '10%',
                    },{
                        title: '专业',
                        dataIndex: 'college',
                        render: (text) => {
                            return (
                                <span title={text}>{text}</span>
                            )
                        }
                    }, {
                        title: '平均GPA',
                        dataIndex: 'number'
                    }]
                })
    
                if (nextProps.filterName.curGradeName !== "全部年级") {
                    this.setState({
                        columns : [{
                            title:'序号',
                            dataIndex: 'sortIndex',
                            width: '10%',
                        },{
                            title: '班级',
                            dataIndex: 'college',
                            render: (text) => {
                                return (
                                    <span title={text}>{text}</span>
                                )
                            }
                        }, {
                            title: '平均GPA',
                            dataIndex: 'number'
                        }]
                    })
                }
            }
        }
    }

}


