import React, { Component } from 'react'
import { Modal, Divider, Table, Button} from 'antd';
import { hostUrl } from '@/env'
import { postAction } from "@/axios";
// const Option=Select.Option
export default class ModalDetail extends Component {
    state = {
        tableData: [],
        pagination: {
            current: 1, // 当前页数，
            pageSize: 10, // 每页条数
            total: 0, // 数据总条数
            showTotal: (total) => `共${total}条数据`, //显示数据总条数
            showQuickJumper: true // 显示页码快速跳转
        },
        typeList:[],
        type:'',
    }
    getData() {
        const {  curCollege, curMajor, curClass, curGrade } = this.props.filterValue;
        postAction(
            "/bigdata/mentalInfo/queryStudentMental",
            {
                collegeCode: curCollege,
                majorCode: curMajor,
                classCode: curClass,
                currentGrade: curGrade,               
                "pageInfo": {
                    "pageNum": this.state.pagination.current,
                    "pageSize": this.state.pagination.pageSize
                }
            }
        ).then(
            (res) => {
                if (res.success) {
                    let data=res.obj.list.map((item,index)=>{
                        item.key=index+1
                        item.sortIndex=index+1
                        return item
                    })
                    this.setState({
                        tableData: data,
                        pagination: {
                            current: res.obj.pageNum, // 当前页数，
                            pageSize: res.obj.pageSize, // 每页条数
                            total: res.obj.total, // 数据总条数
                            showTotal: (total) => `共${total}条数据`, //显示数据总条数
                            showQuickJumper: true // 显示页码快速跳转
                        }
                    })
                } else {
                    this.setState({
                        tableData: [],
                        pagination: {
                            current: 1, // 当前页数，
                            pageSize: 10, // 每页条数
                            total: 0, // 数据总条数
                            showTotal: (total) => `共${total}条数据`, //显示数据总条数
                            showQuickJumper: true // 显示页码快速跳转
                        }
                    })
                }
            }
        )
    }

    handleExport = () => {
        const {  curCollege, curMajor, curClass, curGrade } = this.props.filterValue;
        window.location.href = `${hostUrl}/bigdata/mentalInfo/exportStudentMental?collegeCode=${curCollege}&majorCode=${curMajor}&currendGrade=${curGrade}&classCode=${curClass}`
        
    }

    getTypeList=()=>{
        const {curCollege, curMajor, curClass, curGrade } = this.props.filterValue;
        postAction(
            '/bigdata/portrait/getMentalHealthLabel',
            {
                "collegeCode":curCollege,
                "majorCode":curMajor,
                "currentGrade":curGrade,
                "classCode":curClass
            }
        ).then(res=>{
            if(res.success){
                this.setState({
                    typeList:res.obj
                })
            }
        })
    }
    
    handleTableChange = (pagination) => {
        this.setState(
            () => {
                return {
                    pagination: {
                        current: pagination.current, // 当前页数，
                        pageSize: pagination.pageSize, // 每页条数
                        total: pagination.total, // 数据总条数
                        showTotal: (total) => `共${total}条数据`,
                        showQuickJumper: true // 显示页码快速跳转
                    }
                }
            },
            () => {
                this.getData();
            }
        )
    };
    handleTypeChange=(value)=>{
        this.setState({
            type:value
        })
    }
    // 表格搜索
    handleSearch=()=>{
        this.setState({
            pagination: {
                current: 1, // 当前页数，
                pageSize: 10, // 每页条数
                total: 0, // 数据总条数
                showTotal: (total) => `共${total}条数据`, //显示数据总条数
                showQuickJumper: true // 显示页码快速跳转
            }
        },()=>{
            this.getData()
        })
        
    }
    handleAreaChange=(value)=>{
        this.setState({
            area:value
        })
    }
    componentDidMount = () => {
        // this.getTypeList()
        this.getData()
    }
    
    render() {
        const columns = [
            {
                title: '序号',
                dataIndex: 'sortIndex',
                width: '6%'
            },
            {
                title: '学号',
                dataIndex: 'studentId',
                width: '12%',
                render: (text) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '一卡通号',
                dataIndex: 'studentNo',
                width: '13%',
                render: (text) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '姓名',
                dataIndex: 'studentName',
                width: '10%'
            },           
            {
                title: '学院',
                dataIndex: 'collegeName',
                render: (text) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '专业',
                dataIndex: 'majorName',
                render: (text) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            },
            {
                title: '所属班级',
                dataIndex: 'className',
                width: '10%'
            },
            {
                title: '标签',
                dataIndex: 'labelName',
                width:'10%'
            },
        ]
        return (
            <Modal centered width={800} visible={this.props.visible} footer={null} onCancel={this.props.handleClose} title='查看详情'>
                <div className="filter-text" style={{ top: '22px', left: '0px', height: 32, lineHeight: '32px' }}>
                    {/*{this.props.filterName.curYearName}<Divider type="vertical" />*/}
                    {this.props.filterName.curCollegeName}
                    <Divider type="vertical" />{this.props.filterName.curMajorName}
                    <Divider type="vertical" />{this.props.filterName.curGradeName}
                    <Divider type="vertical" />{this.props.filterName.curClassName}
                    <Divider type="vertical" />
                    心理健康                
                    <Button onClick={this.handleExport} style={{ float: 'right' }} type='primary'>导出</Button>    
                </div>
                {/* <div style={{marginTop:10}}>
                    选择标签：
                    <Select onChange={this.handleTypeChange} defaultValue='' style={{width:150,marginRight:10}}>
                        <Option key='' value=''>全部</Option>
                        {
                            this.state.typeList.map(item=><Option key={item.labelNo} value={item.labelNo}>{item.labelName}</Option>)
                        }
                    </Select>                  
                    <Button style={{marginRight:10}} onClick={this.handleSearch} type='primary'>搜索</Button>
                    <Button onClick={this.handleExport} type='primary'>导出</Button>
                </div> */}
                <Table
                    size='small'
                    columns={columns}
                    dataSource={this.state.tableData}
                    style={{ marginTop: 15 }}
                    pagination={this.state.pagination}
                    onChange={this.handleTableChange}
                >

                </Table>
            </Modal>
        )
    }
}



