import React, {Component, Fragment} from 'react';
import {Row, Col} from 'antd';

// import GradeDistribution from "./GradeDistribution";
// import AverageScores from './AverageScores';
import Predict from './YuCeGuaKe/Predict';
import XueYeZhuangTai from "./XueYeZhuangTai";
import XueYeShunXu from "./XueYeShunXu"

export default class Xueye extends Component {
    render () {
        return (
            <Fragment>
                <Row>
                    {/*<Col span={12} className="card-padding card-right">*/}
                        {/*<GradeDistribution height={400} filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>*/}
                    {/*</Col>*/}

                    <Col span={24} className="card-padding">
                        <p style={{backgroundColor:"#fff",marginBottom:"0px",padding:"20px",fontSize:"16px",fontWeight:600}}>仅作为学生管理者判断学生学业状况的一个参考数据，不作为任何评奖评优或其他工作的评价指标！辅修、通识、国外学习课程类、非本专业教学计划要求的课程，不纳入统计。</p>
                    </Col>

                    {/*<Col span={24} className="card-padding">*/}
                        {/*<AverageScores height={400} filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>*/}
                    {/*</Col>*/}

                    <Col span={24} className="card-padding">
                        <XueYeZhuangTai filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>

                    <Col span={24} className="card-padding">
                        <XueYeShunXu filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>

                    <Col span={24} className="card-padding card-bottom">
                        <Predict height={620} filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                    </Col>

                </Row>
            </Fragment>
        )
    }
}


