import React, {Component} from 'react';
import {Table, Divider} from 'antd';
import ReactDOM from 'react-dom';
import MainCard from "../../../../../common/components/MainCard";
import BarLineChart from "./BarLineChart";
import { hostUrl } from '../../../../../env';
import {postAction} from "../../../../../axios";


const columns = [{
    title:'序号',
    dataIndex: 'sortIndex',
    width: '10%',
},{
    title: '学院',
    dataIndex: 'college',
    render: (text) => {
        return (
            <span title={text}>{text}</span>
        )
    }
}, {
    title: '算数平均分',
    dataIndex: 'bar'
}, {
    title: '平均GPA',
    dataIndex: 'line'
}]

export default class XueYeZhuangTai extends Component {

    constructor(props) {
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            barData: [
                // {month: "Jan", Tokyo: 7.0, test: 80}
            ],
            lineScale: {
                "平均GPA": {minLimit: 0, ticks: [0, 1, 2, 3, 4, 5]}
            },
            pagination: {
                pageSize: 5,
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            },
            tableData: [
                // {key: '1', sortIndex: '1', college: '建筑学院', average: 165, gpa: '5%'}
            ]
        }
    }

    getData () {
        const {curYear,curCollege,curMajor,curGrade,curClass,curSemester} = this.props.filterValue;
        let data = {
            semesterYear: curYear,
            collegeCode: curCollege,
            majorCode: curMajor,
            grade: curGrade,
            classCode: curClass,
            semester: curSemester,
            reorder: "1"
            };

        postAction(
            "/bigdata/studentScoresWeight/queryStudentStudy",data
        ).then(
            (res) => {
                if (res.success && res.obj.length > 0) {

                    let _barData = [];
                    let _tableData = [];

                    res.obj.forEach(
                        (item, index) => {

                            _barData.push({
                                name: item.targetObject,
                                "算数平均分": Number(item.scoreAvg),
                                "平均GPA": Number(item.gpa),
                            })

                            _tableData.push({
                                key: (index + 1),
                                sortIndex: (index + 1),
                                college: item.targetObject,
                                bar: item.scoreAvg,
                                line: item.gpa
                            })
                        }
                    )
                    this.setState({
                        tableData: _tableData,
                        barData: _barData,
                        pagination: {
                            pageSize: 5,
                            showQuickJumper: true,
                            total: res.obj.length,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        },
                    })
                } else {
                    this.setState({
                        tableData:[],
                        barData:[],
                        pagination: {
                            pageSize: 5,
                            showQuickJumper: true,
                            total: null,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        },
                    })
                }
            }
        )
    }

    //导出明细()
    getDetail () {
        const {curYear,curCollege,curMajor,curGrade,curClass,curSemester} = this.props.filterValue;
        window.location.href=`${hostUrl}/bigdata/studentScoresWeight/exportStudentScoreInfo?semesterYear=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}&classCode=${curClass}&semester=${curSemester}&reorder=1`;
    }


    //tab切换
    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'xueye-xls')
            }
        })
    }

    render () {
        return (
            <MainCard
                className="main-card"
                title={<div className="common-title">学业状态（根据固定学院顺序进行排序）
                    <div className="filter-text" style={{top:20,left:0}}>{(this.props.filterName.curYearName.substring(0,this.props.filterName.curYearName.length - 1)) + "-" + (Number((this.props.filterName.curYearName).substring(0,this.props.filterName.curYearName.length - 1)) + 1) + "学年"}
                        <Divider type="vertical" />{this.props.filterName.curCollegeName}
                        <Divider type="vertical" />{this.props.filterName.curMajorName}
                        <Divider type="vertical" />{this.props.filterName.curGradeName}
                        <Divider type="vertical" />{this.props.filterName.curClassName}
                        <Divider type="vertical" />{this.props.filterName.curSemesterName}
                    </div>
                    </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title: "学业状态（根据固定学院顺序进行排序）"
                }}
                detailCallback={this.getDetail.bind(this)}
                tableToExcel={{
                    table: 'xueye-xls',
                    filename: '学业状态（根据固定学院顺序进行排序）excel下载'
                }}
            >

                {
                    this.state.tab === '1'
                        ?
                        <BarLineChart
                            data={this.state.barData}
                            LineDataKey="平均GPA"
                            padding={[40,70,60,60]}
                            geomType="intervalStack"
                            lineScale={this.state.lineScale}
                            axisGrid={null}
                            colors={["#3aa1ff","#5ddfcf","#4ecb73","#fbd437","#eaa674","#8a7bd4","#975fe5","#749fea","#36cbcb","#74b1ea","#5254cf","#435188"]}
                            onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                            barTooltip={['type*value', (type,item) => {
                                return {
                                    name: "算数平均分",
                                    value: item
                                };
                            }]}
                        />

                        :
                        <React.Fragment>
                            <Table
                                dataSource={this.state.tableData}
                                columns={columns}
                                bordered
                                size="middle"
                                pagination={this.state.pagination}
                            />

                            <Table ref='table' columns={columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                        </React.Fragment>
                }

            </MainCard>
        )
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)

            if (nextProps.filterName.curCollegeName === "全部学院") {
                this.setState({
                    columns: [{
                        title: '序号',
                        dataIndex: 'sortIndex',
                        width: '10%',
                    }, {
                        title: '学院',
                        dataIndex: 'college',
                        render: (text) => {
                            return (
                                <span title={text}>{text}</span>
                            )
                        }
                    }, {
                        title: '算数平均分',
                        dataIndex: 'bar'
                    }, {
                        title: '平均GPA',
                        dataIndex: 'line'
                    }]
                })
            } else {
                this.setState({
                    columns: [{
                        title: '序号',
                        dataIndex: 'sortIndex',
                        width: '10%',
                    }, {
                        title: '专业',
                        dataIndex: 'college',
                        render: (text) => {
                            return (
                                <span title={text}>{text}</span>
                            )
                        }
                    }, {
                        title: '算数平均分',
                        dataIndex: 'bar'
                    }, {
                        title: '平均GPA',
                        dataIndex: 'line'
                    }]
                })

                if (nextProps.filterName.curGradeName !== "全部年级") {
                    this.setState({
                        columns: [{
                            title: '序号',
                            dataIndex: 'sortIndex',
                            width: '10%',
                        }, {
                            title: '班级',
                            dataIndex: 'college',
                            render: (text) => {
                                return (
                                    <span title={text}>{text}</span>
                                )
                            }
                        }, {
                            title: '算数平均分',
                            dataIndex: 'bar'
                        }, {
                            title: '平均GPA',
                            dataIndex: 'line'
                        }]
                    })
                }
            }
        }
    }
}


