import React, { Component } from 'react';
import { Row, Col } from 'antd';
import topImg from '../img/top.png';
import { postAction } from "../../../../../axios";
import JiangXueJin from "./RongYuChuFen/JiangXueJin/index";
import XueXiChengJi from "./XueXiChengJi";
import RongYuChengHao from "./RongYuChuFen/RongYuChengHao/index";
import ChuFen from './RongYuChuFen/ChuFen/index';

const container = {
	height: '1050px',
	background: `url(${topImg}) no-repeat center 30px`,
	padding: '30px 35px 0px 45px',
	boxSize: 'border-box',
	position: 'relative'
}

export default class Top extends Component {
    constructor(props) {
        super(props);
        this.state = {
            filterValue: {
                curYear: '',
                curGrade: '',
                curCollege: '',
                curMajor: '',
                curClass: ''
            },
            // 学习成绩
            studyData: [
                // { name: "学霸", "上学期": 12, "下学期": 23 }
            ]
        }
    }

    //学习成绩 1130
    getStudyLabel() {
        const { curYear, curGrade, curCollege, curMajor, curClass } = this.state.filterValue
        postAction(
            "/bigdata/portrait/getStudyLabel",
            {
                year: curYear,
                grade: curGrade,
                collegeCode: curCollege,
                majorCode: curMajor,
                classCode: curClass
            }
        ).then(
            (res) => {
                if (res.success && res.obj.length > 0) {
                    let newData = []
                    res.obj.forEach(({ secSemester, firSemester, labelName }) => {
                        newData.push(
                            {
                                name: labelName,
                                上学期: firSemester,
                                下学期: secSemester
                            }
                        )
                    })
                    this.setState({ studyData: newData });
                }
            }
        )
    }

    componentWillReceiveProps(nextProps) {
        this.setState({
            filterValue: nextProps.filterValue
        }, () => {
            this.getStudyLabel()
        })
    }
    
    componentDidMount () {
    	this.setState({
            filterValue: this.props.filterValue
        }, () => {
            this.getStudyLabel()
        })
    }

    render() {

        return (
            <Row style={container} type="flex" justify="space-between" align="bottom">

                <Col span={12} style={{ paddingRight: 260 }}>
                    {/* 奖学金 */}
                    <JiangXueJin filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                </Col>

                <Col span={12} style={{ paddingRight: 200, position:"relative", right:"-14%", top:"15%" }}>
                    {/* 荣誉称号 */}
                    <RongYuChengHao filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                </Col>

                {/* 学年显示 */}
                <div style={{
                    position: 'absolute',
                    left: 'calc(48% - 172px)',
                    top: '200px',
                    color: '#8a5629',
                    transform: 'rotate(-32deg)'
                }}>
                    {this.props.filterValue && this.props.filterValue.curYear}学年
                </div>

                <Col span={12} style={{ paddingRight: 260 }}>
                    {/* 处分 */}
                    <ChuFen filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                </Col>
				
                <Col span={12} style={{ marginBottom: -20, paddingLeft: 140 }}>
                    {/* 学习成绩 */}
                    <XueXiChengJi filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                </Col>
            </Row>
        )
    }
}




