import React, { Component } from 'react';
import { Chart, Geom, Tooltip, Coord, Label } from "bizcharts"
import NoData from '../../../../../../common/components/NoData/NoDataComponent';

// 饼图
class ShowChart extends Component {
    
    render() {
		const data = this.props.data; // 数据
		if (data && data[0]) {	
			return (
				<Chart
					height={200}
					data={this.props.data}
					padding={[20, 20, 20, 20]}
					forceFit 
				>
					<Coord type="theta" radius={0.8} />
					<Tooltip showTitle={false} />
					<Geom type="intervalStack" position="value" color={['name', ['#F04864', '#13C2C2','#FACC14','#1890FF']]} shape="triangleShape" >
						<Label content="name" formatter={(val, item) => {
							return item.point.name + ": " + item.point.value
						}}/>
					</Geom>
				</Chart>
			)
			
        } else {
            // 无数据时
            return (<NoData height={200} data={'nodata'}/>);
        }
    }
}

export default ShowChart;


