import React, { Component } from 'react';
import { Row, Col } from 'antd';
import centerImg from '../img/2.png'
import BiYe from "./BiYe/index";
import ShangWang from './ShangWang/index'

const container={
	height: '436px',
	background: `url(${centerImg}) no-repeat top center`,
	padding: '0px 40px 30px 40px',
	boxSize: 'border-box',
	position: 'relative',
};

const dashLine = {
	position: 'absolute',
	height: '3px',
	backgroundImage: 'linear-gradient(to right, #ccc 0%, #ccc 50%, transparent 50%)',
	backgroundSize: '20px 3px',
	backgroundRepeat: 'repeat-x',
	top: '50px',
}

export default class Center extends Component {
    constructor(props) {
        super(props);
        this.state = {
			filterValue: {
				curYear: '',
				curGrade: '',
				curCollege: '',
				curMajor: '',
				curClass: ''
			},

        }
    }

    render() {
        return (
            <Row style={container} type="flex" justify="space-between" align="bottom">
			
                <Col span={12} style={{ paddingRight: 250 }}>
                    {/* 毕业情况 */}
                    <BiYe filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                </Col>
				
                {/* 学年显示 */}
                <div style={{
                    position: 'absolute',
                    left: 'calc(50% - 75px)',
                    top: '39px',
                    color: '#8a5629',
                    transform: 'rotate(32deg)'
                }}>
                    {this.props.filterValue && this.props.filterValue.curYear}年
                </div>
				
                {/* 虚线 */}
                <div style={{
                    ...dashLine,
                    width: 'calc(50% - 130px)',
                    left: '0',
                }}/>
                <div style={{
                    ...dashLine,
                    width: 'calc(50% - 60px)',
                    right: '0',
                }}/>
				
                <Col span={12} style={{ paddingLeft: 60, paddingRight: 160 }}>
                    {/* 上网情况 */}
                    <ShangWang filterName = {this.props.filterName} filterValue = { this.props.filterValue }/>
                </Col>
            </Row>
        )
    }
}



