import React, {Component, Fragment} from 'react';
import {Select, Button} from 'antd';
import {postAction} from "../../../axios";
import {connect} from 'react-redux';

const Option = Select.Option;

// const curYear = new Date().getFullYear();
// const curMonth = new Date().getMonth()+1;
// let yearLists = [];
// for (let i=0; i<6; i++) {
// 	yearLists.push({
// 		name: curYear - i + "年",
// 		value: curYear - i + ""
// 	})
// }

class Filter extends Component {

    constructor(props) {
        super(props);
        this.state = {
            curYear: 2018,
            curYearName: "2018年",
            yearLists: [
				{name: "2018年", value: 2018},
				{name: "2017年", value: 2017},
				{name: "2016年", value: 2016},
				{name: "2015年", value: 2015},
				{name: "2014年", value: 2014},
			],
            // 学院
            curCollege:'',
            curCollegeName: "全部学院",
            collegeLists:[],
            // 专业
            curMajor:'',
            curMajorName:'全部专业',
            majorLists:[
            	{
            		majorCode: "",
            		majorName: "全部专业"
            	}
            ],
            
            curGrade:'',
            curGradeName: "全部年级",
            gradeLists:[
            	{
            		value: "全部年级",
            		name: ""
            	}
            ],
            
            curClass:'',
            curClassName: "全部班级",
            classLists:[
            	{
            		classCode: "",
            		className: "全部班级"
            	}
            ],
            
            curSemester:'',
            curSemesterName:"全部学期",
            semesterLists:[
	            {name:"全部学期", value: ""},
	            {name: '上学期',value: "1"},
	            {name: '下学期',value: "2"}
            ]
        };
        this.changeFilter = this.changeFilter.bind(this); // 改变筛选项
        this.getColledgeLists = this.getColledgeLists.bind(this); // 获取学院list
        this.getMajorLists = this.getMajorLists.bind(this); // 获取专业list
        this.getGradeLists = this.getGradeLists.bind(this); // 获取年级list
        this.getClassLists = this.getClassLists.bind(this); // 获取班级list
        this.sureBtn = this.sureBtn.bind(this); // 筛选条件确定按钮
    }
    
    // 筛选条件确定按钮
    sureBtn () {
    	let a={
    		curYear : this.state.curYear,
    		curCollege: this.state.curCollege,
    		curMajor:this.state.curMajor,
    		curGrade:this.state.curGrade,
    		curClass:this.state.curClass,
    		curSemester:this.state.curSemester
    	};
    	
    	let b={
    		curYearName : this.state.curYearName,
    		curCollegeName: this.state.curCollegeName,
    		curMajorName:this.state.curMajorName,
    		curGradeName:this.state.curGradeName,
    		curClassName:this.state.curClassName,
    		curSemesterName:this.state.curSemesterName
    	};
    	
    	this.props.getFilterValueAndName(a, b);
    }
    
    // 获取班级list
    getClassLists(){
    	postAction(
    		"/bigdata/school/listClassByMajor",
    		{
    			year: this.state.curGrade,
    			majorCode: this.state.curMajor
    		}
    	).then(
    		(res) => {
    			if (res.success){
    				this.setState({
    					curClass: "",
    					classLists: res.obj,
						curClassName:"全部班级"
    				})
    			}
    		}
    	)
    }
    
    // 获取年级list
    getGradeLists() {
    	postAction(
    		"/bigdata/school/getGradeList",
    		{
    			year: this.state.curYear,
    			collegeCode: this.state.curCollege,
    			majorCode: this.state.curMajor
    		}
    	).then(
    		(res) =>{
    			if (res.success){
    				this.setState({
    					curGrade: "",
    					gradeLists: res.obj,
						curGradeName: "全部年级",
    				})
    			}
    		}
    	)
    }
    
    // 获取专业list
    getMajorLists () {
    	postAction(
    		"/bigdata/school/listMajorByCollege",
    		{
    			collegeCode: this.state.curCollege
    		}
    	).then(
    		(res) => {
    			if (res.success) {
    				this.setState({
    					curMajor: "",
    					majorLists: res.obj,
						curMajorName: "全部专业",
    				})
    			}
    		}
    	)
    }
    
     // 获取学院list
    getColledgeLists(){
    	postAction(
    		"/bigdata/school/listCollege",
    	).then(
    		(res)=>{
    			if (res.success) {
    				this.setState({
    					curCollege: "",
    					collegeLists: res.obj,
						curCollegeName: "全部学院",
    				})
    			}
    		}
    	)
    }
    
    changeFilter(num, value, option){
    	if (num === 0) { // 变更自然年
    		this.setState(
    			{
	    			curYear: value,
		            curYearName: option.props.children,
	    		},
	    		() => {
	    			this.getGradeLists()
	    			this.getClassLists()
	    		}
    		)
    	} else if (num === 1){ // 变更学院
    		this.setState(
	    		{
	    			curCollege: value,
		            curCollegeName: option.props.children,
	    		},
	    		() =>{
					if(!this.props.biYeStatus){
                        this.getMajorLists()
					} else {
                        this.getMajorLists()
                        this.getGradeLists()
                        this.getClassLists()
					}
	    		}
    		)
    	} else if (num === 2){ // 变更专业
    		this.setState(
    			{
	    			curMajor: value,
		            curMajorName: option.props.children,
	    		},
    			() => {
    				this.getGradeLists()
    				this.getClassLists()
    			}
    		)
    	} else if (num === 3){ // 变更年级
    		this.setState(
    			{
    				curGrade: value,
	            	curGradeName: option.props.children,
    			},
    			() => {
    				this.getClassLists()
    			}
    		)
    	} else if (num === 4){ // 变更班级
    		this.setState({
    			curClass: value,
	            curClassName: option.props.children,
    		})
    	} else if (num === 5){ // 变更学期
    		this.setState({
    			curSemester: value,
	            curSemesterName: option.props.children,
    		})
    	}
    }

    render () {
		let { biYeStatus, xueQiStatus } = this.props;

        return (
            <Fragment>
                <span className="filter-title">选择条件：</span>

                <Select 
                	value={this.state.curYear} 
					onChange={this.changeFilter.bind(this, 0)}
					style={{ width: 180 }} 
                >
                    {
                        this.state.yearLists.map((item, index) => <Option key={index} value={item.value}>{item.name}</Option>)
                    }
                </Select>

                <Select 
                	value={this.state.curCollege}
                	placeholder="请选择学院" 
                	style={{ width: 180 }} 
                	onChange={this.changeFilter.bind(this, 1)}
                >
                    {
                        this.state.collegeLists.map(
                        	(item, index) => <Option 
                        				key={index} 
                        				title={item.collegeName}
                        				value={item.collegeCode}
                        			  >
                        				{item.collegeName}
                        			  </Option>
                        )
                    }
                </Select>

                <Select 
                	value={this.state.curMajor}
                	placeholder="请选择专业" 
                	style={{ width: 150 }} 
                	onChange={this.changeFilter.bind(this, 2)}
                >
                    {
                        this.state.majorLists.map(
                        	(item, index) => <Option 
                        				key={index} 
                        				title={item.majorName}
                        				value={item.majorCode}
                        			  >
                        				{item.majorName}
                        			  </Option>)
                    }
                </Select>

				{
					biYeStatus
					?
						<Fragment>
							<Select
								value = {this.state.curGrade}
								placeholder="请选择年级"
								style={{ width: 120 }}
								onChange={this.changeFilter.bind(this, 3)}
							>
								{
									this.state.gradeLists.map(
										(item, index) => <Option
											key={index}
											title={item.value}
											value={item.name}
										>
											{item.value}
										</Option>
									)
								}
							</Select>

							<Select
								value = {this.state.curClass}
								placeholder="请选择班级"
								style={{ width: 120 }}
								onChange={this.changeFilter.bind(this, 4)}
								>
							{
								this.state.classLists.map((item, index) => <Option key={index} title={item.className} value={item.classCode}>{item.className}</Option>)
							}
								</Select>
                        </Fragment>
						: null
				}

                
                {
                    xueQiStatus
                	?
                	<Select 
                		value={this.state.curSemester}
                		placeholder="请选择学期" 
                		style={{ width: 120 }} 
                		onChange={this.changeFilter.bind(this, 5)}
                	>
                	    {
                	        this.state.semesterLists.map((item, index) => <Option key={index} value={item.value}>{item.name}</Option>)
                	    }
                	</Select>
                	:
                	null
                }

                <Button type="primary" icon="search" onClick={this.sureBtn}>搜  索 </Button>
            </Fragment>
        )
    }
    
    componentDidMount() {
    	this.getColledgeLists()
    	this.getGradeLists()
    	this.sureBtn()
    }
    
}

const mapStateToProps = (state) => {
    return {
        xueQiStatus: state.xueqiReducer.xueQiStatus,
        biYeStatus: state.biyeReducer.biYeStatus
    }
}


export default connect(mapStateToProps, null)(Filter)



