import React, {Component} from 'react';
import {Card, Row, Col, Divider, Tooltip, Button, message} from 'antd';
import PieChart from '../../../../common/components/BizCharts/PieChart';
import TaoKeTable from "./TaoKeTable";
import {postAction} from "../../../../axios";
import DownloadChart from '../../../../common/components/BizCharts/DownloadChart';


export default class TaoKe extends Component {

    constructor(props) {
        super(props);
        this.state = {
            chartIns: undefined,
            pieData : [
                // { item: '预测无挂科', count: 40, id: '1'}
            ],
            id:"",

        }
        this.getData = this.getData.bind(this);
    }

    getData () {
        const {curYear,curCollege,curMajor,curGrade,curClass} = this.props.filterValue;
        postAction(
            "/bigdata/attendClass/truancyAnalyze",
            {
                year: curYear,
                collegeCode: curCollege,
                majorCode: curMajor,
                grade: curGrade,
                classCode: curClass
            }
        ).then(
            (res) => {
                if (res.success && res.obj.length > 0) {
                    let newData = [];
                    res.obj.forEach(
                        (item) => {
                            newData.push({
                                item: item.typeName,
                                count: item.truancyNum,
                                id: item.typeCode
                            })
                        }
                    );
                    this.setState({
                    	id: newData[0].id,
                        pieData:newData
                    })
                } else {
                    this.setState({
                    	id:"",
                        pieData:[]
                    })
                }
            }
        )
    }

    onFirstLoad(ev){
        if(this.props.onFirstLoad){
            this.props.onFirstLoad(ev);
        }
    }

    handleMingxi=()=>{
        message.warning('暂无数据');
        // const {curYear,curCollege,curMajor,curGrade} = this.props.filterValue;
        // window.location.href=`${hostUrl}/bigdata/student/exportStudentDist?year=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}`;
    }

    render () {

        const {chartIns} = this.state;

        return (
            <Card
                title={<div className="common-title">疑似逃课情况<span>单位：人</span></div>}
                headStyle={{border:"none"}}
                extra={<React.Fragment>
                	<div className="filter-text">{`${this.props.filterName.curYearName.substring(0,this.props.filterName.curYearName.length - 1)}学年-${Number((this.props.filterName.curYearName).substring(0,this.props.filterName.curYearName.length - 1)) + 1}学年`}
	                    <Divider type="vertical" />{this.props.filterName.curCollegeName}
	                    <Divider type="vertical" />{this.props.filterName.curMajorName}
	                    <Divider type="vertical" />{this.props.filterName.curGradeName}
	                    <Divider type="vertical" />{this.props.filterName.curClassName}
	                </div>
                    <Tooltip placement="top" title='导出明细'>
                        <Button onClick={this.handleMingxi} style={{marginLeft:10,padding:"0px 8px"}} theme="outlined">
                            <i className="iconfont icon-daochu" />
                        </Button>
                    </Tooltip>
                </React.Fragment>
                }
            >
                <Row>
                    <Col span={14}>
                        <Card
                            style={{boxShadow:"none"}}
                            bordered={false}
                            bodyStyle={{paddingTop:0}}
                            extra={
                                <DownloadChart chartIns= {chartIns} title = {"疑似逃课情况"}>
                                    <Tooltip placement="top" title='导出'>
                                        <Button theme="outlined" style={{padding:"0px 8px"}}>
                                            <i className="iconfont icon-daochu1" />
                                        </Button>
                                    </Tooltip>
                                </DownloadChart>                            }
                        >
                            <PieChart
                                data={this.state.pieData}
                                innerRadius={0.55}
                                height={this.props.height - 60}
                                padding={[10,10,60,10]}
                                colors = {["#3aa1ff","#5ddfcf","#4ecb73","#fbd437","#eaa674","#8a7bd4","#975fe5","#749fea","#36cbcb","#74b1ea","#5254cf","#435188"]}
                                onFirstLoad={(chartIns) => {
                                    this.setState({ chartIns })
                                }}
                                onClickAction={(ev)=>{
                                    if (ev.data) {
                                        // 点击获取当前行item的id
                                        this.setState({
                                            id: ev.data._origin.id
                                        })
                                    }
                                }}
                            />
                        </Card>
                    </Col>

                    <Col span={10}>
                        <TaoKeTable id={this.state.id} filterValue={this.props.filterValue}/>
                    </Col>
                </Row>
            </Card>
        )
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if(JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50);
        }
    }

}





