import React, {Component} from 'react';
import {Chart, Geom, Axis, Tooltip, Legend} from 'bizcharts';
import DataSet from '@antv/data-set';
// import NoData from '../../../../../common/components/NoData/NoDataComponent';


export default class ShowChart extends Component {

    // 第一次加载完触发事件
    onFirstLoad(ev){
        if(this.props.onFirstLoad){
            this.props.onFirstLoad(ev);
        }
    }

    // 点击事件
    onClickAction(ev){
        if(this.props.onClickAction){
            this.props.onClickAction(ev);
        }
    }

    render () {

        // const height = this.props.height || 350; // 高度设置，默认值
        const { DataView } = DataSet; // 数据视图
        const data = this.props.data; // 数据
        const dv = new DataView();
        // let field, dimension; // 获取关键字
        // if (data && data[0]) {
        //     dimension = Object.keys(data[0])[0]; //获取键
        //     field = Object.keys(data[0])[1]; //获取值
        // } else {
        //     // 无数据时
        //     return (<NoData height={height} data={'nodata'}/>);
        // }
        // 初始化参数
        dv.source(data).transform({
            type: 'fold',
            fields: [ '未迟到', '迟到' ], // 展开字段集
            key: 'type',                  // key字段
            value: 'value',               // value字段
        });
        const cols = {
            month: {
                range: [ 0 , 1 ]
            }
        };

        return (
            <Chart
                onGetG2Instance={(ev)=>this.onFirstLoad(ev)}
                onPlotClick={(ev)=>this.onClickAction(ev)}
                height={this.props.height - 100}
                data={dv}
                scale={cols}
                forceFit
                padding={this.props.padding}
            >
                <Axis name="month" />
                <Axis name="value" label={{
                    formatter: val => {
                        return val + '分';
                    }
                }} />
                <Legend />
                <Tooltip crosshairs={{type:'line'}} shared={false}/>
                <Geom type="area" position="month*value" color={['type', ['#65A1E7', '#F5A623']]} />
                <Geom
                    type="line"
                    position="month*value"
                    size={2}
                    color={['type', ['#65A1E7', '#F5A623']]}
                    tooltip={['type*value', (type, item)=>{
                        return {
                            name:type,
                            value:item + '分钟'
                        }
                    }]}
                />
            </Chart>
        )
    }
}


